import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ApplicantsViewGrid from 'Employer/views/ApplicantsView/components/ApplicantsViewGrid'
import { getUrlToSearchState } from 'Shared/selectors/url'
import { fetchHighSchoolEducationGradingSystemIfNeeded } from 'Shared/ducks/resources'
import { getApplicationsCounters } from 'Shared/ducks/jobOfferApplications'
import { getApplicationsCountersByTypeAndId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  return ({
    applicationsLoading: state.jobOfferApplications.isLoading,
    applicationListsLoaded: state.jobOfferApplications.applicationListsLoaded,
    searchState: getUrlToSearchState(ownProps.location.search, true),
    getApplicationsNumber: status => getApplicationsCountersByTypeAndId(ownProps.match.params.jobOfferId, status)(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchHighSchoolEducationGradingSystem: () => dispatch(fetchHighSchoolEducationGradingSystemIfNeeded()),
  getApplicationsCounters: () => dispatch(getApplicationsCounters([ownProps.match.params.jobOfferId]))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ApplicantsViewGrid))



