import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileDocuments from 'Employer/components/CandidateProfileDetails/CandidateProfileDocuments'
import { getApplicationCvFile } from 'Shared/selectors/files'

const mapStateToProps = (state, ownProps) => ({
  documents: getApplicationCvFile(state, ownProps.match.params.applicationId),
  shouldUnlock: false
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileDocuments)



