import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import validate from 'Employer/connectors/Forms/InterviewInvite/InterviewInviteFormValidator'
import InterviewInviteForm from 'Employer/forms/InterviewInvite/InterviewInviteForm'
import { sendInterviewMessage } from 'Shared/ducks/jobOfferApplications'
import { getCandidateByApplicationId } from 'Shared/selectors/candidates'
import { getJobOfferByApplicationId, getJobOfferById } from 'Shared/selectors/jobOffers'
import { getOrganisationName } from 'Shared/selectors/organisations'
import { getInitialInviteDescription } from 'Shared/helpers/jobApplications'
import { useApiGoogleMeet } from 'Shared/services/api/useApiGoogleMeet'

export const formName = 'InterviewInviteForm'

const { apiSendInvitation } = useApiGoogleMeet()

let Form = reduxForm({
  form: formName,
  validate
})(InterviewInviteForm)

const mapStateToProps = (state, ownProps) => {
  const employer = state.user.details
  const applicationId = ownProps.match.params.applicationId || ownProps.applicationId
  const candidate = ownProps.candidate || getCandidateByApplicationId(applicationId)(state)
  const jobOffer = getJobOfferById(state, ownProps.match.params.jobOfferId) || getJobOfferByApplicationId(applicationId)(state)

  return {
    initialValues: {
      description: getInitialInviteDescription(candidate, jobOffer, employer, getOrganisationName(state, jobOffer.organisationId)),
      timeSlots: []
    },
    jobOffer,
    candidate,
    invitationSending: state.jobOfferApplications.invitationSending,
    applicationId,
    employerDetails: employer
  }
}

const mapDispatchToProps = (dispatch) => ({
  sendInterviewMessage: async (values, jobOffer, candidateId, applicationId) => {
    return dispatch(sendInterviewMessage(values, jobOffer, candidateId, applicationId))
  },
  apiSendInvitation: apiSendInvitation
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



