import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import BulkMessagesForm from 'Employer/forms/BulkMessagesForm/BulkMessagesForm'
import validate from 'Employer/connectors/Forms/BulkOperations/BulkSendMessageFormValidator'
import {
  changeSelectedApplicationsStatusToContacted,
  deselectApplicationForBulkAction,
  selectApplicationForBulkAction
} from 'Shared/ducks/jobOfferApplications'
import { getBulkOperationsItems, getBulkOperationItemsIds, getCandidateIdsByApplicationIdArray } from 'Shared/selectors/jobOfferApplications'
import { t } from 'i18next'
import NotificationService from 'Shared/services/NotificationService'
import JobOfferApplicationsService from 'Shared/services/JobOfferApplicationsService'
import { setCandidateAsContacted } from 'Shared/ducks/candidateSearchFilters'
import { pluck } from 'ramda'

const jobOfferApplicationsService = new JobOfferApplicationsService()

export const formName = 'BulkSendingMessages'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: async (values, dispatch, props) => {
    try {
      await jobOfferApplicationsService.bulkSendMessages({
        ...values,
        ids: props.bulkOperationItemsIds
      })
      dispatch(changeSelectedApplicationsStatusToContacted(props.bulkOperationItemsIds))
      dispatch(setCandidateAsContacted(props.bulkOperationCandidateIds))
      return NotificationService.success(t('alerts.bulkSendMessagesSuccess'))
    } catch (err) {
      NotificationService.error(t('alerts.bulkSendMessagesError'))
      return console.error(err)
    }
  }
})(BulkMessagesForm)

const mapStateToProps = (state, ownProps) => {
  const idsArray = getBulkOperationItemsIds()(state)

  return ({
    bulkOperationItems: getBulkOperationsItems(state),
    bulkOperationItemsIds: idsArray,
    bulkOperationCandidateIds: getCandidateIdsByApplicationIdArray(state, pluck('id')(idsArray)),
    initialValues: {
      jobOfferId: ownProps.match.params.jobOfferId
    }
  })
}

const mapDispatchToProps = dispatch => ({
  selectApplicationForBulkAction: (applicationId, candidateId, fullName, status, jobOfferId) => dispatch(selectApplicationForBulkAction(applicationId, candidateId, fullName, status, jobOfferId)),
  deselectApplicationForBulkAction: applicationId => dispatch(deselectApplicationForBulkAction(applicationId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



