import { getRequest, postRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData, getLocalData, removeLocalData } from 'Shared/helpers/session'

export const useApiGoogleMeet = () => {
  const apiGetGoogleAuthLink = async () => {
    if (getLocalData('googleAuthLink')) {
      console.log('Already exist link!')
      return
    }

    setLocalData('redirectLink', window.location.href)

    console.log('apiGetGoogleAuthLink...')

    const url = endPoints.olivGetGoogleAuthLink
    try {
      let res = await getRequest(url)
      if (res.success) {
        setLocalData('googleAuthLink', res.data)
        return res.success
      }
      return res.success
    } catch (err) {
      console.log('Err', err)
    }
  }

  const apiGetGoogleToken = async (employerId = null) => {
    if (!employerId) return
    if (getLocalData('googleToken')) {
      console.log('Already exist token!')
      return
    }

    const data = {
      employerId: employerId,
      code: getLocalData('googleCode') || ''
    }

    console.log('apiGetGoogleToken...')

    const url = endPoints.olivGetGoogleToken
    try {
      let res = await getRequest(url, data)
      if (res.success) {
        setLocalData('googleToken', res.data)
        removeLocalData('googleCode')
        return res.data
      }
      removeLocalData('googleCode')
      return res.success
    } catch (err) {
      console.log('Err', err)
    }
  }

  const apiIsExpiredGoogleToken = async (employerId = null) => {
    if (!employerId) return
    if (getLocalData('isExpiredGoogleToken')) {
      console.log('Already exist token!')
      return
    }

    const data = { employerId: employerId }
    console.log('apiIsExpiredGoogleToken...')

    const url = endPoints.olivGoogleIsExpired
    try {
      let res = await getRequest(url, data)
      if (res.success) {
        setLocalData('isExpiredGoogleToken', res.success)
        return res.data
      }
      removeLocalData('isExpiredGoogleToken')
      return res.success
    } catch (err) {
      setLocalData('isExpiredGoogleToken', true)
      console.log('Err', err)
    }
  }

  const apiSendInvitation = async (data) => {
    if (!data) return

    const url = endPoints.olivSendInvitation
    console.log('apiSendInvitation...')

    try {
      let res = await postRequest(url, data)
      if (res.success) {
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
    }
  }

  return { apiGetGoogleAuthLink, apiGetGoogleToken, apiSendInvitation, apiIsExpiredGoogleToken }
}



