import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileCertificates from 'Employer/components/CandidateProfileDetails/CandidateProfileCertificates'
import { getCandidateCertificates } from 'Shared/selectors/certificates'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    certificates: getCandidateCertificates(state, candidateId),
    withHighlights: true,
    candidateId
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileCertificates)



