import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import ApplicationProfileAbout from 'Employer/components/CandidateProfileDetails/CandidateProfileAbout'
import { getApplicationCoverLetter } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => ({
  pitch: getApplicationCoverLetter(state, ownProps.match.params.applicationId)
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(ApplicationProfileAbout)



