import { connect } from 'react-redux'
import { isInvalid, submit } from 'redux-form'
import { withRouter } from 'react-router-dom'
import ApplicantHitButtons from 'Employer/views/ApplicantsView/components/ApplicantHitButtons'
import { formName as declinedFormName } from 'Employer/connectors/Forms/DeclinedMessageFormConnector'
import { formName as hiredFormName } from 'Employer/connectors/Forms/Applications/HiredMessageFormConnector'
import { startChatConversation } from 'Chat/ducks/classes/channels'
import { getChannelByJobOfferAndCandidateId, isChannelMember } from 'Chat/selectors/channels'
import { getApplicationChatId } from 'Shared/selectors/jobOfferApplications'
import { hasActiveConversation } from 'Shared/selectors/jobOfferApplicationChats'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import { useApiGoogleMeet } from 'Shared/services/api/useApiGoogleMeet'
import { getUserDetails } from 'Shared/selectors/user'

const { apiGetGoogleAuthLink, apiGetGoogleToken, apiIsExpiredGoogleToken } = useApiGoogleMeet()

const mapStateToProps = (state, ownProps) => {
  const chatId = getApplicationChatId(state, ownProps.applicationId)

  return ({
    areChannelsFetched: state.chat.areChannelsFetched,
    isChannelMember: isChannelMember(state, chatId),
    hasActiveConversation: hasActiveConversation(state, chatId),
    joinToChannelLoading: state.chat.joinToChannelLoading,
    applicationsLoading: state.jobOfferApplications.isLoading,
    declinedMessageFormValid: !isInvalid(declinedFormName)(state),
    hiredMessageFormValid: !isInvalid(hiredFormName)(state),
    jobOffer: getJobOfferById(state, ownProps.match.params.jobOfferId),
    getChannelByJobOfferAndCandidateId: (jobOfferId, candidateId) => getChannelByJobOfferAndCandidateId(state, jobOfferId, candidateId),
    apiGetGoogleAuthLink: apiGetGoogleAuthLink,
    apiGetGoogleToken: apiGetGoogleToken,
    apiIsExpiredGoogleToken: apiIsExpiredGoogleToken,
    userDetails: getUserDetails(state)
  })
}

const mapDispatchToProps = (dispatch) => ({
  submitDeclinedForm: () => dispatch(submit(declinedFormName)),
  submitHiredForm: () => dispatch(submit(hiredFormName)),
  startChatConversation: (jobOffer, candidateId, applicationId) => dispatch(startChatConversation(jobOffer, candidateId, applicationId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ApplicantHitButtons))



