import React from 'react'
import { Field } from 'redux-form'
import { Genders } from 'Shared/constants/Job'
import withTranslations from 'Shared/hoc/withTranslations'
import { Radio, RadioWrapper } from 'Shared/components/FormElements'

const GenderPreferences = ({ t }) => {
  return (
    <div className='form__group'>
      <RadioWrapper
        name='preferredGender'
        legend={t('jobOffer.postAJob.preferredGender.title')}
        className='form__wrapper--radio-checkbox'
      >
        <Field
          className='step_basic__radio_button'
          component={Radio}
          text={t('jobOffer.postAJob.preferredGender.male.label')}
          radioButtonValue={Genders.male}
          name='preferredGender'
        />
        <Field
          className='step_basic__radio_button'
          component={Radio}
          text={t('jobOffer.postAJob.preferredGender.female.label')}
          radioButtonValue={Genders.female}
          name='preferredGender'
        />
        <Field
          className='step_basic__radio_button'
          component={Radio}
          text={t('jobOffer.postAJob.preferredGender.undisclosed.label')}
          radioButtonValue={Genders.undisclosed}
          name='preferredGender'
        />
      </RadioWrapper>
    </div>
  )
}

export default withTranslations(GenderPreferences)



