import React, { PureComponent } from 'react'
import { Field } from 'redux-form'
import moment from 'moment'
import cx from 'classnames'
import { InputDatePicker } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import { convertDateToMoment } from 'Shared/helpers/date'
import { interviewHoursIntervals } from 'Shared/constants/Date'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import Button from 'Shared/components/Button'
import { Select } from 'Shared/components/FormElements/Select'
import Label from 'Shared/components/FormElements/Label'
import FormUtilsService from 'Shared/services/FormUtilsService'

const formUtilsService = new FormUtilsService()

const MAX_SLOTS_AMOUNT = 3

class SlotRow extends PureComponent {
  constructor (props) {
    super(props)
    this.state = this.generateSlotsInitialState()
  }

  generateSlotsInitialState = () => {
    let i
    const state = {}

    for (i = 0; i < MAX_SLOTS_AMOUNT; i++) {
      state[i] = {}
    }
    return state
  }

  fieldsAvailable = () => this.props.fields.length < MAX_SLOTS_AMOUNT

  addSlot = () => this.fieldsAvailable() && this.props.fields.push({})

  removeSlot = (index) => () => {
    this.setState({ [index]: '' })
    return this.props.fields.remove(index)
  }

  componentDidMount () {
    this.fieldsAvailable() && this.props.fields.length === 0 && this.addSlot()
  }

  get timeFromIntervalsOptions () {
    const timeFromIntervals = interviewHoursIntervals.slice(0, interviewHoursIntervals.length - 1)
    return formUtilsService.mapArrayToOptions(timeFromIntervals)
  }

  getTimeIntervalsOptions = (index) => {
    const timeToIntervals = () => interviewHoursIntervals.slice(
      interviewHoursIntervals.indexOf(this.state[index]['timeFrom']) + 1
    )

    return formUtilsService.mapArrayToOptions(timeToIntervals())
  }

  handleChangeTime = (fieldName, index) => time => this.setState({ [index]: { ...this.state[index], [fieldName]: time } })

  render () {
    const { t, meta: { touched, submitFailed, error } } = this.props

    const labelClassNames = cx({
      'slot-label-error': true,
      'slot-label-error--visible': (touched || submitFailed) && error
    })

    return (
      <div>
        <Label className={labelClassNames}>{error && t(error.key, error.options)}</Label>
        {
          this.props.fields.map((slot, index) => {
            return (
              <div key={slot} className='columns slot-container'>
                <Field
                  className='column col-5 col-md-12 slot-row-input'
                  component={InputDatePicker}
                  label={t('common.date')}
                  name={`${slot}.date`}
                  icon={'ion-md-clock'}
                  floatRight
                  minDate={moment().utc()}
                  placeholderText={t('forms.startDate.placeholder')}
                  format={value => convertDateToMoment(value)}
                  selectsStart
                />
                <Field
                  className='column col-3 col-md-12 slot-row-input'
                  component={Select}
                  name={`${slot}.from`}
                  withIcon
                  label={t('common.from')}
                  placeholderText={t('forms.startDate.placeholder')}
                  options={this.timeFromIntervalsOptions}
                  onSelect={this.handleChangeTime('timeFrom', index)}
                />
                <Field
                  className='column col-3 col-md-12 slot-row-input'
                  component={Select}
                  name={`${slot}.to`}
                  withIcon
                  label={t('common.to')}
                  placeholderText={t('forms.startDate.placeholder')}
                  options={this.getTimeIntervalsOptions(index)}
                  onSelect={this.handleChangeTime('timeTo', index)}
                />
                {index === 0
                  ? null
                  : <div className='column col-1 ol-ml-auto slot-row-button'>
                    <Button onClick={this.removeSlot(index)} justIcon><i className='ion-md-trash' /></Button>
                  </div>
                }
              </div>
            )
          })
        }
        {this.fieldsAvailable() &&
          <AddNewSectionButton
            className='slot-add-new-slot'
            onClick={this.addSlot}
            text={t('modals.inviteToInterview.addNewSlot')}
          />
        }
      </div>
    )
  }
}

SlotRow.propTypes = {

}

export default withTranslations(SlotRow)



