import React, { Component } from 'react'
import { Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Checkbox, RadioWrapper, UrlInput } from 'Shared/components/FormElements'

class ExternalLink extends Component {
  render () {
    const { t, isCustomCareerSiteUrlEnabled } = this.props
    return (
      <div className='form__group'>
        <RadioWrapper
          name='customCareerSiteUrl'
          legend={t('jobOffer.postAJob.externalLink.addLink.title')}
          layout={'checkbox-vertical'}
        >
          <Field
            component={Checkbox}
            text={t('jobOffer.postAJob.externalLink.addLink.label')}
            name='customCareerSiteUrlEnabled'
            normalize={v => !!v}
          />
        </RadioWrapper>
        {
          isCustomCareerSiteUrlEnabled &&
          <Field
            component={UrlInput}
            label={t('jobOffer.postAJob.externalLink.url.label')}
            name='customCareerSiteUrl'
          />
        }
      </div>
    )
  }
}

export default withTranslations(ExternalLink)



