import React, { Component } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Field } from 'redux-form'
import { RadioWrapper, Radio, Input } from 'Shared/components/FormElements'
import ProfileStepSection from 'Shared/components/ProfileStepSection/index'
import Notification from 'Shared/components/Notification'
import TipButton from 'Shared/components/TipButton/TipButton'
import { ExternalRoutes } from 'Shared/constants/Routes'
import Select from 'Shared/components/FormElements/Select/Select'

class Salary extends Component {
  showSalaryTips = () => {
    window.open(ExternalRoutes.salaryGuide, '_blank')
  }

  render () {
    const { t, hasRemunerationTypeFieldError } = this.props
    return (
      <div>
        <div className='form__group__title--with-tip'>
          <ProfileStepSection
            label={t('jobOffer.postAJob.salary.label')}
            text={t('jobOffer.postAJob.salary.text')}
          />
          <TipButton onClick={this.showSalaryTips}>{t('common.salaryGuide')}</TipButton>
        </div>
        <div className='form__group'>
          <RadioWrapper
            name='remunerationType'
            legend={t('jobOffer.postAJob.salary.typeOfJob.label')}
            hasRequiredError={hasRemunerationTypeFieldError}
          >
            <Field
              className='step_basic__radio_button'
              component={Radio}
              radioButtonValue='paid'
              name='remunerationType'
              text={t('jobOffer.postAJob.salary.typeOfJob.typePaid')}
            />
            <Field
              className='step_basic__radio_button'
              component={Radio}
              radioButtonValue='unpaid'
              name='remunerationType'
              text={t('jobOffer.postAJob.salary.typeOfJob.typeUnpaid')}
            />
            <Field
              className='step_basic__radio_button'
              component={Radio}
              radioButtonValue='tbd'
              name='remunerationType'
              text={t('jobOffer.postAJob.salary.typeOfJob.typeToBeDiscussed')}
            />
          </RadioWrapper>
        </div>
        {this.props.remunerationType === 'unpaid' &&
        <Notification
          messageText={t('jobOffer.postAJob.salary.warning')}
          iconClassName='ion-md-warning'
          warning
        />
        }
        {this.props.remunerationType === 'paid' &&
          <div className='job-offer__input-with-select--wrapper'>
            <Field
              component={Input}
              label={t('jobOffer.postAJob.salary.label')}
              name='salary'
              type='text'
              className='job-offer__salary-input--with-currency'
            />
            <Field
              component={Select}
              name='currencyId'
              options={this.props.currencies}
              className='job-offer__salary-select--with-currency'
            />
          </div>
        }
      </div>
    )
  }
}

export default withTranslations(Salary)



