import startCase from 'lodash/startCase'
import { isEmpty, isNil } from 'ramda'
import moment from 'moment'
import { lengthLimit } from 'Shared/constants/Editor'
import { hoursLeftFromDate } from 'Shared/helpers/date'

const R = require('ramda')

const validateSingleSlot = slot => {
  const errors = {}
  const requiredFields = ['date', 'from', 'to']

  requiredFields.forEach(field => {
    if (isNil(slot[field])) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!isNil(slot['date'])) {
    const timeFrom = moment(slot.from, 'H:m A')
    const timeTo = moment(slot.to, 'H:m A')

    const dateFrom = moment(slot.date).set({ hours: timeFrom.format('H'), minutes: timeFrom.format('m') }).utc().format()
    const dateTo = moment(slot.date).set({ hours: timeTo.format('H'), minutes: timeTo.format('m') }).utc().format()

    const isDateFromPast = date => hoursLeftFromDate(date) <= 1
    const validateDate = (fieldName, value) => (
      R.when(
        isDateFromPast,
        () => {
          errors[fieldName] = {
            key: 'errors.dateFromPast',
            options: {}
          }
        }
      )(value)
    )

    validateDate('from', dateFrom)
    validateDate('to', dateTo)
  }

  return errors
}

export default values => {
  const errors = {}
  const requiredFields = ['description']

  const [min, max] = lengthLimit.interviewInvite

  if (values['description'] && values['description'].length < min) {
    errors['description'] = {
      key: 'errors.textFieldTooShort',
      options: {
        field: startCase('description'),
        chars: startCase(min.toString())
      }
    }
  }

  if (values['description'] && values['description'].length > max) {
    errors['description'] = {
      key: 'errors.textFieldTooLong',
      options: {
        field: startCase('description'),
        chars: startCase(max.toString())
      }
    }
  }

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!values['timeSlots'] || isEmpty(values['timeSlots'])) {
    errors['timeSlots'] = { _error: { key: 'errors.timeSlotGeneral' } }
  } else {
    const timeSlotsErrors = {}

    values['timeSlots'].forEach((slot, index) => { timeSlotsErrors[index] = validateSingleSlot(slot) })

    errors['timeSlots'] = timeSlotsErrors
  }

  return errors
}



