import React, { useState, useEffect } from 'react'
import { isEmpty, not } from 'ramda'
import PropTypes from 'prop-types'
import './ApplicantDetailsView.scss'
import { CandidateProfileViewLoader } from 'Shared/components/Main/MainLoaders'
import { addLightBackground, removeLightBackground } from 'Shared/helpers/layout'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import ApplicationProfileNavbarConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileNavbarConnector'
import ApplicationProfileHeaderConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileHeaderConnector'
import ApplicationProfileAboutConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileAboutConnector'
import ApplicationProfileGeneralInfoConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileGeneralInfoConnector'
import ApplicationProfileEducationConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileEducationConnector'
import ApplicationProfileCoverLetterConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileCoverLetterConnector'
import ApplicationProfileCvDocumentConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileCvDocumentConnector'
import ApplicationProfileCertificatesConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileCertificatesConnector'
import ApplicationProfileExperienceConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileExperienceConnector'
import ApplicationProfileVolunteeringConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileVolunteeringConnector'
import ApplicationProfileSkillsConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileSkillsConnector'
import ApplicationProfileInterestsConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileInterestsConnector'
import ApplicationProfileLanguagesConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileLanguagesConnector'
import ApplicationProfileVideoCvConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileVideoCvConnector'
import ApplicationProfileAdditionalDocumentsConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileAdditionalDocumentsConnector'
import ApplicationProfileWebsitesConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileWebsitesConnector'
import MobileWindow from 'Shared/components/MobileWindow/MobileWindow.mobile'
import { pathWithoutIdentifier } from 'Shared/helpers/locationIdentifier'
import { AuhtorisedEmployerRoutes } from 'Shared/constants/Routes'
import BackButtonMobile from 'Shared/components/BackButton/BackButton.mobile'
import { t } from 'i18next'

const ApplicantDetailsView = (props) => {
  const [loadingDetails, setLoadingDetails] = useState(true)

  useEffect(() => {
    addLightBackground()
    props.fetchJobOfferApplication()
    setLoadingDetails(false)
  }, [])

  useEffect(() => {
    if (not(isEmpty(props.applicantDetails.candidateId))) {
      props.markApplicationAsSeenIfNeeded()
      props.setCandidateAsViewed(props.applicantDetails.candidateId)
    }
  }, [props.applicantDetails])

  useEffect(() => {
    return () => {
      removeLightBackground()
    }
  }, [])

  const goBack = () => {
    const { match: { params: { jobOfferId } } } = props
    const { opener } = window
    const backToApplicants = opener
      ? `${pathWithoutIdentifier(opener.location.pathname)}${opener.location.search}`
      : `${AuhtorisedEmployerRoutes.jobOfferApplicants}/${jobOfferId}/applicants/undecided`

    return props.history.push(backToApplicants)
  }

  const {
    hasCandidateAnyCertificates,
    hasCandidateAnyExperience,
    hasCandidateAnyVolunteering,
    hasCandidateAnySkills,
    hasCandidateAnyLanguages,
    hasCandidateVideoCv,
    hasAnyAdditionalDocuments,
    hasCandidateAnyInterests,
    hasCandidateAnyWebsites
  } = props

  return loadingDetails ? <CandidateProfileViewLoader /> : (
    <MobileWindow show>
      <div className='applicant-details-mobile'>
        <ApplicationProfileNavbarConnector />
        <BackButtonMobile button onClick={goBack} />
        <div className='applicant-details-view-mobile'>
          <ApplicationProfileHeaderConnector />
          <ProfilePreviewSection header={t('employer.applicants.application')}>
            <ApplicationProfileCoverLetterConnector />
            <ApplicationProfileCvDocumentConnector />
          </ProfilePreviewSection>
          <ProfilePreviewSection header={t('common.about')}>
            <ApplicationProfileAboutConnector />
            <ApplicationProfileGeneralInfoConnector />
          </ProfilePreviewSection>
          <ProfilePreviewSection header={t('profile.education.title')}>
            <ApplicationProfileEducationConnector />
          </ProfilePreviewSection>
          {
            hasCandidateAnyCertificates && (
              <ProfilePreviewSection header={t('profile.education.sections.additionalCertifications.label')}>
                <ApplicationProfileCertificatesConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyExperience && (
              <ProfilePreviewSection header={t('profile.experience.title')}>
                <ApplicationProfileExperienceConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyVolunteering && (
              <ProfilePreviewSection header={t('profile.experience.sections.volunteering.label')}>
                <ApplicationProfileVolunteeringConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnySkills && (
              <ProfilePreviewSection header={t('profile.experience.sections.skills.label')}>
                <ApplicationProfileSkillsConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyLanguages && (
              <ProfilePreviewSection header={t('profile.additional.sections.languages.label')}>
                <ApplicationProfileLanguagesConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateVideoCv && (
              <ProfilePreviewSection header={t('profile.media.sections.videoCV.label')}>
                <ApplicationProfileVideoCvConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasAnyAdditionalDocuments && (
              <ProfilePreviewSection header={t('profile.additional.sections.additionalDocuments.label')}>
                <ApplicationProfileAdditionalDocumentsConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyInterests && (
              <ProfilePreviewSection header={t('profile.experience.sections.interests.label')}>
                <ApplicationProfileInterestsConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyWebsites && (
              <ProfilePreviewSection header={t('candidate.profile.socialLinks.social')}>
                <ApplicationProfileWebsitesConnector />
              </ProfilePreviewSection>
            )
          }
        </div>
      </div>
    </MobileWindow>
  )
}

ApplicantDetailsView.defaultProps = {
  applicantDetails: {
    candidateId: '',
    coverLetter: '',
    cvId: ''
  }
}

ApplicantDetailsView.propTypes = {
  applicantDetails: PropTypes.shape({
    candidateId: PropTypes.string.isRequired,
    coverLetter: PropTypes.string,
    cvId: PropTypes.string.isRequired
  })
}

export default ApplicantDetailsView



