import React from 'react'
import { Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Checkbox, RadioWrapper } from 'Shared/components/FormElements'

const AdditionalPreferences = ({ t }) => {
  return (
    <div className='form__group'>
      <RadioWrapper
        name='commitment'
        legend={t('jobOffer.postAJob.additionalPreferences.title')}
        layout={'checkbox-vertical'}
        optional
      >
        <Field
          component={Checkbox}
          text={t('jobOffer.postAJob.additionalPreferences.coverLetterRequired')}
          name='coverLetterRequired'
          normalize={v => !!v}
        />
        <Field
          component={Checkbox}
          text={t('jobOffer.postAJob.additionalPreferences.preferredVideoCv')}
          name='preferredVideoCv'
          normalize={v => !!v}
        />
        <Field
          component={Checkbox}
          text={t('jobOffer.postAJob.additionalPreferences.preferredExperience')}
          name='preferredExperience'
          normalize={v => !!v}
        />
        <Field
          component={Checkbox}
          text={t('jobOffer.postAJob.additionalPreferences.isPrivate')}
          name='isPrivate'
          normalize={v => !!v}
          tooltip={t('employer.jobOffers.privateJobTooltip')}
        />
      </RadioWrapper>
    </div>
  )
}

export default withTranslations(AdditionalPreferences)



