import React, { Fragment } from 'react'
import './ApplicationDetailsActionButtons.scss'
import ApplicationProfileAvailabilityConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileAvailabilityConnector'
import ApplicationStatusThumbsToggleConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileStatusThumbsToggleConnector'
import ApplicationProfileInitChatConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileInitChatConnector'
import ApplicationProfileStatusDropdownConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileStatusDropdownConnector'
import ApplicationProfileDetailsInviteToInterviewTriggerConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileDetailsInviteToInterviewTriggerConnector'

function ApplicationDetailsActionButtons (props) {
  return (
    <Fragment>
      <div>
        <ApplicationStatusThumbsToggleConnector />
        <ApplicationProfileInitChatConnector />
        <ApplicationProfileStatusDropdownConnector />
        <ApplicationProfileDetailsInviteToInterviewTriggerConnector />
      </div>
      <ApplicationProfileAvailabilityConnector />
    </Fragment>
  )
}

export default ApplicationDetailsActionButtons



