import { connectAutoComplete } from 'react-instantsearch-dom'
import { connect } from 'react-redux'
import omit from 'lodash/omit'
import { transformById, transformToArray } from 'Shared/helpers/redux'
import MultiSelectInput from 'Shared/components/FormElements/Select/MultiSelectInput'
import FormUtilsService from 'Shared/services/FormUtilsService'

const formUtils = new FormUtilsService()

const mapStateToProps = (state, ownProps) => {
  const diff = omit({ ...transformById(ownProps.hits, 'entityId'), ...state.skills.entries },
    ownProps.selectedValues.map(option => option.value))

  return ({
    options: formUtils.mapCollectionToOptions(transformToArray(diff), 'entityId', 'name')
  })
}

export default connectAutoComplete(connect(mapStateToProps)(MultiSelectInput))



