import { connect } from 'react-redux'
import { isEmpty } from 'ramda'
import { withRouter } from 'react-router-dom'
import { fetchJobOfferByIdIfNeeded } from 'Shared/ducks/jobOffers'
import { fetchLanguagesIfNeeded } from 'Shared/ducks/resources'
import EditJobOffersView from 'Employer/views/EditJobOffersView/EditJobOffersView'

const mapStateToProps = state => ({
  languagesLoaded: !isEmpty(state.resources.languages.entries)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchJobOffer: () => dispatch(fetchJobOfferByIdIfNeeded(ownProps.match.params.jobOfferId)),
  fetchLanguages: () => dispatch(fetchLanguagesIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditJobOffersView))



