import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded, isInvalid } from 'redux-form'
import { formName as BulkInviteToInterviewForm } from 'Employer/connectors/Forms/BulkOperations/BulkInviteToInterviewFormConnector'
import { formName as BulkSendMessageForm } from 'Employer/connectors/Forms/BulkOperations/BulkSendMessageFormConnector'
import { getBulkOperationsItems } from 'Shared/selectors/jobOfferApplications'
import ApplicantsBulkManager from 'Employer/components/ApplicantsBulkManager/ApplicantsBulkManager'
import {
  downloadApplicantsCVs,
  resetBulkActionSelection,
  updateJobOfferApplicationsWithStatus,
  hideDownloadCVsConfirm
} from 'Shared/ducks/jobOfferApplications'
import { formName as declinedFormName } from 'Employer/connectors/Forms/DeclinedMessageFormConnector'
import { formName as hiredFormName } from 'Employer/connectors/Forms/Applications/HiredMessageFormConnector'

const mapStateToProps = state => ({
  bulkOperationItems: getBulkOperationsItems(state),
  bulkSendMessagesSucceeded: hasSubmitSucceeded(BulkSendMessageForm)(state),
  bulkInterviewInvitationSucceeded: hasSubmitSucceeded(BulkInviteToInterviewForm)(state),
  downloadCVsModalVisible: state.jobOfferApplications.downloadCVsModalVisible,
  declinedMessageFormValid: !isInvalid(declinedFormName)(state),
  hiredMessageFormValid: !isInvalid(hiredFormName)(state)
})

const mapDispatchToProps = (dispatch) => ({
  resetBulkActionSelection: () => dispatch(resetBulkActionSelection()),
  updateJobOfferApplicationsWithStatus: async (status, message) => {
    return dispatch(updateJobOfferApplicationsWithStatus(status, message))
  },
  downloadApplicantsCVs: () => dispatch(downloadApplicantsCVs()),
  bulkInterviewInvitations: () => dispatch(submit(BulkInviteToInterviewForm)),
  bulkSendMessages: () => dispatch(submit(BulkSendMessageForm)),
  hideDownloadCVsConfirm: () => dispatch(hideDownloadCVsConfirm()),
  submitDeclinedForm: () => dispatch(submit(declinedFormName)),
  submitHiredForm: () => dispatch(submit(hiredFormName))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ApplicantsBulkManager))



