import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ApplicantHit from 'Employer/views/ApplicantsView/components/ApplicantHit'
import {
  getApplicationStatusById,
  getApplicationChatId,
  getApplicationIdByCandidateAndJobOfferId,
  isItemSelectedForBulkAction,
  isAnyBulkSelected,
  isOlivPick,
  isApplicationBoosted,
  isApplicationAutoShortlisted
} from 'Shared/selectors/jobOfferApplications'
import { isChannelMember } from 'Chat/selectors/channels'
import { hasActiveConversation } from 'Shared/selectors/jobOfferApplicationChats'
import { highSchoolGradingSystemName } from 'Shared/selectors/resources'
import { deselectApplicationForBulkAction, selectApplicationForBulkAction } from 'Shared/ducks/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const applicationId = getApplicationIdByCandidateAndJobOfferId(state, ownProps.hit.id, ownProps.match.params.jobOfferId)
  const chatId = getApplicationChatId(state, applicationId)

  return ({
    applicationId,
    areChannelsFetched: state.chat.areChannelsFetched,
    applicationStatus: getApplicationStatusById(state, applicationId),
    isChannelMember: isChannelMember(state, chatId),
    hasActiveConversation: hasActiveConversation(state, chatId),
    joinToChannelLoading: state.chat.joinToChannelLoading,
    getHighSchoolGradingSystemName: systemCode => highSchoolGradingSystemName(state, systemCode),
    isItemSelectedForBulkAction: isItemSelectedForBulkAction(state, applicationId),
    isAnyBulkSelected: isAnyBulkSelected(state),
    isOlivPick: isOlivPick(state, applicationId),
    isBoosted: isApplicationBoosted(state, applicationId),
    isAutoShortlisted: isApplicationAutoShortlisted(state, applicationId)
  })
}

const mapDispatchToProps = dispatch => ({
  selectApplicationForBulkAction: (applicationId, candidateId, fullName, status, jobOfferId) => dispatch(selectApplicationForBulkAction(applicationId, candidateId, fullName, status, jobOfferId)),
  deselectApplicationForBulkAction: applicationId => dispatch(deselectApplicationForBulkAction(applicationId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ApplicantHit))



