import React from 'react'
import ProfileStepSection from 'Shared/components/ProfileStepSection'
import { Radio, RadioWrapper, Select } from 'Shared/components/FormElements'
import { Field } from 'redux-form'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import { t } from 'i18next'

const renderOption = ({ label, template }) => (
  <span>
    <span>{label}</span>
    <br />
    {template.jobType && <span className='template-select-option__job-type'>{t(`employer.jobOffers.jobTypes.${template.jobType}`)}</span>}
    <span className='template-select-option__job-posted-at'>{t('common.posted')}&nbsp;{formatDate(template.publishedAt, formats.dashed)}</span>
  </span>
)

const SelectTemplate = ({ templateValue, jobOfferTemplates, initializeBlank, fetchTemplate, untouch, fetchJobOfferTemplates, fetchPreviousJobOffersAsTemplates }) => {
  const mappedOptions = jobOfferTemplates.map((template) => {
    return ({ label: template.name || template.title, value: template.id, template })
  })

  const getOptionRendererIfNeeded = templateValue === 'popular' ? ({}) : ({ optionRenderer: renderOption })

  return (
    <div className='form__group'>
      <ProfileStepSection
        label={t('jobOffer.jobTemplate.title')}
        text={t('jobOffer.jobTemplate.description')}
      />
      <div className='form__group'>
        <RadioWrapper
          name='blank'
          legend={t('jobOffer.jobTemplate.fields.label')}
        >
          <Field
            className='step_basic__radio_button'
            component={Radio}
            text={t('jobOffer.jobTemplate.fields.blank')}
            name='template'
            radioButtonValue='blank'
            key='blank'
            onChange={initializeBlank}
          />
          <Field
            className='step_basic__radio_button'
            component={Radio}
            text={t('jobOffer.jobTemplate.fields.previous')}
            name='template'
            radioButtonValue='previous'
            key='previous'
            onChange={fetchPreviousJobOffersAsTemplates}
          />
          <Field
            className='step_basic__radio_button'
            component={Radio}
            text={t('jobOffer.jobTemplate.fields.popular')}
            name='template'
            radioButtonValue='popular'
            key='popular'
            onChange={fetchJobOfferTemplates}
          />
        </RadioWrapper>
        {
          (templateValue === 'popular' || templateValue === 'previous') &&
          <div className='form__group template-select'>
            <Field
              component={Select}
              options={mappedOptions}
              label={t('jobOffer.jobTemplate.selectTemplate.label')}
              placeholder={t('common.select')}
              onSelect={fetchTemplate}
              name='templateId'
              optionClassName='template-select-option'
              focusAction={untouch}
              onSelectBlur={() => {}}
              remoteChange
              withIcon
              {...getOptionRendererIfNeeded}
            />
          </div>
        }
      </div>
    </div>
  )
}

export default SelectTemplate



