import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Field } from 'redux-form'
import { Editor } from 'Shared/components/FormElements'
import { lengthLimit } from 'Shared/constants/Editor'
import ProfileStepSection from 'Shared/components/ProfileStepSection'
import TipButton from 'Shared/components/TipButton/TipButton'
import { ExternalRoutes } from 'Shared/constants/Routes'

const JobDescription = ({ t }) => {
  const showDescriptionTips = () => {
    window.open(ExternalRoutes.descriptionWritingGuide, '_blank')
  }

  return (
    <div>
      <div className='form__group__title--with-tip'>
        <ProfileStepSection
          label={t('jobOffer.postAJob.jobDescription.label')}
          text={t('jobOffer.postAJob.jobDescription.text')}
        />
        <TipButton onClick={showDescriptionTips}>{t('common.descriptionWritingTips')}</TipButton>
      </div>
      <div className='form__group'>
        <Field
          component={Editor}
          label={t('forms.roleDescription.label')}
          name='description'
          limit={lengthLimit.jobOfferDescriptionMax}
          placeholder={t('forms.roleDescription.placeholder')}
        />
      </div>
      <div className='form__group'>
        <Field
          component={Editor}
          label={t('forms.requirementsToApply.label')}
          name='requirements'
          limit={lengthLimit.jobOfferDescriptionMax}
          placeholder={t('forms.requirementsToApply.placeholder')}
        />
      </div>
    </div>
  )
}

export default withTranslations(JobDescription)



