import React from 'react'
import { lengthLimit } from 'Shared/constants/ReadMore'
import ReadMore from 'Shared/components/ReadMore/ReadMore'
import './CandidateProfileAbout.scss'

function CandidateProfileAbout (props) {
  return (
    <div className='candidate-profile-about-me__container'>
      <div className='candidate-profile-about-me'>
        <ReadMore text={props.pitch} minLength={lengthLimit.profileAboutMe} />
      </div>
    </div>
  )
}

export default CandidateProfileAbout



