import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JoinActiveChannelPopup from 'Chat/components/JoinActiveChannelPopup'
import { getApplicationCandidateId, getApplicationChatId, getApplicationJobOfferId } from 'Shared/selectors/jobOfferApplications'
import { getCandidateFirstName } from 'Shared/selectors/candidates'
import { joinChannel } from 'Chat/ducks/classes/channels'
import { getJobOfferApplicationChatSignature } from 'Shared/selectors/jobOfferApplicationChats'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.applicationId)
  const chatId = getApplicationChatId(state, ownProps.applicationId)
  const signature = getJobOfferApplicationChatSignature(state, chatId)

  return ({
    userId: state.user.details.id,
    candidateName: getCandidateFirstName(state, candidateId),
    jobOfferId: getApplicationJobOfferId(state, ownProps.applicationId),
    candidateId,
    signature
  })
}

const mapDispatchToProps = dispatch => ({
  joinChannel: (signature, { candidateId, employerIds, jobOfferId }) => dispatch(joinChannel(signature, { candidateId, employerIds, jobOfferId }))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JoinActiveChannelPopup))



