import React, { Component } from 'react'
import { Field } from 'redux-form'
import { pipe, prop, map } from 'ramda'
import withTranslations from 'Shared/hoc/withTranslations'
import { Input, Select, Radio, RadioWrapper, NumericInput } from 'Shared/components/FormElements'
import ProfileStepSection from 'Shared/components/ProfileStepSection/index'
import { EMPLOYMENT_TYPES } from 'Shared/constants/JobPreferences'
import { normalizeRadio } from 'Shared/helpers/forms'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { JobTypes } from 'Shared/constants/Job'
import { transformToArray } from 'Shared/helpers/redux'

const formUtilsService = new FormUtilsService()

const formatEmploymentTypes = () => (
  pipe(
    map(prop('id'))
  )(EMPLOYMENT_TYPES)
)

class JobDetails extends Component {
  state = {
    internshipConversionVisible: this.props.isJobAnInternship || false,
    departments: []}

  async componentDidMount () {
    const { departments } = await this.props.fetchDepartments()

    this.setState({
      departments: formUtilsService.mapOptionsByLanguageTranslations(transformToArray(departments), 'description')
    })
  }

  buildEmploymentTypeRadios (employmentTypes) {
    return map(type => (
      <Field
        className='step_basic__radio_button step_basic__radio_button--job-type'
        component={Radio}
        text={this.props.t(`common.jobType.${type}`)}
        name='jobType'
        radioButtonValue={type}
        key={type}
        description={this.props.t(`common.jobTypeDescription.${type}`)}
        onChange={() => this.toggleInternshipConversion(type)}
      />
    ))(employmentTypes)
  }

  toggleInternshipConversion (type) {
    const internshipConversionVisible = type === JobTypes().mirror.internship

    this.setState({
      internshipConversionVisible
    })
  }

  componentWillReceiveProps ({ isJobAnInternship }) {
    if (isJobAnInternship !== this.props.isJobAnInternship) {
      this.setState({ internshipConversionVisible: isJobAnInternship })
    }
  }

  internshipConversion () {
    if (!this.state.internshipConversionVisible) {
      return null
    }

    const { t, hasPermanentJobOpportunityFieldError } = this.props

    return (
      <div className='form__group'>
        <RadioWrapper
          legend={t('forms.permanentJobOpportunity.label')}
          name='permanentJobOpportunity'
          hasRequiredError={hasPermanentJobOpportunityFieldError}
        >
          <Field
            className='step_basic__radio_button'
            component={Radio}
            text={t('common.yesIDo')}
            normalize={normalizeRadio}
            name='permanentJobOpportunity'
            radioButtonValue
          />
          <Field
            className='step_basic__radio_button'
            component={Radio}
            text={t('common.noIDont')}
            name='permanentJobOpportunity'
            normalize={normalizeRadio}
            radioButtonValue={false}
          />
        </RadioWrapper>
      </div>
    )
  }

  render () {
    const { t, hasJobTypeFieldError } = this.props
    const { departments } = this.state

    return (
      <div>
        <ProfileStepSection
          label={t('jobOffer.jobDetails.title')}
          text={t('jobOffer.jobDetails.description')}
        />

        <Field
          component={Input}
          label={t('forms.jobTitle.label')}
          placeholder={t('forms.jobTitle.placeholder')}
          name='title'
          type='text'
        />

        <div className='columns form__wrapper--inline'>
          <div className='column col-6'>
            <Field
              component={Select}
              options={departments}
              label={t('forms.jobCategory.label')}
              placeholder={t('common.select')}
              name='departmentId'
              withIcon
            />
          </div>
          <div className='column col-6'>
            <Field
              component={NumericInput}
              label='Number of vacancies'
              name='numberOfVacancies'
              min={1}
            />
          </div>
        </div>
        <div className='form__group'>
          <RadioWrapper
            name='jobType'
            legend={t('forms.employmentType.label')}
            hasRequiredError={hasJobTypeFieldError}
          >
            {this.buildEmploymentTypeRadios(formatEmploymentTypes())}
          </RadioWrapper>
        </div>
        {this.internshipConversion()}
      </div>
    )
  }
}

export default withTranslations(JobDetails)



