import React from 'react'
import PropTypes from 'prop-types'
import DropdownMenu from 'Shared/components/DropdownMenu'
import withTranslations from 'Shared/hoc/withTranslations'
import './ApplicationDropdownMenu.scss'
import { arrayApplicantStatuses } from 'Shared/constants/Applicants'

const ApplicationDropdownMenu = (props) => {
  const { jobOfferId } = props.match.params

  const changeApplicationStatus = (status) => () => {

    console.log('changeApplicationStatus', status)

    return props.handleApplicationAction({
      buttonStatus: status,
      applicantStatus: props.currentStatus,
      candidateName: props.candidateName,
      changeStatus: (optionalArguments = {}) => {
        return props.changeApplicationStatus({
          jobOfferId,
          candidateId: props.candidateId,
          status,
          id: props.applicationId,
          oldStatus: props.currentStatus,
          candidateName: props.candidateName,
          ...optionalArguments
        })
      }
    })
  }

  const generateItems = () => {
    return arrayApplicantStatuses.filter(menuItem => menuItem !== props.currentStatus)
      .map((menuItem, index) => {
        return (
          <li
            key={index}
            className='job-list__grid--dropdown'
            onClick={changeApplicationStatus(menuItem)}
          >
            {props.t(`employer.applicants.applicantsType.${menuItem}`)}
          </li>
        )
      })
  }

  return (
    <span className='application-dropdown__menu'>
      <DropdownMenu
        triggerButton={props.triggerButton}
        align={'right'}
        closeOnOutsideClick
      >
        <li className='job-list__grid--label'>{props.t('common.moveTo')}:</li>
        {generateItems()}
      </DropdownMenu>
    </span>
  )
}

ApplicationDropdownMenu.propTypes = {
  candidateId: PropTypes.string.isRequired,
  applicationId: PropTypes.string,
  currentStatus: PropTypes.string,
  changeApplicationStatus: PropTypes.func.isRequired,
  triggerButton: PropTypes.node.isRequired,
  handleApplicationAction: PropTypes.func.isRequired
}

export default withTranslations(ApplicationDropdownMenu)



