import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import DeclineApplicationModal from 'Employer/components/DeclineApplicationModal'
import { hasSubmitSucceeded, submit, isSubmitting } from 'redux-form'
import { formName as declinedFormName } from 'Employer/connectors/Forms/Applications/DeclineApplicationFormConnector'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import { getCandidateFirstName } from 'Shared/selectors/candidates'

const mapStateToProps = (state, ownProps) => {
  const applicationId = ownProps.applicationId
  const candidateId = getApplicationCandidateId(state, applicationId)

  return ({
    applicationId,
    shouldCloseModal: hasSubmitSucceeded(declinedFormName)(state),
    isModalSubmitting: isSubmitting(declinedFormName)(state),
    candidateFirstName: getCandidateFirstName(state, candidateId)
  })
}

const mapDispatchToProps = dispatch => ({
  submitDeclinedForm: () => dispatch(submit(declinedFormName))
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(DeclineApplicationModal)



