import React, { Component } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { applicationStatuses, statusesToAcceptApplicant } from 'Shared/constants/Applicants'
import DeclinedMessageForm from 'Employer/connectors/Forms/DeclinedMessageFormConnector'
import HiredMessageFormConnector from 'Employer/connectors/Forms/Applications/HiredMessageFormConnector'
import { t } from 'i18next'
import Modal from 'Shared/components/Modal'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import { shouldTriggerConfirmModal } from 'Shared/helpers/jobApplications'
import { isMobile } from 'Shared/helpers/mobile'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { showPropOr } from 'Shared/helpers/responseData'
import { SmallJustIconButton } from 'Shared/components/Button'
import ApplicationDropdownMenuConnector from 'Employer/connectors/Components/ApplicationDropdownMenuConnector'
import InterviewInviteFormConnector from 'Employer/connectors/Forms/InterviewInvite/InterviewInviteFormConnector'
import InviteModal from 'Employer/components/InviteModal/InviteModal'
import ToggleApplicantWrapper from 'Employer/components/ToogleApplicantWrapper'
import JoinActiveChannelPopupConnector from 'Chat/connectors/Components/JoinActiveChannelPopup/JoinActiveChannelPopupConnector'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'
import * as R from 'ramda'
import { GoogleMeet } from 'Employer/views/JobOffersView/GoogleMeet'
import { getLocalData } from 'Shared/helpers/session'

class ApplicantHitButtons extends Component {
  state = {
    statusModalVisible: false,
    changeStatusAction: () => {},
    candidateName: '',
    buttonStatus: 'unset',
    initiatingChatConversation: false,
    inviteModalVisible: false,
    googleAuthLink: false,
    hasGoogleToken: false,
    isExpiredGoogleToken: false,
    employerId: this.props.userDetails && this.props.userDetails.id
  }

  getLink = async () => {
    await this.props.apiGetGoogleAuthLink().then(res => {
      this.setState({googleMeetAuthLink: res})
    })
  }

  isExpiredGoogleToken = async () => {
    this.props.apiIsExpiredGoogleToken(this.state.employerId).then(res => {
      res && this.getLink()
      this.setState({isExpiredGoogleToken: res})
    })
  }

  getGoogleToken = async () => {
    await this.props.apiGetGoogleToken(this.state.employerId).then(res => {
      !res && this.getLink()
      this.setState({hasGoogleToken: res})
    })
  }

  handleGoogleMeet = () => {
    this.isExpiredGoogleToken()
    this.getGoogleToken()
  }

  componentDidMount () { }

  componentDidUpdate (prevProps, prevState) {
    this.setState({hasGoogleToken: getLocalData('googleToken')})
    this.setState({googleAuthLink: getLocalData('googleAuthLink')})
    this.setState({isExpiredGoogleToken: getLocalData('isExpiredGoogleToken')})
  }

  shouldComponentUpdate (nextProps, nextState, nextContext) {
    return R.not(R.equals(this.props.applicationId, nextProps.applicationId)) ||
      R.not(R.equals(this.props.applicationStatus, nextProps.applicationStatus)) ||
      R.not(R.equals(this.props.applicationsLoading, nextProps.applicationsLoading)) ||
      R.not(R.equals(this.props.areChannelsFetched, nextProps.areChannelsFetched)) ||
      R.not(R.equals(this.props.declinedMessageFormValid, nextProps.declinedMessageFormValid)) ||
      R.not(R.equals(this.props.hasActiveConversation, nextProps.hasActiveConversation)) ||
      R.not(R.equals(this.props.hiredMessageFormValid, nextProps.hiredMessageFormValid)) ||
      R.not(R.equals(this.props.hit, nextProps.hit)) ||
      R.not(R.equals(this.props.isChannelMember, nextProps.isChannelMember)) ||
      R.not(R.equals(this.props.jobOffer, nextProps.jobOffer)) ||
      R.not(R.equals(this.props.joinToChannelLoading, nextProps.joinToChannelLoading)) ||
      R.not(R.equals(this.props.location, nextProps.location)) ||
      R.not(R.equals(this.state.buttonStatus, nextState.buttonStatus)) ||
      R.not(R.equals(this.state.candidateName, nextState.candidateName)) ||
      R.not(R.equals(this.state.initiatingChatConversation, nextState.initiatingChatConversation)) ||
      R.not(R.equals(this.state.inviteModalVisible, nextState.inviteModalVisible)) ||
      R.not(R.equals(this.state.statusModalVisible, nextState.statusModalVisible)) ||
      R.not(R.equals(this.state.changeStatusAction, nextState.changeStatusAction))
  }

  resetModal = () =>
    this.setState({
      statusModalVisible: false,
      changeStatusAction: () => {}
    })

  openInviteToInterviewModal = () => this.setState({ inviteModalVisible: true })
  closeInviteToInterviewModal = () => this.setState({ inviteModalVisible: false })

  getConfirmationModal = (buttonStatus) => {
    switch (buttonStatus) {
      case applicationStatuses.declined:
        return (
          <Modal
            title={t('modals.changeApplicationStatus.declinedTitle')}
            handleSubmit={this.props.submitDeclinedForm}
            show={this.state.statusModalVisible}
            handleCancel={this.resetModal}
            handleClose={this.resetModal}
            cancelText={t('common.cancel')}
            submitText={t('common.submit')}
            showContact={false}
            showCancel
            closeOnSubmit={this.props.declinedMessageFormValid}
          >
            <DeclinedMessageForm
              changeStatusAction={this.state.changeStatusAction}
              candidateFirstName={this.state.candidateName}
            />
          </Modal>
        )
      case applicationStatuses.hired:
        return (
          <Modal
            title={t('modals.changeApplicationStatus.title', { status: t('employer.jobOffers.jobOffersType.hired') })}
            handleSubmit={this.props.submitHiredForm}
            show={this.state.statusModalVisible}
            handleCancel={this.resetModal}
            handleClose={this.resetModal}
            cancelText={t('common.cancel')}
            submitText={t('common.submit')}
            showContact={false}
            showCancel
            closeOnSubmit={this.props.hiredMessageFormValid}
          >
            <HiredMessageFormConnector
              changeStatusAction={this.state.changeStatusAction}
              candidateFirstName={this.state.candidateName}
            />
          </Modal>
        )
      default:
        return (
          <ConfirmModal
            title={t('modals.changeApplicationStatus.title', { status: this.state.buttonStatus })}
            content={t('modals.changeApplicationStatus.description', { status: this.state.buttonStatus })}
            handleConfirm={this.state.changeStatusAction}
            loading={this.props.applicationsLoading}
            isModalVisible={this.state.statusModalVisible}
            hideConfirmModal={this.resetModal}
            closeOnSubmit={false}
          />
        )
    }
  }

  handleApplicationAction = (actionObj) => {
    const { buttonStatus, applicantStatus, candidateName } = actionObj
    /** open modal only if status is different. **/
    const changeApplicantStatus = async (optionalArguments = {}) => {
      await actionObj.changeStatus(optionalArguments)
      return this.resetModal()
    }

    return shouldTriggerConfirmModal(buttonStatus, applicantStatus)
      ? this.setState({
        statusModalVisible: true,
        buttonStatus,
        applicantStatus,
        candidateName,
        changeStatusAction: changeApplicantStatus
      })
      : changeApplicantStatus()
  }

  get toggleButtons () {
    const {
      applicationId,
      applicationStatus,
      hit,
      match: { params: { jobOfferId } }
    } = this.props

    return statusesToAcceptApplicant.indexOf(applicationStatus) > -1 && (
      <ToggleApplicantWrapper
        smallBtn
        handleApplicationAction={this.handleApplicationAction}
        candidateName={showPropOr('firstName', '', hit)}
        jobOfferId={jobOfferId}
        applicationId={applicationId}
        candidateId={hit.id}
        applicantStatus={applicationStatus}
        buttonComponent={<SmallJustIconButton />}
      />
    )
  }

  handleOpenChatConversation = async (candidateId, applicationId) => {
    await this.props.startChatConversation(this.props.jobOffer, candidateId, applicationId)
    const { sid } = this.props.getChannelByJobOfferAndCandidateId(this.props.jobOffer.id, candidateId)
    return this.props.history.push(`/employer/chat/${sid}`)
  }

  startChatConversation = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    const { id } = this.props.hit
    this.setState({ initiatingChatConversation: true })
    await this.handleOpenChatConversation(id, this.props.applicationId)
    return this.setState({ initiatingChatConversation: false })
  }

  get chatConversation () {
    const { isChannelMember, hasActiveConversation, areChannelsFetched, applicationId, joinToChannelLoading } = this.props

    switch (true) {
      case areChannelsFetched && hasActiveConversation && !isChannelMember:
        return (
          <JoinActiveChannelPopupConnector applicationId={applicationId}>
            <SmallJustIconButton className='tooltip-container-margin' loading={joinToChannelLoading}>
              {!joinToChannelLoading && <i className='ion-md-chatboxes' />}
            </SmallJustIconButton>
          </JoinActiveChannelPopupConnector>
        )
      case areChannelsFetched:
        return (
          <SmallJustIconButton
            className='tooltip-container-margin'
            loading={this.state.initiatingChatConversation}
            disabled={this.state.initiatingChatConversation}
            onClick={this.startChatConversation}>
            {this.state.initiatingChatConversation ? null : <i className='ion-md-chatboxes' /> }
          </SmallJustIconButton>
        )
      default:
        return (
          <SmallJustIconButton className='tooltip-container-margin' loading />
        )
    }
  }

  render () {
    const { hit, applicationId, applicationStatus } = this.props

    return (
      <div>
        {this.toggleButtons}
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton className='tooltip-container-margin'><i className='ion-md-chatboxes' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          {
            isMobile()
              ? this.chatConversation
              : <Tooltip content={this.props.t('employer.applicants.chatApplicantTooltip')}>
                {this.chatConversation}
              </Tooltip>
          }
        </LimitedAccessWrapperConnector>
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton><i className='ion-ios-more' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          <ApplicationDropdownMenuConnector
            jobOfferId={this.props.match.params.jobOfferId}
            handleApplicationAction={this.handleApplicationAction}
            candidateName={showPropOr('firstName', '', hit)}
            candidateId={hit.id}
            applicationId={applicationId}
            currentStatus={applicationStatus}
            openInviteToInterviewModal={this.openInviteToInterviewModal}
            triggerButton={<SmallJustIconButton><i className='ion-ios-more' onClick={(e) => { this.handleGoogleMeet() }} /></SmallJustIconButton>}
          />
        </LimitedAccessWrapperConnector>
        {this.state.statusModalVisible && this.getConfirmationModal(this.state.buttonStatus)}
        {this.state.inviteModalVisible && (
          <InviteModal
            title={this.props.t('modals.inviteToInterview.title')}
            isModalVisible={this.state.inviteModalVisible}
            hideInviteModal={this.closeInviteToInterviewModal}
            candidate={hit}
            closeOnSubmit={false}
          >
            <div>
              {/* {JSON.stringify(this.state)} */}
              {/* { ((this.state.googleMeetAuthLink) && (this.state.isExpiredGoogleToken || !this.state.hasGoogleToken)) &&  } */}

              {(!this.state.isExpiredGoogleToken || this.state.hasGoogleToken)
                ? <InterviewInviteFormConnector applicationId={applicationId} candidate={hit} />
                : <GoogleMeet googleMeetAuthLink={this.state.googleAuthLink} /> }
            </div>
          </InviteModal>
        )}
      </div>
    )
  }
}

export default withTranslations(ApplicantHitButtons)



