import { createSelector } from 'reselect'
import * as R from 'ramda'
import { transformToArray } from 'Shared/helpers/redux'
import { getUserDetails } from 'Shared/selectors/user'
import { getEmployers } from 'Shared/selectors/employers'

export const getJobOffersMembers = state => state.jobOffersMembers.entries

const transformUserToOption = R.applySpec({
  value: R.prop('id'),
  label: value => R.prop('firstName', value) + ' ' + R.prop('lastName', value),
  firstName: R.prop('firstName'),
  photoUrl: R.prop('photoUrl'),
  avatarColour: R.prop('avatarColour'),
  clearableValue: R.always(true)
})

export const getJobOfferMembersById = createSelector(
  getJobOffersMembers,
  getUserDetails,
  (state, jobOfferId) => jobOfferId,
  (jobOffersMembers, userDetails, jobOfferId) => {
    const jobOfferMembers = transformToArray(
      R.pickBy(
        R.pipe(
          R.prop('jobOfferId'),
          R.equals(jobOfferId)
        ),
        jobOffersMembers
      )
    )
    return R.map(
      R.pipe(
        R.propOr({}, 'employer'),
        R.ifElse(
          R.propEq('id', userDetails.id),
          getOwnSelection,
          transformUserToOption
        )
      )
    )(jobOfferMembers)
  }
)

const getOwnSelection = R.pipe(
  transformUserToOption,
  R.mergeDeepLeft({ clearableValue: false })
)

export const getDefaultMemberOptionIfNoMemberExist = createSelector(
  getJobOffersMembers,
  (state, jobOfferId) => jobOfferId,
  getUserDetails,
  getJobOfferMembersById,
  (jobOfferMembers, jobOfferId, userDetails, selectedMembers) => {
    return R.pipe(
      R.filter(R.propEq('jobOfferId', jobOfferId)),
      R.length,
      R.ifElse(
        R.equals(0),
        R.always([getOwnSelection(userDetails)]),
        R.always(selectedMembers)
      )
    )(transformToArray(jobOfferMembers))
  }
)

export const getCurrentUserAsTeamMemberOption = createSelector(
  getUserDetails,
  getOwnSelection
)

export const getJobOfferMembersOptions = createSelector(
  getEmployers,
  getUserDetails,
  (employers, userDetails) => {
    return R.pipe(
      transformToArray,
      R.filter(R.propEq('organisationId', userDetails.organisationId)),
      R.map(
        R.ifElse(
          R.propEq('id', userDetails.id),
          getOwnSelection,
          transformUserToOption
        )
      )
    )(employers)
  }
)



