import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileAvailability from 'Employer/components/CandidateProfileDetails/CandidateProfileAvailability'
import { candidateAvailabilityRange, isCandidateAvailable } from 'Shared/selectors/candidates'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    userAvailability: candidateAvailabilityRange(state, candidateId),
    isAvailable: isCandidateAvailable(state, candidateId)
  })
}

const mapDispatchToProps = dispatch => ({})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(CandidateProfileAvailability)



