import React, { useState } from 'react'
import { t } from 'i18next'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import ApplicationDropdownMenuConnector from 'Employer/connectors/Components/ApplicationDropdownMenuConnector'
import { BigJustIconButton } from 'Shared/components/Button'
import { shouldTriggerConfirmModal } from 'Shared/helpers/jobApplications'
import applicantStatuses from 'Shared/constants/Applicants'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import DeclineApplicationModalConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileDeclineModalConnector'
import ApplicationProfileHireModalConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileHireModalConnector'
import { EmployerPermissions } from 'Shared/constants/User'
import InviteModal from 'Employer/components/InviteModal/InviteModal'
import InterviewInviteFormConnector from 'Employer/connectors/Forms/InterviewInvite/InterviewInviteFormConnector'
import './ApplicationStatusChangeDropdown.scss'

const TriggerButton = props => <BigJustIconButton {...props} className='application-status-change-dropdown__button'><i className='ion-ios-more' /></BigJustIconButton>

function ApplicationStatusChangeDropdown (props) {
  const [changeStatusModalVisibility, setChangeStatusModalVisibility] = useState(false)
  const [inviteToInterviewModalVisibility, setInviteToInterviewsModalVisibility] = useState(false)
  const [buttonStatus, setButtonStatus] = useState('unset')
  const [changeStatusAction, setChangeStatusAction] = useState(() => {})

  const openInviteToInterviewModal = () => setInviteToInterviewsModalVisibility(true)
  const closeInviteToInterviewModal = () => setInviteToInterviewsModalVisibility(false)

  const setChangeStatusState = (buttonStatus, changeStatusAction) => {
    setChangeStatusModalVisibility(true)
    setButtonStatus(buttonStatus)
    setChangeStatusAction(changeStatusAction)
  }

  const resetState = () => {
    setChangeStatusModalVisibility(false)
    setButtonStatus('unset')
    setChangeStatusAction(() => {})
  }

  const handleChangeStatus = actionObj => {
    const { buttonStatus, applicantStatus, candidateName } = actionObj

    const changeApplicantStatus = async (optionalArguments = {}) => {
      await actionObj.changeStatus(optionalArguments)
    }

    return shouldTriggerConfirmModal(buttonStatus, applicantStatus)
      ? setChangeStatusState(buttonStatus, applicantStatus, candidateName, changeApplicantStatus)
      : actionObj.changeStatus()
  }

  const getConfirmStatusModal = selectedStatus => {
    switch (selectedStatus) {
      case applicantStatuses.declined:
        return (
          <DeclineApplicationModalConnector
            applicationId={props.applicationId}
            show={changeStatusModalVisibility}
            onClose={resetState}
          />
        )
      case applicantStatuses.hired:
        return (
          <ApplicationProfileHireModalConnector
            applicationId={props.applicationId}
            show={changeStatusModalVisibility}
            onClose={resetState}
          />
        )
      default:
        return (
          <ConfirmModal
            title={t('modals.changeApplicationStatus.title', { status: buttonStatus })}
            content={t('modals.changeApplicationStatus.description', { status: buttonStatus })}
            handleConfirm={changeStatusAction}
            loading={props.applicationsLoading}
            isModalVisible={changeStatusModalVisibility}
            hideConfirmModal={resetState}
            closeOnSubmit={false}
          />
        )
    }
  }

  const inviteToInterviewModal = () => (
    <InviteModal
      title={t('modals.inviteToInterview.title')}
      isModalVisible={inviteToInterviewModalVisibility}
      hideInviteModal={closeInviteToInterviewModal}
      candidate={props.candidate}
      closeOnSubmit={false}
    >
      <InterviewInviteFormConnector />
    </InviteModal>
  )

  return (
    <div className='application-status-change-dropdown__container'>
      <LimitedAccessWrapperConnector
        placeholderComponent={<TriggerButton />}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        <ApplicationDropdownMenuConnector
          handleApplicationAction={handleChangeStatus}
          closeChangeStatusModal={resetState}
          applicationId={props.applicationId}
          candidateName={props.candidateName}
          candidateId={props.candidateId}
          currentStatus={props.applicationStatus}
          openInviteToInterviewModal={openInviteToInterviewModal}
          triggerButton={<TriggerButton />}
        />
      </LimitedAccessWrapperConnector>
      {getConfirmStatusModal(buttonStatus)}
      {inviteToInterviewModal()}
    </div>
  )
}

export default ApplicationStatusChangeDropdown



