import React, { useEffect } from 'react'
import { t } from 'i18next'
import Modal from 'Shared/components/Modal'
import HireApplicationFormConnector from 'Employer/connectors/Forms/Applications/HireApplicationFormConnector'
import './HireApplicationModal.scss'

function HireApplicationModal (props) {
  useEffect(() => {
    if (props.shouldCloseModal) {
      props.onClose()
    }
  }, [props.shouldCloseModal])

  return (
    <Modal
      title={t('modals.changeApplicationStatus.title', { status: t('employer.jobOffers.jobOffersType.hired') })}
      show={props.show}
      handleClose={props.onClose}
      handleCancel={props.onClose}
      handleSubmit={props.submitDeclinedForm}
      disableSubmitButton={props.isModalSubmitting}
      disableCancelButton={props.isModalSubmitting}
      closeOnSubmit={false}
      cancelText={t('common.cancel')}
      submitText={props.isModalSubmitting ? t('common.loading') : t('common.submit')}
    >
      <HireApplicationFormConnector
        applicationId={props.applicationId}
        candidateFirstName={props.candidateFirstName}
      />
    </Modal>
  )
}

export default HireApplicationModal



