import React, { PureComponent } from 'react'
import { FieldArray } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import SlotRow from './components/SlotRow'
import './TimeSlotsManager.scss'

class TimeSlotsManager extends PureComponent {
  render () {
    return (
      <div className='time-slots-manager'>
        <div className='interview-invite-form__section-title'>
          {this.props.t('modals.inviteToInterview.timeSlotsTitle')}
        </div>
        <div className='interview-invite-form__section-description'>
          {this.props.t('modals.inviteToInterview.timeSlotsContent')}
        </div>
        <FieldArray
          component={SlotRow}
          name='timeSlots'
        />
      </div>
    )
  }
}

export default withTranslations(TimeSlotsManager)



