import React, { useState } from 'react'
import { t } from 'i18next'
import InviteModal from 'Employer/components/InviteModal/InviteModal'
import InterviewInviteFormConnector from 'Employer/connectors/Forms/InterviewInvite/InterviewInviteFormConnector'
import { PrimaryButton } from 'Shared/components/Button'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import { EmployerPermissions } from 'Shared/constants/User'
import JoinActiveChannelPopupConnector from 'Chat/connectors/Components/JoinActiveChannelPopup/JoinActiveChannelPopupConnector'
import './InviteToInterviewTrigger.scss'

function InviteToInterviewTrigger (props) {
  const { interviewInvitationStatus } = props
  const [initiatingChatConversation, setInitiatingChatConversation] = useState(false)
  const [inviteToInterviewModalVisibility, setInviteToInterviewsModalVisibility] = useState(false)

  const openInviteToInterviewModal = () => setInviteToInterviewsModalVisibility(true)
  const closeInviteToInterviewModal = () => setInviteToInterviewsModalVisibility(false)

  const startChatConversation = async () => {
    setInitiatingChatConversation(true)
    await props.startChatConversation(props.jobOffer, props.candidateId, props.applicationId)
    const { sid } = await props.getChannelByJobOfferAndCandidateId
    setInitiatingChatConversation(false)
    return props.history.push(`/employer/chat/${sid}`)
  }

  const inviteToInterviewModal = () => (
    <InviteModal
      title={t('modals.inviteToInterview.title')}
      isModalVisible={inviteToInterviewModalVisibility}
      hideInviteModal={closeInviteToInterviewModal}
      candidate={props.candidate}
      closeOnSubmit={false}
    >
      <InterviewInviteFormConnector />
    </InviteModal>
  )

  const wasInvited = interviewInvitationStatus === InterviewInvitationStatuses.invited

  const TriggerButton = (props) => {
    return (
      <PrimaryButton
        {...props}
        withIcon
        className='invite-to-interview-trigger__button'
        iconClassName={wasInvited ? 'ion-md-checkmark' : 'ion-md-calendar'}
      >
        { wasInvited ? t('common.viewInvitation') : t('common.inviteToInterview') }
      </PrimaryButton>
    )
  }

  const getChatButton = () => {
    const { isChannelMember, hasActiveConversation, areChannelsFetched, applicationId, joinToChannelLoading } = props

    switch (true) {
      case areChannelsFetched && hasActiveConversation && !isChannelMember:
        return (
          <JoinActiveChannelPopupConnector applicationId={applicationId}>
            <TriggerButton disabled={joinToChannelLoading} loading={joinToChannelLoading} />
          </JoinActiveChannelPopupConnector>
        )
      case areChannelsFetched:
        return (
          <TriggerButton disabled={joinToChannelLoading} loading={joinToChannelLoading} onClick={startChatConversation} />
        )
      default:
        return (
          <TriggerButton loading disabled />
        )
    }
  }

  const getInviteToInterviewButton = () => (
    <TriggerButton
      disabled={initiatingChatConversation || !interviewInvitationStatus}
      loading={!interviewInvitationStatus}
      onClick={wasInvited ? startChatConversation : openInviteToInterviewModal}
    />
  )

  const getTriggerButton = () => wasInvited ? getChatButton() : getInviteToInterviewButton()

  return (
    <div className='invite-to-interview-trigger__container'>
      <LimitedAccessWrapperConnector
        placeholderComponent={<TriggerButton />}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        {getTriggerButton()}
      </LimitedAccessWrapperConnector>
      {inviteToInterviewModal()}
    </div>
  )
}

export default InviteToInterviewTrigger



