import React, { Component } from 'react'
import { Field } from 'redux-form'
import { Select, SearchMap } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import ProfileStepSection from 'Shared/components/ProfileStepSection'
import { apiCitySearch } from 'Shared/services/api/useApiGetCities'
import debounce from 'lodash/debounce'
class Location extends Component {
  state = {
    locationInputValue: '',
    locationOptions: []
  }

  onKeyUp = (searchKeyword) => {
    if (searchKeyword.length === 0) return
    apiCitySearch(null, { city: searchKeyword || this.state.locationInputValue }).then(res => {
      this.setState({locationOptions: res})
    })
  }

  searchWithDebounce = debounce(this.onKeyUp, 1000)

  render () {
    const { t, formValues } = this.props
    // console.log(this.props)
    const hasAddress = (formValues && formValues.address && formValues.address) || ''
    const transformAddress = hasAddress && { value: hasAddress, label: hasAddress, country: '', country_name: '' }
    const citiesWithCountry = [transformAddress, ...this.state.locationOptions].map(city => {
      const cityWithCountryCode = city.value && `${city.value} ${city.country && `, ${city.country}`}`
      const cityWithCountryName = city.value && `${city.value} ${city.country_name && `, ${city.country_name}`}`
      return ({...city, value: cityWithCountryCode, label: cityWithCountryName})
    })

    return (
      <div>
        <ProfileStepSection
          label={t('jobOffer.postAJob.location.label')}
          text={t('jobOffer.postAJob.location.text')}
        />
        <Field
          component={Select}
          options={citiesWithCountry}
          name='locationCityWithCountry'
          placeholder={hasAddress || 'Search and select location'}
          onInputChange={(e) => {
            this.setState({locationInputValue: e})
            this.searchWithDebounce(e)
          }}
          className={hasAddress && 'hasAddress'}
        />
        {/* <Field
          component={Select}
          options={citiesWithCountry}
          placeholder={t('forms.location.placeholder')}
          name='location'
        /> */}
      </div>
    )
  }
}

export default withTranslations(Location)



