import React, { Component } from 'react'
import { ApplicantHitLoader } from 'Shared/components/HitLoader'

class ApplicantListLoader extends Component {
  render () {
    return (
      <div className='columns'>
        {
          [1, 2, 3, 4, 5, 6, 7, 8, 9].map((e, i) => <ApplicantHitLoader key={`hit-loader-${i}`} />)
        }
      </div>
    )
  }
}

export default ApplicantListLoader



