import React from 'react'
import PropTypes from 'prop-types'
import applicantStatuses, { arrayApplicantStatuses } from 'Shared/constants/Applicants'

const ToggleApplicantButton = props => {
  const isButtonActive = (currentStatus, buttonStatus) =>
    props.applicantStatus !== applicantStatuses.undecided && currentStatus === buttonStatus

  const changeApplicationStatus = (e) => {
    e.stopPropagation()
    e.preventDefault()
    return props.handleApplicationAction({
      buttonStatus: props.buttonMarkStatus,
      applicantStatus: props.applicantStatus,
      candidateName: props.candidateName,
      changeStatus: (optionalArguments = {}) => {
        return props.changeApplicationStatus({
          jobOfferId: props.jobOfferId,
          candidateId: props.candidateId,
          status: props.buttonMarkStatus === props.applicantStatus ? applicantStatuses.undecided : props.buttonMarkStatus,
          id: props.applicationId,
          oldStatus: props.applicantStatus,
          candidateName: props.candidateName,
          ...optionalArguments
        })
      }
    })
  }
  return (
    {
      ...React.cloneElement(props.buttonComponent, {
        active: isButtonActive(props.applicantStatus, props.buttonMarkStatus),
        onClick: changeApplicationStatus,
        ...props
      })
    }
  )
}

ToggleApplicantButton.propTypes = {
  applicantStatus: PropTypes.oneOf(arrayApplicantStatuses).isRequired,
  buttonMarkStatus: PropTypes.oneOf(arrayApplicantStatuses).isRequired,
  jobOfferId: PropTypes.string.isRequired,
  applicationId: PropTypes.string.isRequired,
  candidateId: PropTypes.string.isRequired,
  children: PropTypes.node,
  changeApplicationStatus: PropTypes.func.isRequired,
  handleApplicationAction: PropTypes.func.isRequired
}

export default ToggleApplicantButton



