import React from 'react'
import PropTypes from 'prop-types'
import ToggleApplicantButtonConnector from 'Employer/connectors/Components/ToggleApplicantButtonConnector'
import applicantStatuses, { arrayApplicantStatuses } from 'Shared/constants/Applicants'
import { SmallJustIconButton, BigJustIconButton } from 'Shared/components/Button'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import withTranslations from 'Shared/hoc/withTranslations'
import { isMobile } from 'Shared/helpers/mobile'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'

const ToggleApplicantWrapper = props => {
  const ButtonPlaceholder = props.smallBtn ? SmallJustIconButton : BigJustIconButton

  const shorlistButton = () => isMobile()
    ? <ToggleApplicantButtonConnector className='tooltip-container-margin' {...props} buttonMarkStatus={applicantStatuses.shortlisted}>
      <i className='ion-md-thumbs-up' />
    </ToggleApplicantButtonConnector>
    : <Tooltip content={props.t('employer.applicants.shortlistApplicantTooltip')} className='tooltip-container-margin'>
      <ToggleApplicantButtonConnector {...props} buttonMarkStatus={applicantStatuses.shortlisted}>
        <i className='ion-md-thumbs-up' />
      </ToggleApplicantButtonConnector>
    </Tooltip>

  const declineButton = () => isMobile()
    ? <ToggleApplicantButtonConnector className='tooltip-container-margin' {...props} buttonMarkStatus={applicantStatuses.declined}>
      <i className='ion-md-thumbs-down' />
    </ToggleApplicantButtonConnector>
    : <Tooltip content={props.t('employer.applicants.declineApplicantTooltip')} className='tooltip-container-margin'>
      <ToggleApplicantButtonConnector {...props} buttonMarkStatus={applicantStatuses.declined}>
        <i className='ion-md-thumbs-down' />
      </ToggleApplicantButtonConnector>
    </Tooltip>

  return (
    <React.Fragment>
      <LimitedAccessWrapperConnector
        placeholderComponent={<ButtonPlaceholder className='tooltip-container-margin'><i className='ion-md-thumbs-up' /></ButtonPlaceholder>}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        {shorlistButton()}
      </LimitedAccessWrapperConnector>
      <LimitedAccessWrapperConnector
        placeholderComponent={<ButtonPlaceholder className='tooltip-container-margin'><i className='ion-md-thumbs-down' /></ButtonPlaceholder>}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        {declineButton()}
      </LimitedAccessWrapperConnector>
    </React.Fragment>
  )
}

ToggleApplicantWrapper.propTypes = {
  applicantStatus: PropTypes.oneOf(arrayApplicantStatuses).isRequired,
  jobOfferId: PropTypes.string.isRequired,
  applicationId: PropTypes.string.isRequired,
  candidateId: PropTypes.string.isRequired,
  buttonComponent: PropTypes.node.isRequired
}

export default withTranslations(ToggleApplicantWrapper)



