import React, { useEffect } from 'react'
import { t } from 'i18next'
import { PreviewModalMobile } from 'Shared/components/Modal'
import DeclineApplicationFormConnector from 'Employer/connectors/Forms/Applications/DeclineApplicationFormConnector'
import Button, { PrimaryButton } from 'Shared/components/Button'
import './DeclineApplicationModal.scss'

function DeclineApplicationModalMobile (props) {
  useEffect(() => {
    if (props.shouldCloseModal) {
      props.onClose()
    }
  }, [props.shouldCloseModal])

  return (
    <PreviewModalMobile
      title={t('modals.changeApplicationStatus.declinedTitle')}
      show={props.show}
      handleClose={props.onClose}
    >
      <DeclineApplicationFormConnector
        applicationId={props.applicationId}
        candidateFirstName={props.candidateFirstName}
      />
      <div className='decline-application-modal-mobile__buttons'>
        <Button disabled={props.isModalSubmitting} onClick={props.onClose}>{t('common.cancel')}</Button>
        <PrimaryButton disabled={props.isModalSubmitting} onClick={props.submitDeclinedForm}>{props.isModalSubmitting ? t('common.loading') : t('common.submit')}</PrimaryButton>
      </div>
    </PreviewModalMobile>
  )
}

export default DeclineApplicationModalMobile



