import React, { PureComponent } from 'react'
import { t } from 'i18next'
import './ApplicantsViewGrid.scss'
import ApplicationHitsConnector from 'Employer/connectors/Views/Applicants/ApplicationHitsConnector'
import ApplicantListLoader from 'Employer/views/ApplicantsView/components/ApplicantListLoader'

class ApplicantsViewGrid extends PureComponent {
  state = {
    showHits: true
  }

  componentDidMount () {
    this.props.fetchHighSchoolEducationGradingSystem()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.match.params.status !== prevProps.match.params.status) {
      this.props.getApplicationsCounters()
      // this is to force mount/unmount hits component to refresh the values received from algolia //21.03.2019 Sandra
      this.setState({ showHits: false }, () => setTimeout(() => this.setState({ showHits: true })))
    }
  }

  render () {
    const { match: { params: { status } } } = this.props

    return (
      <div>
        {
          this.state.showHits && !this.props.applicationsLoading && this.props.hitsNumber === 0 &&
          <div className='applicants-view__empty-state'>
            {t('employer.applicants.emptyState', { status: t(`employer.applicants.applicantsType.${status}`) })}
          </div>
        }
        { !this.state.showHits && this.props.hitsNumber > 0 && <ApplicantListLoader /> }
        {
          this.state.showHits &&
          !this.props.applicationsLoading &&
          this.props.hitsNumber !== 0 &&
          <ApplicationHitsConnector positionY={this.state.positionY} /> }
      </div>
    )
  }
}

export default ApplicantsViewGrid



