import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileGeneralInfo from 'Employer/components/CandidateProfileDetails/CandidateProfileGeneralInfo'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import {
  getCandidateAge,
  getCandidateGender,
  getCandidateNationality,
  getCandidateVisa,
  getCandidateDrivingLicence,
  getCandidateCarCapacity
} from 'Shared/selectors/candidates'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { isSensitiveMarket } from 'Shared/selectors/geolocation'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    userAge: getCandidateAge(state, candidateId),
    userGender: getCandidateGender(state, candidateId),
    userNationality: getCandidateNationality(state, candidateId),
    userVisa: getCandidateVisa(state, candidateId),
    userDrivingLicence: getCandidateDrivingLicence(state, candidateId),
    userCarCapacity: getCandidateCarCapacity(state, candidateId),
    isSensitiveMarket: isSensitiveMarket(state)
  })
}

const mapDispatchToProps = dispatch => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded())
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(CandidateProfileGeneralInfo)



