import React, { Component } from 'react'
import ApplicantsViewConnector from 'Employer/connectors/Views/Applicants/ApplicantsViewConnector'
import ApplicantsBulkStatusManagerConnector from 'Employer/connectors/BulkManager/ApplicantsBulkManagerConnector'

class ApplicantsViewWrapper extends Component {
  render () {
    return (
      <div>
        <ApplicantsViewConnector />
        <ApplicantsBulkStatusManagerConnector />
      </div>
    )
  }
}

export default ApplicantsViewWrapper



