import React, { Component, Fragment } from 'react'
import ProfileStepSection from 'Shared/components/ProfileStepSection'
import { Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { isNil, pathOr, prop, has, propOr } from 'ramda'
import { Editor, SearchMap, SingleImageUploader, Select } from 'Shared/components/FormElements'
import { lengthLimit } from 'Shared/constants/Editor'
import Divider from 'Shared/components/FormElements/Divider'
import { areValuesEmpty } from 'Shared/helpers/object'
import Label from 'Shared/components/FormElements/Label/Label'
import { Input } from 'Shared/components/FormElements/Input/Input'
import { apiCitySearch } from 'Shared/services/api/useApiGetCities'
class CompanyInformation extends Component {
  state = {
    shouldDisplayCompanyLogoField: false,
    locationInputValue: '',
    locationOptions: []
  }

  onKeyUp = (searchKeyword) => {
    if (searchKeyword.length === 0) return
    apiCitySearch(null, { city: searchKeyword || this.state.locationInputValue }).then(res => {
      this.setState({locationOptions: res})
    })
  }

  componentDidMount () {
    if (isNil(prop('logoUrl', this.props.companyInformation))) {
      this.setState({ shouldDisplayCompanyLogoField: true })
    }
  }

  render () {
    const { companyInformation, t, hasCompanyProfilePermission } = this.props
    const shouldDisplayCompanyForm = hasCompanyProfilePermission && (areValuesEmpty(companyInformation) || areValuesEmpty(companyInformation.location))
    const isInitiallyNil = value => isNil(prop(value, companyInformation))
    const showUploadLogoError = isNil(companyInformation.logoUrl) && has('logoUrl', pathOr({}, ['formErrors', 'companyInformation'], this.props)) && this.props.submitFailed
    const logoError = propOr({}, 'logoUrl', this.props.formErrors.companyInformation)

    const citiesWithCountry = [...this.state.locationOptions].map(city => {
      const cityWithCountryCode = `${city.value}, ${city.country}`
      const cityWithCountryName = `${city.value}, ${city.country_name || city.country}`
      return ({...city, value: cityWithCountryCode, label: cityWithCountryName})
    })

    return <div>
      {
        (shouldDisplayCompanyForm || this.state.shouldDisplayCompanyLogoField) && hasCompanyProfilePermission &&
        <Fragment>
          <Divider />
          <ProfileStepSection
            label={t('jobOffer.companySection.title')}
            text={t('jobOffer.companySection.description')}
          />
          {areValuesEmpty(companyInformation.location) &&
          <div className='form__group'>
            <Field
              component={Select}
              label={t('forms.location.label')}
              placeholder={t('forms.location.placeholder')}
              name='companyInformation.locationCitiesWithCountry'
              options={citiesWithCountry}
              onInputChange={(e) => {
                this.setState({locationInputValue: e})
                this.onKeyUp(e)
              }}
            />
          </div>
          }
          {isInitiallyNil('description') &&
          <div className='form__group'>
            <Field
              component={Editor}
              label={t('forms.companyDescription.label')}
              placeholder={t('forms.companyDescription.placeholder')}
              name='companyInformation.description'
              limit={lengthLimit.companyDetailsDescription[1]}
            />
          </div>
          }
          <div className='form__group'>
            {!showUploadLogoError && <Label htmlFor='logoUrl'>{t('company.form.sections.logo.label')}</Label>}
            <Field component={Input} name='companyInformation.logoUrl' type='hidden' />
            <SingleImageUploader
              multiple={false}
              uploadFile={this.props.uploadOrganisationLogo}
              deleteFile={this.props.deleteOrganisationLogo}
              imageUrl={companyInformation.logoUrl}
              hasError={showUploadLogoError}
              error={logoError}
            />
          </div>
        </Fragment>
      }
    </div>
  }
}

export default withTranslations(CompanyInformation)



