import { formValueSelector, reduxForm } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { connect } from 'react-redux'
import { t } from 'i18next'
import { withRouter } from 'react-router-dom'
import ChangeApplicationStatusMessageForm from 'Employer/forms/ChangeApplicationStatusMessageForm'
import { getDecelineDefaultDescription } from 'Shared/helpers/jobApplications'
import { getBulkOperationsItems } from 'Shared/selectors/jobOfferApplications'
import { deselectApplicationForBulkAction, selectApplicationForBulkAction, changeApplicationStatusById } from 'Shared/ducks/jobOfferApplications'
import { getOrganisationName } from 'Shared/selectors/organisations'
import applicationStatuses from 'Shared/constants/Applicants'

export const formName = 'HireApplicationForm'

let Form = reduxForm({
  form: formName,
  onSubmit: async (values, dispatch, props) => dispatch(changeApplicationStatusById(props.applicationId, applicationStatuses.hired, values)),
  enableReinitialize: true,
  validate: values => {
    const errors = {}
    if (!values['message']) {
      errors['message'] = {
        key: 'errors.requiredField',
        options: {
          field: t('forms.declinedMessage.label')
        }
      }
    } else if (values['message'].length < 30) {
      errors['message'] = {
        key: 'errors.textFieldTooShort',
        options: {
          field: t('forms.declinedMessage.label'),
          chars: '30'
        }
      }
    }
    return errors
  }
})(ChangeApplicationStatusMessageForm)

const mapStateToProps = (state, ownProps) => {
  const organisationName = getOrganisationName(state, state.user.details.organisationId)

  const selector = formValueSelector(formName)
  return ({
    initialValues: {
      message: getDecelineDefaultDescription(organisationName, ownProps.candidateFirstName),
      includeNotification: true
    },
    bulkOperationItems: getBulkOperationsItems(state),
    includeNotification: selector(state, 'includeNotification')
  })
}

const mapDispatchToProps = (dispatch) => ({
  selectApplicationForBulkAction: (applicationId, candidateId, fullName, status, jobOfferId) => dispatch(selectApplicationForBulkAction(applicationId, candidateId, fullName, status, jobOfferId)),
  deselectApplicationForBulkAction: applicationId => dispatch(deselectApplicationForBulkAction(applicationId))
})

export default withTranslations(withRouter(connect(mapStateToProps, mapDispatchToProps)(Form)))



