import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileVideoCv from 'Employer/components/CandidateProfileDetails/CandidateProfileVideoCv'
import { getCandidateVideoCvUrl } from 'Shared/selectors/candidates'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    videoCvUrl: getCandidateVideoCvUrl(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileVideoCv)



