import React from 'react'
import { Field } from 'redux-form'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import { MultiSelect } from 'Shared/components/FormElements'
import ProfileStepSection from 'Shared/components/ProfileStepSection/index'

const PreferredLanguages = ({ t, languages }) => (
  <div>
    <ProfileStepSection
      label={t('jobOffer.postAJob.languages.label')}
      text={t('jobOffer.postAJob.languages.text')}
      optional
    />
    <div className='form__group'>
      <Field
        component={MultiSelect}
        options={languages}
        placeholder={t('forms.interests.placeholder')}
        name='preferredLanguages'
        withIcon
        maxItemsCount={3}
      />
    </div>
  </div>
)

PreferredLanguages.propTypes = {
  languages: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    value: PropTypes.string
  })).isRequired
}

export default withTranslations(PreferredLanguages)



