import React from 'react'
import ApplicantDetailsViewMobile from './ApplicantDetailsView.mobile'
import ApplicantDetailsViewWeb from './ApplicantDetailsView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ApplicantDetailsView = props => props.isMobile ? <ApplicantDetailsViewMobile {...props} /> : <ApplicantDetailsViewWeb {...props} />

export default withDeviceWidthDetector(ApplicantDetailsView)



