import React, { Component, Fragment } from 'react'
import { withRouter } from 'react-router-dom'
import WindowModal from 'Shared/components/WindowModal'
import Label from 'Shared/components/FormElements/Label/Label'
import MultiSelectRefinementList from 'Algolia/components/MultiSelectRefinementList'
import CandidateLoggedInAtRefinementList from 'Algolia/components/CandidateLoggedInAtRefinementList'
import CandidateAvailabilityToggleRefinement from 'Algolia/components/CandidateAvailabilityToggleRefinement'
import VideoCvRadioSelect from 'Algolia/components/VideoCvRadioSelect'
import DrivingLicenseRadioSelect from 'Algolia/components/DrivingLicenseRadioSelect'
import { filterLabels, transformItems } from 'Shared/helpers/Algolia'
import './ApplicantsMoreSearchFilters.scss'
import { t } from 'i18next'

class ApplicantsMoreSearchFilters extends Component {
  handleCancel = () => {
    this.props.history.push(`${this.props.location.pathname}${this.props.currentSearch}`)
    this.props.hideMoreFilters()
  }

  handleSave = () => {
    this.props.hideMoreFilters()
  }

  render () {
    return (
      <WindowModal
        title={t('common.allFilters')}
        additionalButtons={this.props.additionalButtons}
        handleCancel={this.handleCancel}
        handleSubmit={this.handleSave}
        submitText={t('common.apply')}
        saveInDom
        show={this.props.showMoreFilters}
      >
        <div className='applicants-search-filters__filters-container parsing--hidden'>
          <MultiSelectRefinementList
            attribute='city'
            name={t('searchAttributes.city')}
            withPopup
          />
          <MultiSelectRefinementList
            attribute='visaStatus'
            name={t('searchAttributes.visaStatus')}
            withPopup
          />
          <MultiSelectRefinementList
            attribute='majors.name'
            name={t('searchAttributes.major')}
            withPopup
          />
          <CandidateLoggedInAtRefinementList />
          <CandidateAvailabilityToggleRefinement />
        </div>
        <div>
          <div className='applicants-more-filters__filters-row parsing--hidden'>
            <div className='applicants-more-filters__refinement-container'>
              <Label>{t('searchAttributes.countryOfResidence')}</Label>
              <MultiSelectRefinementList
                attribute='countryOfResidence'
                name={t('searchAttributes.countryOfResidence')}
              />
            </div>
            <div className='applicants-more-filters__refinement-container'>
              <Label>{t('searchAttributes.language')}</Label>
              <MultiSelectRefinementList
                attribute='languages'
                name={t('searchAttributes.language')}
                withAlgoliaSearch={false}
                transformItems={transformItems.language(this.props.languages)}
              />
            </div>
            <div className='applicants-more-filters__refinement-container'>
              <Label>{t('searchAttributes.degreeStatus')}</Label>
              <MultiSelectRefinementList
                attribute='degrees'
                name={t('searchAttributes.degreeStatus')}
              />
            </div>
            <div className='applicants-more-filters__refinement-container'>
              <Label>{t('searchAttributes.university')}</Label>
              <MultiSelectRefinementList
                attribute='universities'
                name={t('searchAttributes.university')}
                handleCustomFilter={filterLabels.universities}
              />
            </div>
          </div>
          <div className='applicants-more-filters__filters-row parsing--hidden'>
            <div className='applicants-more-filters__refinement-container'>
              <Label>{t('searchAttributes.employmentType')}</Label>
              <MultiSelectRefinementList
                attribute='interestedIn'
                name={t('searchAttributes.employmentType')}
              />
            </div>
            {
              !this.props.isSensitiveMarket && (
                <Fragment>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.nationality')}</Label>
                    <MultiSelectRefinementList
                      attribute='nationality'
                      name={t('searchAttributes.nationality')}
                      withAlgoliaSearch={false}
                      transformItems={transformItems.nationality(this.props.countries)}
                    />
                  </div>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.gender')}</Label>
                    <MultiSelectRefinementList
                      attribute='gender'
                      name={t('searchAttributes.gender')}
                      withRefinementSearch={false}
                    />
                  </div>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.age')}</Label>
                    <MultiSelectRefinementList
                      attribute='ageGroup'
                      name={t('searchAttributes.age')}
                      withRefinementSearch={false}
                    />
                  </div>
                </Fragment>
              )
            }
            {
              this.props.isSensitiveMarket && (
                <Fragment>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.videoCv')}</Label>
                    <VideoCvRadioSelect />
                  </div>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.driversLicence')}</Label>
                    <DrivingLicenseRadioSelect />
                  </div>
                  <div className='applicants-more-filters__refinement-container' />
                </Fragment>
              )
            }
          </div>
          {
            !this.props.isSensitiveMarket && (
              <Fragment>
                <div className='applicants-more-filters__filters-row parsing--hidden'>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.videoCv')}</Label>
                    <VideoCvRadioSelect />
                  </div>
                  <div className='applicants-more-filters__refinement-container'>
                    <Label>{t('searchAttributes.driversLicence')}</Label>
                    <DrivingLicenseRadioSelect />
                  </div>
                  <div className='applicants-more-filters__refinement-container' />
                  <div className='applicants-more-filters__refinement-container' />
                </div>
              </Fragment>
            )
          }
        </div>
      </WindowModal>
    )
  }
}

export default withRouter(ApplicantsMoreSearchFilters)



