import React, { Component } from 'react'
import { Statuses } from 'Shared/constants/Job'
import { areValuesEmpty } from 'Shared/helpers/object'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { PrimaryButton } from 'Shared/components/Button'
import { EmployerPermissions } from 'Shared/constants/User'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { daysBetweenTwoDates } from 'Shared/helpers/date'
import { isNil } from 'ramda'
import withTranslations from 'Shared/hoc/withTranslations'

class PostJobBtn extends Component {
  state = {
    formValues: {}
  }

  getLabel = () => {
    const { publishedAt, deactivatedAt } = this.state.formValues
    const { initialValues, t } = this.props
    switch (true) {
      case initialValues.status === Statuses.inactive && !isNil(deactivatedAt) && daysBetweenTwoDates(deactivatedAt, publishedAt) <= 30:
        return t('common.reactivate')
      case initialValues.status === Statuses.inactive:
        return t('common.repost')
      case initialValues.status === Statuses.active:
        return t('common.saveChanges')
      default:
        return t('common.postJob')
    }
  }

  componentWillReceiveProps (nextProps, nextContext) {
    if (this.props.formValues !== nextProps.formValues) {
      this.setState({ formValues: nextProps.formValues })
    }
  }

  getTooltipContent = () => {
    const { t, isCompanyProfileComplete, adminFullName } = this.props
    switch (true) {
      case !isCompanyProfileComplete:
        return t('forms.postJobForm.tooltips.completeCompanyProfile', { adminFullName })
      default:
        return t('forms.postJobForm.tooltips.noJobPosts', { adminFullName })
    }
  }

  render () {
    const {
      isCurrentMembershipExpired,
      initialValues: { companyInformation, status },
      neverHadAnyMembership,
      jobPostsAmount,
      isCurrentMembershipCancelled,
      isCompanyProfileComplete,
      hasCompanyProfilePermission,
      submitForm
    } = this.props

    return (
      !hasCompanyProfilePermission &&
      (
        areValuesEmpty(companyInformation) ||
        areValuesEmpty(companyInformation.location) ||
        neverHadAnyMembership ||
        (status !== Statuses.active && jobPostsAmount < 1) ||
        isCurrentMembershipExpired ||
        isCurrentMembershipCancelled ||
        !isCompanyProfileComplete
      )
        ? (
          <LimitedAccessWrapperConnector
            placeholderComponent={<PrimaryButton>{this.getLabel()}</PrimaryButton>}
            allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
            jobOfferId={this.props.match.params.jobOfferId}
          >
            <Tooltip content={this.getTooltipContent()}>
              <PrimaryButton
                disabled
                onClick={submitForm(Statuses.active)}
              >
                {this.getLabel()}
              </PrimaryButton>
            </Tooltip>
          </LimitedAccessWrapperConnector>
        )
        : (
          <LimitedAccessWrapperConnector
            placeholderComponent={<PrimaryButton>{this.getLabel()}</PrimaryButton>}
            allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
            jobOfferId={this.props.match.params.jobOfferId}
          >
            <PrimaryButton
              onClick={submitForm(Statuses.active)}
            >
              {this.getLabel()}
            </PrimaryButton>
          </LimitedAccessWrapperConnector>
        )
    )
  }
}

export default withTranslations(PostJobBtn)



