import React from 'react'
import PropTypes from 'prop-types'
import { pure } from 'recompose'
import cx from 'classnames'
import './AddNewSectionButton.scss'

const AddNewSectionButton = ({ text, onClick, className }) => {
  const buttonClassName = cx({
    'add-new-section-button': true,
    [className]: !!className
  })

  return (
    <button onClick={onClick} className={buttonClassName} type={'button'}>{text}</button>
  )
}

AddNewSectionButton.propTypes = {
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  className: PropTypes.string
}

export default pure(AddNewSectionButton)



