import React from 'react'
import HireApplicationModalMobile from './HireApplicationModal.mobile'
import HireApplicationModalWeb from './HireApplicationModal'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const HireApplicationModal = props => props.isMobile ? <HireApplicationModalMobile {...props} /> : <HireApplicationModalWeb {...props} />

export default withDeviceWidthDetector(HireApplicationModal)



