import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import { isApplicantSeen } from 'Shared/helpers/jobApplications'
import { StaticTagMedium } from 'Shared/components/StaticTag'
import { getQueryFromUrl } from 'Shared/helpers/query'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import Highlight from 'Algolia/components/Highlight'
import * as R from 'ramda'
import { parseHighlightedCandidateHit } from 'Algolia/helpers/highlights'
import CandidateHighlightSnippet from 'Algolia/components/CandidateHighlightSnippet'
import { educationLevels } from 'Shared/constants/Candidates'
import { isMobile } from 'Shared/helpers/mobile'
import ApplicantHitButtonsConnector from 'Employer/connectors/Components/ApplicantHitButtonsConnector.js'
import { JustCheckbox } from 'Shared/components/FormElements/Checkbox'
import cx from 'classnames'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'
import PremiumTag from 'Shared/components/PremiumTag'
import { applicationStatuses } from 'Shared/constants/Applicants'

class ApplicantHit extends Component {
  state = {
    initiatingChatConversation: false
  }

  shouldComponentUpdate (nextProps, nextState, nextContext) {
    return R.not(R.equals(this.props.applicationStatus, nextProps.applicationStatus)) ||
     R.not(R.equals(this.props.areChannelsFetched, nextProps.areChannelsFetched)) ||
     R.not(R.equals(this.props.hasActiveConversation, nextProps.hasActiveConversation)) ||
     R.not(R.equals(this.props.isAnyBulkSelected, nextProps.isAnyBulkSelected)) ||
     R.not(R.equals(this.props.isChannelMember, nextProps.isChannelMember)) ||
     R.not(R.equals(this.props.isItemSelectedForBulkAction, nextProps.isItemSelectedForBulkAction)) ||
     R.not(R.equals(this.props.joinToChannelLoading, nextProps.joinToChannelLoading)) ||
     R.not(R.equals(this.state.initiatingChatConversation, nextState.initiatingChatConversation))
  }

  get tags () {
    const { t, isBoosted, isAutoShortlisted, applicationStatus, isOlivPick, hit, match: { params: { jobOfferId } } } = this.props
    const applicationTags = []
    const tags = {
      boosted: <div className='job-list__premium-tag'><PremiumTag /></div>,
      new: <StaticTagMedium key='new' color='green' name={t('common.new')} />,
      shortlistedByOlivAdvisor: <StaticTagMedium key='olivsPick' color='yellow' name={t('common.olivsPick')} />,
      shortlistedByOliviaBot: <StaticTagMedium key='new' color='primary' name={t('olivia.shortlistedByOlivia.label')} tooltip={t('olivia.shortlistedByOlivia.tooltip')} />
    }

    const addTag = tag => applicationTags.push(tag)

    isBoosted && addTag(tags.boosted)
    isOlivPick && addTag(tags.shortlistedByOlivAdvisor)
    !isApplicantSeen(hit, jobOfferId) && addTag(tags.new)
    isAutoShortlisted && applicationStatus !== applicationStatuses.undecided && addTag(tags.shortlistedByOliviaBot)

    return applicationTags
  }

  getMajorsOrEducationSystem = () => {
    const { hit, getHighSchoolGradingSystemName } = this.props
    const getEducationSystem = R.pipe(R.prop('highSchools'), R.head, R.propOr('', 'educationSystem'), getHighSchoolGradingSystemName)
    const getMajors = () => <Highlight attribute='majorsName' array hit={parseHighlightedCandidateHit(hit)} />

    return R.ifElse(
      R.propEq('educationLevel', educationLevels.highSchool),
      getEducationSystem,
      getMajors
    )(hit)
  }

  handleCheckboxToggle = (e) => {
    e.stopPropagation()
    const {
      selectApplicationForBulkAction,
      deselectApplicationForBulkAction,
      applicationStatus,
      applicationId,
      hit: {
        id,
        firstName,
        lastName
      }
    } = this.props

    return this.props.isItemSelectedForBulkAction
      ? deselectApplicationForBulkAction(applicationId)
      : selectApplicationForBulkAction(applicationId, id, `${firstName} ${lastName}`, applicationStatus, this.props.match.params.jobOfferId)
  }

  render () {
    const { hit, applicationId, applicationStatus } = this.props
    const gridClass = cx({
      'job-list__grid': true,
      'job-list__grid--applicant': true,
      'job-list__grid--selected': this.props.isItemSelectedForBulkAction,
      'job-list__grid--premium': this.props.isBoosted
    })

    const checkboxClass = cx({
      'job-list__checkbox': true,
      'job-list__checkbox--visible': this.props.isAnyBulkSelected || isMobile()
    })

    return (
      <div className='column col-4 col-lg-6 col-md-12'>
        <div className={gridClass}>
          <div className={'applicant-card__checkbox-line'}>
            <JustCheckbox className={checkboxClass} checked={this.props.isItemSelectedForBulkAction} onClick={this.handleCheckboxToggle} />
          </div>
          <div className='job-list__header'>
            <div className='job-list__tag'>{this.tags}</div>
          </div>
          <div className='applicant-hit__description'>
            <LimitedAccessWrapperConnector
              placeholderComponent={<div>
                <div className='job-list__photo'>
                  <UserAvatar details={hit} className='job-list__candidate--photo' />
                </div>
                <div className='job-list__candidate--name'>
                  <Highlight attribute='firstName' hit={hit} />&nbsp;
                  <Highlight attribute='lastName' hit={hit} />
                </div>
                <div className='job-list__candidate--major'>{this.getMajorsOrEducationSystem()}</div>
                <div className='job-list__location'>
                  <span>{hit.city}</span>
                  <span>, </span>
                  <span>{hit.countryOfResidence && hit.countryOfResidence}</span>
                </div>
              </div>}
              allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
              jobOfferId={this.props.match.params.jobOfferId}
            >
              <Link
                to={{
                  pathname: `/employer/job-offer/${this.props.match.params.jobOfferId}/applicant/${applicationId}`,
                  search: `query=${getQueryFromUrl(this.props.location.search)}`
                }}
                target='_blank'
              >
                <div className='job-list__photo'>
                  <UserAvatar details={hit} className='job-list__candidate--photo' />
                </div>
                <div className='job-list__candidate--name'>
                  <Highlight attribute='firstName' hit={hit} />&nbsp;
                  <Highlight attribute='lastName' hit={hit} />
                </div>
                <div className='job-list__candidate--major'>{this.getMajorsOrEducationSystem()}</div>
                <div className='job-list__location'>
                  <span>{hit.city}</span>
                  <span>, </span>
                  <span>{hit.countryOfResidence && hit.countryOfResidence}</span>
                </div>
              </Link>
            </LimitedAccessWrapperConnector>
          </div>
          <CandidateHighlightSnippet hit={hit} />
          <div className='job-list__grid--buttons'>
            <ApplicantHitButtonsConnector
              hit={hit}
              applicationId={applicationId}
              applicationStatus={applicationStatus}
            />
          </div>
        </div>
      </div>
    )
  }
}

ApplicantHit.defaultProps = {
  startChatConversation: () => {},
  handleApplicationAction: () => {}
}

export default withTranslations(ApplicantHit)



