import React from 'react'
import DeclineApplicationModalMobile from './DeclineApplicationModal.mobile'
import DeclineApplicationModalWeb from './DeclineApplicationModal'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const DeclineApplicationModal = props => props.isMobile ? <DeclineApplicationModalMobile {...props} /> : <DeclineApplicationModalWeb {...props} />

export default withDeviceWidthDetector(DeclineApplicationModal)



