import React, { Component } from 'react'
import InfiniteScroll from 'Algolia/components/InfiniteScroll'
import ApplicantHitConnector from 'Employer/connectors/Views/Applicants/ApplicantHitConnector'
import ApplicantListLoader from 'Employer/views/ApplicantsView/components/ApplicantListLoader'
import ApplicantsAllHitsInfiniteScroll from 'Algolia/connectors/ApplicantAllHitsInfiniteScrollConnector'

class ApplicationHits extends Component {
  state = {
    loading: true
  }

  componentDidMount () {
    setTimeout(this.finishLoading, 0)
  }

  finishLoading = () => {
    window.scroll(0, this.props.positionY)
    this.setState({ loading: false })
  }

  getHits = () => this.props.shouldShowSorting
    ? <ApplicantsAllHitsInfiniteScroll hitComponent={ApplicantHitConnector} infiniteScrollClass='columns' />
    : <InfiniteScroll hitComponent={ApplicantHitConnector} infiniteScrollClass='columns' />

  render () {
    const { applicationsLoading } = this.props

    return (
      <div style={{ minHeight: `${this.state.loading ? this.props.positionY * 2 : 0}px` }}>
        {applicationsLoading
          ? <ApplicantListLoader />
          : this.getHits()
        }
      </div>
    )
  }
}

export default ApplicationHits



