import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import Loader from 'Shared/components/Loader/Loader'
import {
  CandidateProfileViewLoader
} from 'Shared/components/Main/MainLoaders'

const EditJobOffersViewLoaders = (props) => {
  const renderLoader = () => <Loader className='loader--app' description={`${props.t('common.loading')}...`} />

  return (
    <Switch>
      <Route exact path='/employer/job-offer/:jobOfferId/edit' component={renderLoader} />
      <Route exact path='/employer/job-offer/:jobOfferId/add' component={renderLoader} />
      <Route exact path='/employer/job-offer/:jobOfferId/applicants/:status' component={renderLoader} />
      <Route
        exact
        path='/employer/job-offer/:jobOfferId/applicant/:applicationId'
        component={CandidateProfileViewLoader}
      />
      <Redirect to='/employer/job-offers/active' />
    </Switch>
  )
}

export default withTranslations(EditJobOffersViewLoaders)



