import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import ApplicationStatusThumbsToggle from 'Employer/components/ApplicationStatusThumbsToggle/ApplicationStatusThumbsToggle'
import { changeApplicationStatusById } from 'Shared/ducks/jobOfferApplications'
import applicationStatuses from 'Shared/constants/Applicants'
import { applicationStatusEqualsTo } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const applicationId = ownProps.match.params.applicationId

  return ({
    isShortlisted: applicationStatusEqualsTo(state, applicationId, applicationStatuses.shortlisted),
    isDeclined: applicationStatusEqualsTo(state, applicationId, applicationStatuses.declined),
    applicationId
  })
}

const mapDispatchToProps = dispatch => ({
  handleShortlistApplication: applicationId => dispatch(changeApplicationStatusById(applicationId, applicationStatuses.shortlisted))
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(ApplicationStatusThumbsToggle)



