import React, { Component } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Field, Form } from 'redux-form'
import { Textarea, Input } from 'Shared/components/FormElements'
import { lengthLimit } from 'Shared/constants/Editor'
import './BulkMessagesForm.scss'
import Label from 'Shared/components/FormElements/Label'
import Notification from 'Shared/components/Notification/index'

const [, max] = lengthLimit.interviewInvite

class BulkMessagesForm extends Component {
  isItemSelected = (applicationId) => {
    const { bulkOperationItems } = this.props

    return !!bulkOperationItems.filter(item => item.applicationId === applicationId).length
  }

  handleSelectionToggle = (applicationId, candidateId, fullName, status, jobOfferId) => () => {
    return this.isItemSelected(applicationId)
      ? this.props.deselectApplicationForBulkAction(applicationId)
      : this.props.selectApplicationForBulkAction(applicationId, candidateId, fullName, status, jobOfferId)
  }

  render () {
    const { t, bulkOperationItems, handleSubmit, timeSlotNotification } = this.props

    return (
      <div>
        <p className='bulk-messages__description'>{t('modals.bulkMessages.description')}</p>
        <Label>{t('employer.jobOffers.counters.applicants')} ({bulkOperationItems.length})</Label>
        <div className='bulk-messages__selected-applicants-container'>
          {bulkOperationItems.map(item =>
            <div
              onClick={this.handleSelectionToggle(item.applicationId, item.candidateId, item.fullName, item.status, item.jobOfferId)}
              key={item.applicationId}
              className='bulk-messages__selected-applicant'
            >
              <div className='bulk-messages__selected-applicant__name'>{item.fullName}</div>
              <div className='icon-im-close bulk-messages__selected-applicant__deselect' />
            </div>
          )}
        </div>
        {
          timeSlotNotification && <div className='bulk-messages__section'>
            <p className='bulk-messages__section-title'>Time Slots</p>
            <Notification
              messageText={t('timeSlots.bulkTimeSlotsInfo')}
              iconClassName='icon-im-info-outline'
              warning
            />
          </div>
        }
        <Form onSubmit={handleSubmit}>
          <Field
            component={Textarea}
            label={t('forms.message.label')}
            name='message'
            limit={max}
            placeholder={t('forms.message.placeholder')}
          />
          <Field
            component={Input}
            name='jobOfferId'
            type='hidden'
          />
        </Form>
      </div>
    )
  }
}

export default withTranslations(BulkMessagesForm)



