import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { t } from 'i18next'
import MultiSelectRefinementList from 'Algolia/components/MultiSelectRefinementList'
import CandidateAvailabilityToggleRefinement from 'Algolia/components/CandidateAvailabilityToggleRefinement'
import SaveSearchCriteriaButtonConnector from 'Employer/connectors/Components/SaveSearchCriteriaButtonConnector'
import ApplicantsMoreSearchFiltersConnector from 'Employer/connectors/Components/ApplicantsMoreSearchFiltersConnector'
import CandidateLoggedInAtRefinementList from 'Algolia/components/CandidateLoggedInAtRefinementList'
import SearchInput from 'Algolia/components/SearchInput'
import { getQueryFromUrl } from 'Shared/helpers/query'
import './ApplicantsSearchFilters.scss'
import { isArabicLayout } from 'Shared/helpers/layout'
import { applicantsSelectedFiltersCount } from 'Shared/helpers/Algolia'
import cx from 'classnames'
import * as R from 'ramda'

class CandidateSearchFilters extends Component {
  state = {
    isSavedSearchLoaded: false,
    showMoreFilters: false,
    currentSearch: ''
  }

  componentDidMount () {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
  }

  componentWillReceiveProps (nextProps, nextContext) {
    if (!nextProps.match.params.savedSearchId || nextProps.match.params.savedSearchId !== this.props.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: false })
    }
  }

  componentDidUpdate (prevProps) {
    if (this.props.match.params.savedSearchId && this.props.match.params.savedSearchId !== prevProps.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: true }, () => setTimeout(() => this.setState({ isSavedSearchLoaded: false }), 1500))
    }
  }

  isFilterLoaded = filterName => this.state.isSavedSearchLoaded && R.has(filterName)(this.props.searchStateValues)
  isQueryLoaded = () => this.state.isSavedSearchLoaded && R.not(R.isEmpty(getQueryFromUrl(this.props.location.search)))

  showMoreFilters = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'hidden'
    const currentSearch = this.props.location.search
    return this.setState({ showMoreFilters: true, currentSearch })
  }
  hideMoreFilters = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
    return this.setState({ showMoreFilters: false })
  }

  handleReset = () => {
    const { jobOfferId, status } = this.props.match.params
    this.props.removeSeenApplicantsFilter()
    this.props.history.push(`/employer/job-offer/${jobOfferId}/applicants/${status}`)
  }

  clearButton = () => {
    const btnClass = cx({
      'applicants-search-filters__filters-clear-btn': true,
      'applicants-search-filters__filters-clear-btn--arabic': isArabicLayout
    })

    return applicantsSelectedFiltersCount(this.props.searchState) > 0 && (
      <div onClick={this.handleReset} className={btnClass}>
        {t('common.clearFilters')}
        {applicantsSelectedFiltersCount(this.props.searchState) > 0 && <div className='applicants-search-filters__filters-clear-counter'>{applicantsSelectedFiltersCount(this.props.searchState)}</div>}
      </div>
    )
  }

  render () {
    return (
      <div className='applicants-search-filters parsing--hidden'>
        <div className=''>
          <div className='applicants-search-filters__search-container'>
            <SearchInput withDebounce isLoaded={this.isQueryLoaded()} additionalButton={<SaveSearchCriteriaButtonConnector />} placeholder={t('searchPlaceholders.candidatesSearch')} />
          </div>
          <div className='columns'>
            <div className='column col-8 col-xs-12'>
              <div className='applicants-search-filters__filters-container'>
                <MultiSelectRefinementList
                  attribute='city'
                  isLoaded={this.isFilterLoaded('city')}
                  name={t('searchAttributes.city')}
                  withPopup
                />
                <MultiSelectRefinementList
                  attribute='visaStatus'
                  isLoaded={this.isFilterLoaded('visaStatus')}
                  name={t('searchAttributes.visaStatus')}
                  withPopup
                />
                <MultiSelectRefinementList
                  attribute='majors.name'
                  isLoaded={this.isFilterLoaded('majors.name')}
                  name={t('searchAttributes.major')}
                  withPopup
                />
                <CandidateLoggedInAtRefinementList isLoaded={this.isFilterLoaded('loggedInAt')} />
                <CandidateAvailabilityToggleRefinement isLoaded={this.isFilterLoaded('availability')} />
              </div>
            </div>
            <div className='column col-4 col-xs-12'>
              <div className='applicants-search-filters__filters-more'>
                {this.clearButton()}
                <div className='applicants-search-filters__filters-more-btn' onClick={this.showMoreFilters}>
                  {t('common.moreFilters')}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <ApplicantsMoreSearchFiltersConnector
            currentSearch={this.state.currentSearch}
            hideMoreFilters={this.hideMoreFilters}
            additionalButtons={this.clearButton()}
            showMoreFilters={this.state.showMoreFilters}
          />
        </div>
      </div>
    )
  }
}

export default withRouter(CandidateSearchFilters)



