import React, { useState } from 'react'
import { t } from 'i18next'
import JoinActiveChannelPopupConnector from 'Chat/connectors/Components/JoinActiveChannelPopup/JoinActiveChannelPopupConnector'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { BigJustIconButton } from 'Shared/components/Button'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './ApplicationInitChatButton.scss'

function ApplicationInitChatButton (props) {
  const [initiatingChatConversation, setInitiatingChatConversation] = useState(false)

  const Wrapper = (wrapperProps) => props.isMobile ? <div>{wrapperProps.children}</div> : <Tooltip content={t('employer.applicants.chatApplicantTooltip')}>{wrapperProps.children}</Tooltip>

  const startChatConversation = async () => {
    setInitiatingChatConversation(true)
    await props.startChatConversation(props.jobOffer, props.candidateId, props.applicationId)
    const { sid } = await props.getChannelByJobOfferAndCandidateId
    setInitiatingChatConversation(false)
    return props.history.push(`/employer/chat/${sid}`)
  }

  const getChatButton = () => {
    const { isChannelMember, hasActiveConversation, areChannelsFetched, applicationId, joinToChannelLoading } = props

    switch (true) {
      case areChannelsFetched && hasActiveConversation && !isChannelMember:
        return (
          <JoinActiveChannelPopupConnector applicationId={applicationId}>
            <BigJustIconButton justIcon loading={joinToChannelLoading}>
              {!joinToChannelLoading && <i className='ion-md-chatboxes' />}
            </BigJustIconButton>
          </JoinActiveChannelPopupConnector>
        )
      case areChannelsFetched:
        return (
          <BigJustIconButton disabled={initiatingChatConversation} onClick={startChatConversation} justIcon>
            <i className='ion-md-chatboxes' />
          </BigJustIconButton>
        )
      default:
        return (
          <BigJustIconButton loading />
        )
    }
  }

  return (
    <div className='application-init-chat__button-container'>
      <Wrapper>
        {getChatButton()}
      </Wrapper>
    </div>
  )
}

export default withDeviceWidthDetector(ApplicationInitChatButton)



