import React, { PureComponent } from 'react'
import { t } from 'i18next'
import { EntitiesListTabWrapper, EntitiesListTab } from 'Shared/components/EntitiesListTabs'
import ApplicantsSearchFiltersConnector from 'Employer/connectors/Components/ApplicantsSearchFiltersConnector'
import SavedCandidatesSearchListConnector from 'Employer/connectors/Components/SavedCandidatesSearchListConnector'
import cx from 'classnames'
import './ApplicantsSearchBox.scss'

class ApplicantsSearchBox extends PureComponent {
  state = {
    isFiltersTabActive: true
  }

  switchTab = isFiltersTabActive => () => this.setState({ isFiltersTabActive })

  render () {
    const filtersContainer = cx({
      'applicants-search-box__content--visible': this.state.isFiltersTabActive,
      'applicants-search-box__content--hidden': !this.state.isFiltersTabActive
    })

    const savedSearchesContainer = cx({
      'applicants-search-box__content--visible': !this.state.isFiltersTabActive,
      'applicants-search-box__content--hidden': this.state.isFiltersTabActive
    })
    return (
      <div className='applicants-search-box'>
        <div className='applicants-search-box__tabs'>
          <EntitiesListTabWrapper light>
            <EntitiesListTab
              text={t('common.search')}
              onClick={this.switchTab(true)}
              isActive={this.state.isFiltersTabActive}
            />
            <EntitiesListTab
              text={t('common.savedSearches')}
              onClick={this.switchTab(false)}
              isActive={!this.state.isFiltersTabActive}
            />
          </EntitiesListTabWrapper>
        </div>
        <div className='applicants-search-box__content'>
          <div className={filtersContainer}>
            <ApplicantsSearchFiltersConnector removeSeenApplicantsFilter={this.props.removeSeenApplicantsFilter} />
          </div>
          <div className={savedSearchesContainer}>
            <SavedCandidatesSearchListConnector onSelectSavedSearch={this.switchTab(true)} />
          </div>
        </div>
      </div>
    )
  }
}

export default ApplicantsSearchBox



