import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import qs from 'query-string'
import { t } from 'i18next'
import * as R from 'ramda'
import cx from 'classnames'
import { SearchResults } from 'Algolia/components/SearchResult'
import ApplicantsViewHeader from 'Employer/views/ApplicantsView/components/ApplicantsViewHeader'
import ApplicantsViewGridConnector from 'Employer/connectors/Views/Applicants/ApplicantsViewGridConnector'
import { includeIdsFilterToExisting } from 'Shared/helpers/Algolia'
import CandidatesStarRatingWrapper from 'Employer/components/CandidatesStarRatingWrapper'
import SwitchButton from 'Shared/components/SwitchButton/SwitchButton'
import INDEX_NAME from 'Shared/constants/Algolia'
import { MIN_CANDIDATES_RATING_VALUE } from 'Shared/constants/Candidates'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import ApplicantsSearchBox from 'Employer/components/ApplicantsSearchBox'
import OliviaBotBannerConnector from 'Employer/connectors/OliviaBotBanner/OliviaBotBannerConnector'

import './ApplicantsView.scss'
import { urlToSearchState } from 'Shared/helpers/querySearch'
import Loader from 'Shared/components/Loader/Loader'

const SortOptions = [INDEX_NAME.CANDIDATES, 'CandidatesByApplicationDate']

class ApplicantsView extends PureComponent {
  constructor (props) {
    super(props)

    const query = qs.parse(this.props.history.location.search)

    this.state = {
      isDropdownOpen: false,
      selectedSortOption: R.or(query.sort, SortOptions[0]),
      sortByDate: false,
      refreshHitsList: false,
      showOnlyUnviewed: false,
      isSavedSearchLoaded: false,
      isSearchBarVisible: true,
      hitsNumber: 0,
      ready: false
    }
  }

  sortOptionActionsMap = {
    [INDEX_NAME.CANDIDATES]: () => {
      this.updateQuerySearch('sort', INDEX_NAME.CANDIDATES)
    },
    'CandidatesByApplicationDate': () => {
      this.updateQuerySearch('sort', 'CandidatesByApplicationDate')
    }
  }

  async componentDidMount () {
    await this.props.getJobOfferActiveApplicationIds(this.props.match.params.jobOfferId)
    await this.props.getApplicationsCounters()
    this.props.addObjectIdFilters(includeIdsFilterToExisting(this.props.applicationLists))
    await this.props.getApplicationsForJobOffer(this.props.match.params.jobOfferId)
    this.props.resetBulkActionSelection()
    this.sortOptionActionsMap[this.state.selectedSortOption]()
    this.setState({ ready: true })
  }

  hasQueryChanged = (nextProps) => {
    const currentSelectedSearchQuery = R.pipe(
      R.propOr(false, 'query'),
      JSON.parse
    )(nextProps.selectedSearch)
    const currentUrlSearchQuery = urlToSearchState(nextProps.location.search, true)

    const omitNoFilterData = searchObj => R.omit(['configure', 'page'], searchObj)

    return R.not(R.equals(omitNoFilterData(currentSelectedSearchQuery), omitNoFilterData(currentUrlSearchQuery)))
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.selectedSearch.id && this.hasQueryChanged(nextProps)) {
      this.props.history.push({
        pathname: `/employer/job-offer/${this.props.match.params.jobOfferId}/applicants/${this.props.match.params.status}`,
        search: nextProps.location.search
      })
    }

    if (!nextProps.match.params.savedSearchId || nextProps.match.params.savedSearchId !== this.props.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: false })
    }
  }

  componentDidUpdate (prevProps, prevState) {
    if (this.props.match.params.status !== prevProps.match.params.status) {
      this.props.addObjectIdFilters(includeIdsFilterToExisting(this.props.applicationLists))
    }

    if (R.not(R.equals(this.props.applicationLists, prevProps.applicationLists))) {
      this.props.addObjectIdFilters(includeIdsFilterToExisting(this.props.applicationLists))
    }

    if (this.props.match.params.savedSearchId && this.props.match.params.savedSearchId !== prevProps.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: true }, () => setTimeout(() => this.setState({ isSavedSearchLoaded: false }), 1500))
    }
  }

  componentWillUnmount () {
    this.props.resetJobOfferActiveApplicationLists()
  }

  isFilterLoaded = filterName => this.state.isSavedSearchLoaded && R.has(filterName)(this.props.searchStateValues)

  updateQuerySearch = (key, value) => {
    const { history } = this.props
    const query = qs.parse(history.location.search)

    query[key] = value

    history.replace(`${this.props.history.location.pathname}?${qs.stringify(query)}`)
  }

  handleSelection = selectedSortOption => {
    this.setState({ selectedSortOption: selectedSortOption })
    this.sortOptionActionsMap[selectedSortOption]()
  }

  finishRefreshHitsList = selectedSortOption => {
    this.setState({ refreshHitsList: false }, () => this.handleSelection(selectedSortOption))
  }

  setSelectedOption = selectedSortOption => () => {
    this.setState({ refreshHitsList: true }, () => this.finishRefreshHitsList(selectedSortOption))
  }

  removeSeenApplicantsFilter = () => {
    if (this.state.showOnlyUnviewed) {
      return document.getElementById('algolia-applicants-viewed').click()
    }
  }

  switchSeenApplicants = () => {
    if (!this.state.showOnlyUnviewed) {
      this.props.addFilter({ seenApplicants: `NOT jobOfferIdsInApplicationsSeen:${this.props.jobOffer.id}` })
      return this.setState({ showOnlyUnviewed: true })
    } else if (this.state.showOnlyUnviewed) {
      this.props.removeFilter([ 'seenApplicants' ])
      return this.setState({ showOnlyUnviewed: false })
    }
  }

  switchSortBy = () => {
    if (!this.state.sortByDate) {
      this.setState({ sortByDate: true })
      return this.setSelectedOption('CandidatesByApplicationDate')()
    } else {
      this.setState({ sortByDate: false })
      return this.setSelectedOption(INDEX_NAME.CANDIDATES)()
    }
  }

  toggleSearchBar = () => this.setState({ isSearchBarVisible: !this.state.isSearchBarVisible })

  isRatingLoaded = () => {
    return this.isFilterLoaded('rating') && R.path(['rating', 'min'], this.props.searchStateValues) > 0
  }

  setSearchResults = hitsNumber => this.setState({ hitsNumber })

  showStarRatingFilterIfNeeded = () => !this.props.isAutoApprovalEnabled && (
    <CandidatesStarRatingWrapper
      min={0}
      max={5}
      attribute='rating'
      ratingValue={parseInt(R.pathOr(
        MIN_CANDIDATES_RATING_VALUE,
        ['props', 'searchState', 'range', 'rating', 'min'],
        this
      ), 10)}
      isApplicant
      isLoaded={this.isRatingLoaded()}
    />
  )

  render () {
    const { jobOffer } = this.props
    const searchBoxClassName = cx({
      'applicant-view__search-box': this.state.isSearchBarVisible,
      'applicant-view__search-box--hidden': !this.state.isSearchBarVisible
    })

    return this.state.ready ? (
      (
        <div>
          <div className='applicant-view-container'>
            <PageTopHeaderMobile title={t('common.applications')} />
            <ApplicantsViewHeader
              jobOffer={jobOffer}
              toggleSearchBar={this.toggleSearchBar}
              hitsNumber={this.state.hitsNumber}
              searchState={this.props.searchState}
            />
          </div>
          <div className='container container--large'>
            <div className={searchBoxClassName}>
              <ApplicantsSearchBox removeSeenApplicantsFilter={this.removeSeenApplicantsFilter} />
            </div>
            <OliviaBotBannerConnector className='applicant-view__olivia-banner' />
            <div className='applicant-view-container__search-wrapper'>
              <SearchResults
                hidden
                setSearchResults={this.setSearchResults}
                hideCurrentRefinements
              />
              <div className='applicant-view-container__switch-filters'>
                { this.showStarRatingFilterIfNeeded() }
                <SwitchButton
                  id='algolia-applicants-viewed'
                  labelLeft={t('common.showUnviewed')}
                  isChecked={this.state.showOnlyUnviewed}
                  onChange={this.switchSeenApplicants}
                />
                {
                  this.props.shouldShowSorting && <SwitchButton
                    labelLeft={`${t('common.sortedBy')} ${t('applicantsList.sortDropdown.CandidatesByApplicationDate')}`}
                    isChecked={this.state.sortByDate}
                    onChange={this.switchSortBy}
                  />
                }
              </div>
            </div>
            {!this.state.refreshHitsList && <ApplicantsViewGridConnector searchState={this.props.searchState} hitsNumber={this.state.hitsNumber} />}
          </div>
        </div>
      )
    ) : <Loader className='loader--app' />
  }
}

ApplicantsView.defaultProps = {
  jobOffer: {}
}

ApplicantsView.propTypes = {
  jobOffer: PropTypes.object.isRequired,
  fetchCountries: PropTypes.func.isRequired,
  resetBulkActionSelection: PropTypes.func.isRequired
}

export default ApplicantsView



