import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { isNil, when } from 'ramda'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import ApplicantsView from 'Employer/views/ApplicantsView/ApplicantsView'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import INDEX_NAME, { APPLICANTS_HITS_PER_PAGE_LIMIT, APPLICANTS_ALL_HITS_PER_PAGE_LIMIT } from 'Shared/constants/Algolia'
import { getFiltersFromUrl, getSearchStateValues, getUrlToSearchState } from 'Shared/selectors/url'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { resetBulkActionSelectionIfNeeded, getApplicationsForJobOffer, getJobOfferActiveApplicationIds, resetJobOfferActiveApplicationLists, getApplicationsCounters } from 'Shared/ducks/jobOfferApplications'
import { MIN_CANDIDATES_RATING_VALUE } from 'Shared/constants/Candidates'
import { getSavedSearchById } from 'Shared/selectors/savedSearchCriteria'
import { getApplicationListsByStatus, shouldShowSorting } from 'Shared/selectors/jobOfferApplications'
import { isAutoApprovalEnabled } from 'Shared/selectors/geolocation'

const filters = { rating: /(rating>=(\d+))/ }

const optionalFilters = {}

const mapStateToProps = (state, ownProps) => {
  const { jobOfferId } = ownProps.match.params
  const jobOffer = getJobOfferById(state, jobOfferId)
  const filtersFromUrl = getFiltersFromUrl(filters, ownProps.location.search)
  const searchState = getUrlToSearchState(ownProps.location.search, true)

  optionalFilters.boostedApplications = `boostedApplications:${jobOfferId}`

  when(
    isNil,
    () => {
      filtersFromUrl.rating = `rating>=${MIN_CANDIDATES_RATING_VALUE}`
    }
  )(filtersFromUrl.rating)

  return ({
    filters: filtersFromUrl,
    optionalFilters,
    indexName: INDEX_NAME.CANDIDATES,
    jobOffer,
    searchState: getUrlToSearchState(ownProps.location.search, true),
    languages: state.resources.languages.entries,
    countries: state.resources.countries.entries,
    searchStateValues: getSearchStateValues(searchState),
    selectedSearch: getSavedSearchById(state, ownProps.match.params.savedSearchId),
    configureProps: { hitsPerPage: shouldShowSorting(state, jobOfferId) ? APPLICANTS_ALL_HITS_PER_PAGE_LIMIT : APPLICANTS_HITS_PER_PAGE_LIMIT },
    applicationLists: getApplicationListsByStatus(state, ownProps.match.params.status),
    shouldShowSorting: shouldShowSorting(state, jobOfferId),
    isAutoApprovalEnabled: isAutoApprovalEnabled(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  resetBulkActionSelection: () => dispatch(resetBulkActionSelectionIfNeeded()),
  getApplicationsForJobOffer: jobOfferId => dispatch(getApplicationsForJobOffer(jobOfferId)),
  getJobOfferActiveApplicationIds: jobOfferId => dispatch(getJobOfferActiveApplicationIds(jobOfferId)),
  resetJobOfferActiveApplicationLists: () => dispatch(resetJobOfferActiveApplicationLists()),
  getApplicationsCounters: () => dispatch(getApplicationsCounters([ownProps.match.params.jobOfferId]))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withAlgolia(ApplicantsView)))



