import React, { useState } from 'react'
import { t } from 'i18next'
import { SmallJustIconButton, BigJustIconButton } from 'Shared/components/Button'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import DeclineApplicationModalConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileDeclineModalConnector'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { EmployerPermissions } from 'Shared/constants/User'
import cx from 'classnames'
import './ApplicationStatusThumbsToggle.scss'

const ToggleButtonComponent = props => {
  const ButtonComponent = props.small || props.isMobile ? SmallJustIconButton : BigJustIconButton
  const buttonClass = cx({
    'status-thumbs__button--selected': props.isSelected
  })

  return (
    <Tooltip content={props.tooltip}>
      <ButtonComponent onClick={props.onClick} className={buttonClass}>
        <i className={props.icon} />
      </ButtonComponent>
    </Tooltip>
  )
}

const ToggleButton = withDeviceWidthDetector(ToggleButtonComponent)

function ApplicationStatusThumbsToggle (props) {
  const [ declineModalVisibility, setDeclineModalVisibility ] = useState(false)

  const openDeclineModal = () => setDeclineModalVisibility(true)
  const closeDeclineModal = () => setDeclineModalVisibility(false)

  const noAction = () => {}

  const shortlistButton = onClick => <ToggleButton
    onClick={onClick}
    small={props.small}
    isSelected={props.isShortlisted}
    tooltip={t('employer.applicants.shortlistApplicantTooltip')}
    icon='ion-md-thumbs-up'
  />

  const declineButton = onClick => <ToggleButton
    onClick={onClick}
    small={props.small}
    isSelected={props.isDeclined}
    tooltip={t('employer.applicants.declineApplicantTooltip')}
    icon='ion-md-thumbs-down'
  />

  const onShortlist = () => props.handleShortlistApplication(props.applicationId)

  return (
    <div className='status-thumbs__container'>
      <LimitedAccessWrapperConnector
        placeholderComponent={shortlistButton(noAction)}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        {shortlistButton(onShortlist)}
      </LimitedAccessWrapperConnector>
      <LimitedAccessWrapperConnector
        placeholderComponent={declineButton(noAction)}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
        jobOfferId={props.jobOfferId}
      >
        {declineButton(openDeclineModal)}
      </LimitedAccessWrapperConnector>
      <DeclineApplicationModalConnector
        applicationId={props.applicationId}
        show={declineModalVisibility}
        onClose={closeDeclineModal}
      />
    </div>
  )
}

export default ApplicationStatusThumbsToggle



