import {
  getFormNames,
  isDirty,
  isSubmitting,
  isInvalid,
  getFormSyncErrors,
  hasSubmitSucceeded,
  hasSubmitFailed
} from 'redux-form'
import { getJobOfferStatus } from 'Shared/selectors/jobOffers'
import has from 'lodash/has'
import { createSelector } from 'reselect'
import * as R from 'ramda'
import { isNotEmpty } from 'Shared/helpers/Ramda'
import { Statuses } from 'Shared/constants/Job'
import { t } from 'i18next'
import { getIdFromLocation } from 'Shared/helpers/string'

const getForms = state => state.form

const isFormSubmitting = formName => state => isSubmitting(formName)(state)

const isFormDirty = formName => state => isDirty(formName)(state)

const isFormInvalid = formName => state => isInvalid(formName)(state)

const getCurrentFormNames = state => getFormNames()(state)

export const getFormsDirtyState = state => getCompleteProfileForms(state).filter(name => isFormDirty(name)(state))

export const isAnyFormSubmitting = state => getCompleteProfileForms(state).some(name => isFormSubmitting(name)(state))

export const isAnyFormInvalid = state => getCompleteProfileForms(state).some(name => isFormInvalid(name)(state))

export const isEveryFormSubmitSucceeded = state => getCompleteProfileForms(state).every(name => hasSubmitSucceeded(name)(state))

export const isAnyFormSubmitFailed = state => getCompleteProfileForms(state).some(name => hasSubmitFailed(name)(state))

export const isFieldInvalid = (formName, fieldName) => state => has(getFormSyncErrors(formName)(state), fieldName) && hasSubmitFailed(formName)(state)

export const experienceDirtyForms = state => getExperienceForms(state).filter(name => isFormDirty(name)(state))

export const certificateDirtyForms = state => getCertificateForms(state).filter(name => isFormDirty(name)(state))

export const volunteeringDirtyForms = state => getVolunteeringForms(state).filter(name => isFormDirty(name)(state))

export const languageDirtyForms = state => getLanguageForms(state).filter(name => isFormDirty(name)(state))

export const educationDirtyForms = state => {
  const educationForms = getEducationForms(state)
  const education = ['CompleteProfile-DefineEducationLevel', ...educationForms]
  return education.filter(name => isFormDirty(name)(state))
}

export const getFieldErrorMessage = (formName, fieldName) => state => {
  return R.prop(fieldName)(getFormSyncErrors(formName)(state))
}

export const getLanguageForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('Language') > -1)
  )
)

export const getExperienceForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('Experience') > -1)
  )
)

export const getCertificateForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('Certificate') > -1)
  )
)

export const getVolunteeringForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('Volunteering') > -1)
  )
)

export const getEducationForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('Education') > -1)
  )
)

export const getCompleteProfileForms = createSelector(
  getCurrentFormNames,
  currentForms => (
    currentForms.filter(formName => formName.indexOf('CompleteProfile') > -1)
  )
)

export const getJobOfferForms = createSelector(
  getCurrentFormNames,
  R.filter(formName => R.includes(formName, ['EditJobOffer', 'AddJobOffer']))
)

export const shouldDisableConfirmNavigation = createSelector(
  getCompleteProfileForms,
  isAnyFormInvalid,
  getJobOfferForms,
  (state) => state,
  (completeProfileForms, isAnyCompleteProfileFormInvalid, jobOfferForms, state) => {
    const getJobOfferInitStatus = getJobOfferStatus(state, getIdFromLocation(window.location.pathname))
    switch (true) {
      case isNotEmpty(completeProfileForms):
        return isAnyCompleteProfileFormInvalid
      case isNotEmpty(jobOfferForms) && jobOfferForms.length === 1 && getJobOfferInitStatus !== Statuses.draft:
        return isFormInvalid(jobOfferForms[0])(state)
      default:
        return false
    }
  }
)

export const isJobOfferFormConfirmNavigation = createSelector(
  getJobOfferForms,
  (jobOfferForms) => isNotEmpty(jobOfferForms) && jobOfferForms.length === 1
)

export const isProfileCompletionFormConfirmNavigation = createSelector(
  getCompleteProfileForms,
  (completeProfileForms) => isNotEmpty(completeProfileForms)
)

export const isDraftForm = createSelector(
  getJobOfferForms,
  (state) => state,
  (jobOfferForms, state) => {
    const getJobOfferInitStatus = getJobOfferStatus(state, getIdFromLocation(window.location.pathname))
    return isNotEmpty(jobOfferForms) && jobOfferForms.length === 1 && getJobOfferInitStatus === Statuses.draft
  }
)

export const isInactiveForm = createSelector(
  getJobOfferForms,
  (state) => state,
  (jobOfferForms, state) => {
    const getJobOfferInitStatus = getJobOfferStatus(state, getIdFromLocation(window.location.pathname))
    return isNotEmpty(jobOfferForms) && jobOfferForms.length === 1 && getJobOfferInitStatus === Statuses.inactive
  }
)

export const submitConfirmNavigationName = createSelector(
  isProfileCompletionFormConfirmNavigation,
  isJobOfferFormConfirmNavigation,
  isDraftForm,
  isInactiveForm,
  shouldDisableConfirmNavigation,
  (isProfileCompletion, isJobOffer, isDraftForm, isInactiveForm, disabled) => {
    switch (true) {
      case isProfileCompletion:
        return t('common.leaveAndSave')
      case isJobOffer && !disabled && isDraftForm:
        return t('common.saveAsDraft')
      case isJobOffer && !disabled && isInactiveForm:
        return t('common.saveAndPublish')
      case isJobOffer && disabled:
        return t('common.stay')
      default:
        return t('common.leaveAndSave')
    }
  }
)

export const leaveConfirmNavigationName = createSelector(
  isProfileCompletionFormConfirmNavigation,
  isJobOfferFormConfirmNavigation,
  shouldDisableConfirmNavigation,
  (isProfileCompletion, isJobOffer, disabled) => {
    switch (true) {
      case isProfileCompletion:
        return t('common.leave')
      case isJobOffer && !disabled:
        return t('common.leaveWithoutSaving')
      case isJobOffer && disabled:
        return t('common.discardChanges')
      default:
        return t('common.leave')
    }
  }
)

export const confirmNavigationModalTitle = createSelector(
  isJobOfferFormConfirmNavigation,
  shouldDisableConfirmNavigation,
  (isJobOffer, disabled) => {
    switch (true) {
      case isJobOffer && disabled:
        return t('modals.unsavedInvalidJobOffer.title')
      default:
        return t('common.unsavedChanges')
    }
  }
)

export const completeProfileErrors = createSelector(
  getCompleteProfileForms,
  getForms,
  (completeProfile, forms) => {
    return R.pipe(
      R.pick(completeProfile),
      R.values,
      R.map(
        R.propOr({}, 'syncErrors')
      ),
      R.mergeAll
    )(forms)
  }
)



