import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ApplicantsSearchFilters from 'Employer/components/ApplicantsSearchFilters'
import { getUrlToSearchState, getSearchStateValues } from 'Shared/selectors/url'

const mapStateToProps = (state, ownProps) => {
  const searchState = getUrlToSearchState(ownProps.location.search, true)

  return ({
    searchStateValues: getSearchStateValues(searchState),
    searchState
  })
}

export default withRouter(connect(mapStateToProps)(ApplicantsSearchFilters))



