import { reduxForm, formValueSelector, arrayPush, change, getFormValues, getFormSyncErrors } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { t } from 'i18next'
import { mergeProps, transformToArray } from 'Shared/helpers/redux'
import { fetchDepartments } from 'Shared/ducks/departments'
import validate from 'Employer/validators/JobOfferValidator'
import JobOffer from 'Employer/forms/JobOffer/JobOffer'
import { addUserNewSkill } from 'Shared/ducks/skills'
import {
  editJobOffer,
  fetchJobOfferTemplate,
  fetchJobOfferTemplates,
  fetchPreviousJobOffersAsTemplates,
  handleHidePostJobOfferAlert,
  setNewApplicationNotifications
} from 'Shared/ducks/jobOffers'
import { setTeamMembersForJobOffer } from 'Shared/ducks/jobOffersMembers'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { Genders, JobTypes } from 'Shared/constants/Job'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import {
  hasJobSlotsAvailable,
  hasJobPostsAvailable,
  neverHadAnyMembership,
  activeMembershipJobPostsLeft,
  isCurrentMembershipExpired,
  isCurrentMembershipCancelled, getActiveMembershipDetails, hasUnlimitedJobPosts
} from 'Shared/selectors/membership'
import { getEmployerCompanyDetailsCompletion, getUsersOrganisation } from 'Shared/selectors/organisations'
import { isNil, prop, propOr } from 'ramda'
import { fetchCurrenciesIfNeeded } from 'Shared/ducks/resources'
import { getCurrenciesAsOptions } from 'Shared/selectors/resources'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import {
  removeOrganisationImageFile,
  updateOrganisation,
  uploadOrganisationImageFile
} from 'Shared/ducks/organisations'
import { OrganisationFileKeyPrefixes } from 'Shared/constants/Organisation'
import { getOrganisationEmployers } from 'Shared/ducks/employers'
import { organisationAdminEmployerFullName } from 'Shared/selectors/employers'
import { isAdminEmployer } from 'Shared/selectors/user'
import { fetchCurrentMembershipIfNeeded } from 'Shared/ducks/membership'
import { getDefaultMemberOptionIfNoMemberExist, getJobOfferMembersOptions } from 'Shared/selectors/jobOffersMembers'
import { updateUserAttributes } from 'Chat/ducks/classes/users'
import { hasCompanyProfilePermission } from 'Shared/selectors/teamMembersAccess'

export const formName = 'EditJobOffer'

const formUtils = new FormUtilsService()
const mapOptionsByLanguage = formUtils.mapOptionsByLanguageTranslations

let Form = reduxForm({
  form: formName,
  onSubmitFail: scrollToFirstError,
  validate
})(JobOffer)

const mapStateToProps = (state, ownProps) => {
  const organisation = getUsersOrganisation(state)
  const selectedJobOffer = getJobOfferById(state, ownProps.match.params.jobOfferId)
  const languagesEntries = state.resources.languages.entries
  const { coverLetterRequired, preferredVideoCv, preferredExperience, isPrivate } = selectedJobOffer

  return ({
    membershipDetails: getActiveMembershipDetails(state),
    jobPostsUnlimited: hasUnlimitedJobPosts(state),
    jobPostsAmount: activeMembershipJobPostsLeft(state),
    isCurrentMembershipExpired: isCurrentMembershipExpired(state),
    isCurrentMembershipCancelled: isCurrentMembershipCancelled(state),
    neverHadAnyMembership: neverHadAnyMembership(state),
    isCompanyProfileComplete: getEmployerCompanyDetailsCompletion(state),
    canUseJobSlots: hasJobSlotsAvailable(state),
    canUseJobPosts: hasJobPostsAvailable(state),
    pageHeading: t(`jobOffer.editAJob.description.heading`),
    postAJobAccessAlertModalVisible: state.jobOffers.postJobOfferAlertVisible,
    initialValues: {
      ...selectedJobOffer,
      oliviaEnabled: propOr(false, 'oliviaEnabled', selectedJobOffer),
      customCareerSiteUrl: selectedJobOffer.customCareerSiteUrl,
      customCareerSiteUrlEnabled: !!selectedJobOffer.customCareerSiteUrl,
      id: selectedJobOffer.id,
      skills: mapOptionsByLanguage(selectedJobOffer.skills),
      location: {
        address: selectedJobOffer.address,
        city: selectedJobOffer.city,
        country: selectedJobOffer.country,
        locationCoords: selectedJobOffer.locationCoords,
        googlePlacesId: selectedJobOffer.googlePlacesId
      },
      preferredLanguages: selectedJobOffer.preferredLanguages
        .map(lang => ({
          value: languagesEntries[lang.languageCode.toUpperCase()].code,
          label: languagesEntries[lang.languageCode.toUpperCase()].language })),
      salary: selectedJobOffer.remuneration,
      currencyId: selectedJobOffer.currencyId || state.geolocation.currentLocation.currencyId,
      coverLetterRequired: isNil(coverLetterRequired) ? false : coverLetterRequired,
      preferredVideoCv: isNil(preferredVideoCv) ? false : preferredVideoCv,
      preferredExperience: isNil(preferredExperience) ? false : preferredExperience,
      isPrivate: isNil(isPrivate) ? false : isPrivate,
      status: selectedJobOffer.status,
      template: 'blank',
      shouldNotifyOnNewApplication: selectedJobOffer.shouldNotifyOnNewApplication,
      teamMembers: getDefaultMemberOptionIfNoMemberExist(state, ownProps.match.params.jobOfferId),
      companyInformation: {
        logoUrl: organisation.logoUrl,
        description: organisation.description,
        location: {
          address: organisation.address,
          city: organisation.city,
          country: organisation.country,
          locationCoords: organisation.locationCoords,
          googlePlacesId: organisation.googlePlacesId
        }
      },
      locationCityWithCountry: selectedJobOffer.address
    },
    blankInitialValues: {
      skills: [],
      preferredGender: Genders.undisclosed,
      coverLetterRequired: false,
      preferredVideoCv: false,
      preferredExperience: false,
      isPrivate: false,
      preferredLanguages: [],
      status: selectedJobOffer.status,
      template: 'blank',
      teamMembers: [],
      companyInformation: {
        industryId: organisation.industryId,
        logoUrl: organisation.logoUrl,
        name: organisation.name,
        description: organisation.description,
        size: organisation.size,
        location: {
          address: organisation.address,
          city: organisation.city,
          country: organisation.country,
          locationCoords: organisation.locationCoords,
          googlePlacesId: organisation.googlePlacesId
        }
      }
    },
    isJobAnInternship: formValueSelector(formName)(state, 'jobType') === JobTypes().mirror.internship,
    remunerationType: formValueSelector(formName)(state, 'remunerationType'),
    isOfferAsap: formValueSelector(formName)(state, 'startDateAsap'),
    selectedSkills: formValueSelector(formName)(state, 'skills'),
    isCustomCareerSiteUrlEnabled: formValueSelector(formName)(state, 'customCareerSiteUrlEnabled'),
    languages: transformToArray(languagesEntries)
      .map(item => { return {label: item.language, value: item.code} }),
    hasStartDateFieldError: isFieldInvalid(formName, 'startDateAsap')(state),
    hasCommitmentFieldError: isFieldInvalid(formName, 'commitment')(state),
    hasRemunerationTypeFieldError: isFieldInvalid(formName, 'remunerationType')(state),
    hasJobTypeFieldError: isFieldInvalid(formName, 'jobType')(state),
    hasPermanentJobOpportunityFieldError: isFieldInvalid(formName, 'permanentJobOpportunity')(state),
    formValues: getFormValues(formName)(state),
    isAdmin: isAdminEmployer(state),
    jobOfferTemplates: state.jobOffers.templates,
    edit: true,
    languagesEntries: state.resources.languages.entries,
    currencies: getCurrenciesAsOptions(state),
    defaultCurrency: state.geolocation.currentLocation.currencyId,
    industries: mapOptionsByLanguage(transformToArray(state.industries.entries)),
    size: formUtils.mapOptionsWithCustomIdByLanguageTranslations(transformToArray(state.resources.companySize.entries), 'value', 'name'),
    formErrors: getFormSyncErrors(formName)(state),
    teamMembers: getJobOfferMembersOptions(state),
    isAdminEmployer: isAdminEmployer(state),
    adminFullName: organisationAdminEmployerFullName(state, state.user.details.organisationId),
    selectedJobType: formValueSelector(formName)(state, 'jobType'),
    hasCompanyProfilePermission: hasCompanyProfilePermission(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchDepartments: () => dispatch(fetchDepartments()),
  addUserNewSkill: skill => dispatch(addUserNewSkill(skill)),
  pushSkill: skill => dispatch(arrayPush(formName, 'skills', skill)),
  postJob: async (values, oldStatus) => {
    await dispatch(setTeamMembersForJobOffer(ownProps.match.params.jobOfferId, propOr([], 'teamMembers', values).map(val => ({ id: prop('value', val) }))))
    await dispatch(editJobOffer({...values, id: ownProps.match.params.jobOfferId}, oldStatus))
    return dispatch(setNewApplicationNotifications(ownProps.match.params.jobOfferId, values.shouldNotifyOnNewApplication))
  },
  changeFieldValue: (fieldName, value) => dispatch(change(formName, fieldName, value)),
  hidePostAJobAccessAlertModal: () => dispatch(handleHidePostJobOfferAlert()),
  fetchJobOfferTemplates: () => dispatch(fetchJobOfferTemplates()),
  fetchJobOfferTemplate: (id, ownTemplate) => dispatch(fetchJobOfferTemplate(id, ownTemplate)),
  fetchPreviousJobOffersAsTemplates: () => dispatch(fetchPreviousJobOffersAsTemplates()),
  fetchCurrenciesIfNeeded: () => dispatch(fetchCurrenciesIfNeeded()),
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  fetchCurrentMembershipIfNeeded: () => dispatch(fetchCurrentMembershipIfNeeded()),
  uploadOrganisationLogo: async (file, filename) => {
    await dispatch(uploadOrganisationImageFile(file, filename, OrganisationFileKeyPrefixes.logoUrl))
    dispatch(updateUserAttributes())
  },
  deleteOrganisationLogo: () => dispatch(removeOrganisationImageFile(OrganisationFileKeyPrefixes.logoUrl)),
  updateOrganisation: async values => {
    await dispatch(updateOrganisation(values, true))
    dispatch(updateUserAttributes())
  },
  getOrganisationEmployers: () => dispatch(getOrganisationEmployers())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps, { withRef: true })(Form))



