import React, { Component } from 'react'
import { Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Checkbox, RadioWrapper } from 'Shared/components/FormElements'

class ExternalLink extends Component {
  render () {
    const { t, hasShortlistingAvailable } = this.props
    return !hasShortlistingAvailable && (
      <div className='form__group'>
        <RadioWrapper
          name='customCareerSiteUrl'
          legend={t('jobOffer.postAJob.additionalPreferences.autoShortlistingTitle')}
          layout={'checkbox-vertical'}
        >
          <Field
            component={Checkbox}
            text={t('jobOffer.postAJob.additionalPreferences.autoShortlisting')}
            name='oliviaEnabled'
            normalize={v => !!v}
          />
        </RadioWrapper>
        <div className='job-offer__notifications-section-text'>
          {t('jobOffer.postAJob.additionalPreferences.autoShortlistingDescription')}
        </div>
      </div>
    )
  }
}

export default withTranslations(ExternalLink)



