import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'

import './BulkManagerWrapper.scss'

class BulkManagerWrapper extends PureComponent {
  render () {
    const { bulkOperationItemsLength, t } = this.props
    const statusBarClass = cx({
      'bulk-manager': true,
      'bulk-manager--visible': bulkOperationItemsLength
    })

    return (
      <div className={statusBarClass}>
        <Container>
          <div className='bulk-manager__wrapper'>
            <div className='bulk-manager__counter'>
              {t('jobOfferApplications.selectedApplicants', { count: bulkOperationItemsLength })}
            </div>
            <div className='bulk-manager__actions'>
              {this.props.children}
            </div>
          </div>
        </Container>
      </div>
    )
  }
}

BulkManagerWrapper.defaultProps = {
  bulkOperationItemsLength: 0
}

BulkManagerWrapper.propTypes = {
  bulkOperationItemsLength: PropTypes.number
}

export default withTranslations(BulkManagerWrapper)



