import React, { Fragment } from 'react'
import { t } from 'i18next'
import { arrayApplicantStatuses } from 'Shared/constants/Applicants'
import { EntitiesListTabWrapper, EntitiesListTabLink } from 'Shared/components/EntitiesListTabs'
import './ApplicantsViewGrid.scss'
import * as R from 'ramda'

class ApplicantsTabs extends React.Component {
  state = {
    currentStatus: this.props.location.pathname.split('/').slice(-1)[0],
    switchTabAndNotReceiveNewHits: false,
    positionY: window.scrollY
  }

  componentDidMount () {
    this.props.getApplicationsCounters()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.match.params.status !== prevProps.match.params.status) {
      this.setState({ switchTabAndNotReceiveNewHits: true })
    }

    if (this.props.hitsNumber !== prevProps.hitsNumber) {
      this.setState({ switchTabAndNotReceiveNewHits: false })
    }
  }

  getBadgeNumber = applicantStatus => {
    const { getApplicationsNumber, match: { params: { status } } } = this.props
    const apiNumber = getApplicationsNumber(applicantStatus)

    switch (true) {
      case status !== applicantStatus:
        return apiNumber
      case status === applicantStatus && this.state.switchTabAndNotReceiveNewHits:
        return apiNumber
      default:
        return this.props.hitsNumber
    }
  }

  isDefaultSearch = () => {
    const hasDefaultFilters = R.pipe(
      R.pathOr([], ['configure', 'filters']),
      R.includes('jobOfferIdsInApplicationsSeen'),
      R.not
    )
    const hasDefaultRating = R.pipe(
      R.pathOr({}, ['range', 'rating']),
      R.allPass([R.propEq('min', '0'), R.propEq('max', '5')])
    )
    const hasDefaultSorting = R.propEq('sort', 'CandidatesByApplicationDate')
    const hasDefaultRefinementList = R.pipe(
      R.propOr([], 'refinementList'),
      R.values,
      R.all(R.isEmpty)
    )

    return R.allPass([hasDefaultFilters, hasDefaultRating, hasDefaultSorting, hasDefaultRefinementList])(this.props.searchState)
  }

  savePositionY = () => this.setState({ positionY: window.scrollY })

  get Tabs () {
    const { match: { params: { jobOfferId } } } = this.props
    return (
      <div className='applicants-view__tabs'>
        <EntitiesListTabWrapper>
          {
            arrayApplicantStatuses.map(applicantStatus => {
              const redirectPath = {
                pathname: `/employer/job-offer/${jobOfferId}/applicants/${applicantStatus}`,
                search: this.props.location.search
              }
              const { status } = this.props.match.params

              return (
                <EntitiesListTabLink
                  key={`applicants-tab-${applicantStatus}`}
                  text={t(`employer.applicants.applicantsType.${applicantStatus}`)}
                  showBadge={this.props.applicationListsLoaded && (this.isDefaultSearch() || status === applicantStatus)}
                  badge={this.getBadgeNumber(applicantStatus)}
                  onClick={this.savePositionY}
                  to={redirectPath}
                  isActive={redirectPath === this.props.location.pathname}
                />
              )
            })
          }
        </EntitiesListTabWrapper>
      </div>
    )
  }

  render () {
    return (
      <Fragment>
        {this.Tabs}
      </Fragment>
    )
  }
}

export default ApplicantsTabs



