import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { FieldArray, Field } from 'redux-form'
import SkillsSelectConnector from 'Candidate/connectors/SkillsSelectConnector'
import { TagWrapper } from 'Shared/components/FormElements/index'
import withTranslations from 'Shared/hoc/withTranslations'

class SkillsWrapper extends Component {
  state = {
    refinement: '',
    createDisabled: false
  }

  setRefinement = value => this.setState({ refinement: value })

  disableCreate = () => this.setState({ createDisabled: true })
  enableCreate = () => this.setState({ createDisabled: false })

  addSkill = value => this.props.pushSkill(value)

  addNewSkill = async (option) => {
    this.disableCreate()
    const { id } = await this.props.addUserNewSkill(option.value)
    this.props.pushSkill({ value: id, label: option.label })
    this.enableCreate()
  }

  render () {
    const { t } = this.props
    const disabled = this.state.createDisabled || (this.props.selectedSkills.length >= this.props.skillsLength)
    return (
      <div>
        <FieldArray
          name='skills'
          component={TagWrapper}
          sortable
        />
        <Field
          component={SkillsSelectConnector}
          clearable
          defaultRefinement={this.state.refinement}
          onSearch={this.setRefinement}
          disabled={disabled}
          placeholder={disabled
            ? t('forms.skills.disabledPlaceholder', { number: this.props.selectedSkills.length })
            : t('forms.skills.placeholder')}
          name='skill'
          onSelect={this.addSkill}
          onNewOptionClick={this.addNewSkill}
          selectedValues={this.props.selectedSkills}
        />
      </div>
    )
  }
}

SkillsWrapper.propTypes = {
  selectedSkills: PropTypes.array,
  addUserNewSkill: PropTypes.func.isRequired,
  pushSkill: PropTypes.func.isRequired,
  skillsLength: PropTypes.number.isRequired
}

SkillsWrapper.defaultProps = {
  selectedSkills: []
}

export default withTranslations(SkillsWrapper)



