import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import InviteToInterviewTrigger from 'Employer/components/InviteToInterviewTrigger/InviteToInterviewTrigger'
import { getApplicationCandidateId, getApplicationJobOfferId, getApplicationChatId, getApplicationInterviewInvitationStatus } from 'Shared/selectors/jobOfferApplications'
import { getChannelByJobOfferAndCandidateId, isChannelMember } from 'Chat/selectors/channels'
import { hasActiveConversation } from 'Shared/selectors/jobOfferApplicationChats'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import { getCandidateDetails } from 'Shared/selectors/candidates'
import { startChatConversation } from 'Chat/ducks/classes/channels'

const mapStateToProps = (state, ownProps) => {
  const applicationId = ownProps.match.params.applicationId
  const candidateId = getApplicationCandidateId(state, applicationId)
  const jobOfferId = getApplicationJobOfferId(state, applicationId)
  const jobOffer = getJobOfferById(state, jobOfferId)
  const chatId = getApplicationChatId(state, applicationId)

  return ({
    interviewInvitationStatus: getApplicationInterviewInvitationStatus(state, applicationId),
    candidate: getCandidateDetails(state, candidateId),
    getChannelByJobOfferAndCandidateId: getChannelByJobOfferAndCandidateId(state, jobOfferId, candidateId),
    jobOfferId,
    jobOffer,
    applicationId,
    candidateId,
    isChannelMember: isChannelMember(state, chatId),
    hasActiveConversation: hasActiveConversation(state, chatId),
    areChannelsFetched: state.chat.areChannelsFetched,
    joinToChannelLoading: state.chat.joinToChannelLoading
  })
}

const mapDispatchToProps = dispatch => ({
  startChatConversation: (jobOffer, candidateId, applicationId) => dispatch(startChatConversation(jobOffer, candidateId, applicationId))

})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(InviteToInterviewTrigger)



