import ApplicantsAllHitsInfiniteScroll from 'Algolia/components/ApplicantsAllHitsInfiniteScroll'
import qs from 'query-string'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { propOr } from 'ramda'
import {
  sortHitsByApplicationDate
} from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const query = qs.parse(ownProps.history.location.search)

  return ({
    sortBy: propOr('', 'sort', query),
    sortHitsByApplicationDate: hits => sortHitsByApplicationDate(state, hits)
  })
}

export default withRouter(connect(mapStateToProps)(ApplicantsAllHitsInfiniteScroll))



