import React from 'react'
import ApplicationDetailsActionButtonsMobile from './ApplicationDetailsActionButtons.mobile'
import ApplicationDetailsActionButtonsWeb from './ApplicationDetailsActionButtons'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ApplicationDetailsActionButtons = props => props.isMobile ? <ApplicationDetailsActionButtonsMobile {...props} /> : <ApplicationDetailsActionButtonsWeb {...props} />

export default withDeviceWidthDetector(ApplicationDetailsActionButtons)



