import React, { useEffect } from 'react'
import { t } from 'i18next'
import Modal from 'Shared/components/Modal'
import DeclineApplicationFormConnector from 'Employer/connectors/Forms/Applications/DeclineApplicationFormConnector'
import './DeclineApplicationModal.scss'

function DeclineApplicationModal (props) {
  useEffect(() => {
    if (props.shouldCloseModal) {
      props.onClose()
    }
  }, [props.shouldCloseModal])

  return (
    <Modal
      title={t('modals.changeApplicationStatus.declinedTitle')}
      show={props.show}
      handleClose={props.onClose}
      handleCancel={props.onClose}
      handleSubmit={props.submitDeclinedForm}
      disableSubmitButton={props.isModalSubmitting}
      disableCancelButton={props.isModalSubmitting}
      closeOnSubmit={false}
      cancelText={t('common.cancel')}
      submitText={props.isModalSubmitting ? t('common.loading') : t('common.submit')}
    >
      <DeclineApplicationFormConnector
        applicationId={props.applicationId}
        candidateFirstName={props.candidateFirstName}
      />
    </Modal>
  )
}

export default DeclineApplicationModal



