import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileDocuments from 'Employer/components/CandidateProfileDetails/CandidateProfileDocuments'
import { getCandidateNonCvFiles } from 'Shared/selectors/files'

const mapStateToProps = state => ({
  documents: getCandidateNonCvFiles(state),
  shouldUnlock: false
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileDocuments)



