import React from 'react'
import SkillsWrapper from 'Shared/components/SkillsWrapper'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import withTranslations from 'Shared/hoc/withTranslations'
import { MAX_SKILLS_LENGTH } from 'Shared/constants/Validation'
import ProfileStepSection from 'Shared/components/ProfileStepSection'

const Skills = (props) => (
  <div>
    <ProfileStepSection
      label={props.t('jobOffer.postAJob.skills.label')}
      text={props.t('jobOffer.postAJob.skills.text')}
    />
    <SkillsWrapper {...props} skillsLength={MAX_SKILLS_LENGTH.JOB_OFFER} />
  </div>
)

export default withTranslations(withAlgolia(Skills))



