import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import { isEmpty } from 'ramda'
import cx from 'classnames'

import { DurationTypesLabels, JobTypes } from 'Shared/constants/Job'
import { formats } from 'Shared/constants/Date'

import { getCountryNameByISOCode } from 'Shared/helpers/Country'
import { formatDate } from 'Shared/helpers/date'

import withTranslations from 'Shared/hoc/withTranslations'

import BackButton from 'Shared/components/BackButton/BackButton'
import { BigWithIconButton } from 'Shared/components/Button'
import ApplicantsTabsConnector from 'Employer/connectors/Views/Applicants/ApplicantsTabsConnector.js'
import { applicantsSelectedFiltersCount } from 'Shared/helpers/Algolia'
import './ApplicantsViewHeader.scss'

class ApplicantsViewHeader extends PureComponent {
  render () {
    const { t, jobOffer } = this.props

    const privateIcon = cx({
      'ion-ios-eye-off': jobOffer.isPrivate,
      'ion-ios-eye': !jobOffer.isPrivate
    })

    const detailClass = cx({
      'applicant-view__details': true,
      'applicant-view__details--loading': isEmpty(jobOffer)
    })

    return (
      <div className='container container--large'>
        <div className='applicant-view__heading'>
          <div className='columns'>
            <div className='column col-12'>
              <BackButton
                className='applicant-view__button--back'
                link={'/employer/job-offers/active'}
                name={t('common.goBackToMyJobs')}
              />
            </div>
            <div className='column col-8 col-sm-12 applicant-view__heading__job-title'>
              <div>
                <div className='applicant-view__date'>
                  {t('common.expiresAt', { date: formatDate(jobOffer.expiresAt, formats.dashed) })}
                </div>
                <div className='applicant-view__title'>{jobOffer.title}</div>
                <div className={detailClass}>
                  <span>{`${JobTypes()[jobOffer.jobType]}`}</span>
                  <span>{`${DurationTypesLabels()[jobOffer.duration]}`}</span>
                  <span>{`${jobOffer.city}`}</span>
                  <span>{`${getCountryNameByISOCode(jobOffer.country)}`}</span>
                  <i className={privateIcon} />
                </div>
              </div>
            </div>
            <div className='column col-4 col-sm-12'>
              <div className='applicant-view__buttons'>
                <Link to={`/employer/job-offer/${jobOffer.id}/candidates`}>
                  <BigWithIconButton>
                    <i className='icon-im-members' />{t('employer.jobOffers.findCandidates')}
                  </BigWithIconButton>
                </Link>
                <BigWithIconButton className='applicants-view-heading__filters-btn' onClick={this.props.toggleSearchBar}>
                  <i className='icon-im-filters' />{t('common.filters')}
                  {applicantsSelectedFiltersCount(this.props.searchState) > 0 && <div className='applicants-view-heading__filters-count'>{applicantsSelectedFiltersCount(this.props.searchState)}</div>}
                </BigWithIconButton>
              </div>
            </div>
            <div className='column col-12'>
              <ApplicantsTabsConnector hitsNumber={this.props.hitsNumber} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

ApplicantsViewHeader.propTypes = {
  jobOffer: PropTypes.object.isRequired
}

export default withTranslations(ApplicantsViewHeader)



