import React, { Component } from 'react'
import { PrimaryButtonOutline } from 'Shared/components/Button'
import { t } from 'i18next'
import { ConfirmPopup } from 'Shared/components/Popup'
import './JoinActiveChannelPopup.scss'

class JoinActiveChannelPopup extends Component {
  handleJoin = async () => {
    const { joinChannel, signature, history, userId, candidateId, jobOfferId } = this.props
    await joinChannel(signature, { candidateId, jobOfferId, employerIds: [userId] }).then(res => {
      history.push(`/employer/chat/${res.sid}`)
    })

    // TODO: find out why component doesn't update after changes in reducer // @Sandra 24/01/2019
  }

  render () {
    const { children, candidateName } = this.props

    return (
      <div className='join-active-channel-popup'>
        <ConfirmPopup
          position='bottom'
          align='center'
          triggerButton={children}
          withSubmit
          submitButton={PrimaryButtonOutline}
          onSubmit={this.handleJoin}
          submitName={t('chat.joinConversation')}
          cancelName={t('common.cancel')}
          withCancel
        >
          <div className='join-active-channel-popup__content'>
            {t('chat.joinActiveChannel', { candidateName })}
          </div>
        </ConfirmPopup>
      </div>
    )
  }
}

export default JoinActiveChannelPopup



