import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileLanguages from 'Employer/components/CandidateProfileDetails/CandidateProfileLanguages'
import { getCandidateLanguages } from 'Shared/selectors/languages'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    languages: getCandidateLanguages(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileLanguages)



