import React from 'react'
import { Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Checkbox, RadioWrapper } from 'Shared/components/FormElements'
import ProfileStepSection from 'Shared/components/ProfileStepSection/index'
import TeamMemberMultiSelect from 'Shared/components/FormElements/TeamMemberMultiSelect'
import TipButton from 'Shared/components/TipButton/TipButton'

const Notifications = ({ t, teamMembers, isAdmin }) => {
  const showSettings = () => window.open('/employer/settings/team-members', '_blank')
  return (
    <div>
      <ProfileStepSection
        label={t('jobOffer.postAJob.notificationsSection.label')}
        text={t('jobOffer.postAJob.notificationsSection.text')}
      />
      <div className='form__group form__group--with-tip'>
        {isAdmin && <TipButton onClick={showSettings}>{t('jobOffer.postAJob.notificationsSection.inviteTeam')}</TipButton>}
        <Field
          component={TeamMemberMultiSelect}
          name='teamMembers'
          label={t('jobOffer.postAJob.notificationsSection.teamMembersLabel')}
          options={teamMembers}
        />
      </div>
      <div className='job-offer__no-label'>
        <RadioWrapper
          name='notifications'
          layout={'checkbox-vertical'}
          optional
        >
          <Field
            component={Checkbox}
            text={t('jobOffer.postAJob.notifications.newApplications')}
            name='shouldNotifyOnNewApplication'
            normalize={v => !!v}
          />
        </RadioWrapper>
      </div>
    </div>
  )
}

export default withTranslations(Notifications)



