import { connect } from 'react-redux'
import React from 'react'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileNavbar from 'Employer/components/CandidateProfileDetails/CandidateProfileNavbar'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import ApplicationProfileAvailabilityConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationProfileAvailabilityConnector'
import { getCandidateDetails, getCandidateFullName, isCandidateAvailable } from 'Shared/selectors/candidates'
import { getCandidateProfileNavigationItems } from 'Shared/helpers/profile'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    fullName: getCandidateFullName(state, candidateId),
    details: getCandidateDetails(state, candidateId),
    availabilitySection: () => <ApplicationProfileAvailabilityConnector />,
    navigationItems: getCandidateProfileNavigationItems(),
    isAvailable: isCandidateAvailable(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileNavbar)



