import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileExperience from 'Employer/components/CandidateProfileDetails/CandidateProfileExperience'
import { getCandidateExperience } from 'Shared/selectors/experience'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    experiences: getCandidateExperience(state, candidateId),
    withHighlights: true,
    candidateId
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileExperience)



