import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import applicationStatuses, { arrayApplicantStatuses } from 'Shared/constants/Applicants'
import withTranslations from 'Shared/hoc/withTranslations'
import { SmallButton, SmallJustIconButton } from 'Shared/components/Button'
import DropdownMenu from 'Shared/components/DropdownMenu'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import Modal from 'Shared/components/Modal'
import BulkSendMessageFormConnector from 'Employer/connectors/Forms/BulkOperations/BulkSendMessageFormConnector'
import BulkInviteToInterviewFormConnector from 'Employer/connectors/Forms/BulkOperations/BulkInviteToInterviewFormConnector'
import BulkManagerWrapper from 'Employer/components/BulkManagerWrapper'
import { isStatusDeclined, isStatusHired } from 'Shared/helpers/jobApplications'
import DeclinedMessageForm from 'Employer/connectors/Forms/DeclinedMessageFormConnector'
import HiredMessageFormConnector from 'Employer/connectors/Forms/Applications/HiredMessageFormConnector'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'

// TODO: Will refactor this to be reusable with API call story :) ralf@ 2/19/18 2:24 PM
class ApplicantsBulkManager extends PureComponent {
  state = {
    dropdownVisible: false,
    bulkSendMessageModalVisible: false,
    bulkInviteToInterviewVisible: false,
    bulkDeclineModalVisible: false,
    bulkHiredModalVisible: false
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.bulkOperationItems.length === 0) {
      this.hideBulkSendMessageModal()
      this.hideBulkInviteToInterviewModal()
    }
    nextProps.bulkSendMessagesSucceeded && this.hideBulkSendMessageModal()
    nextProps.bulkInterviewInvitationSucceeded && this.hideBulkInviteToInterviewModal()
  }

  toggleDropdown = () => this.setState({ dropdownVisible: !this.state.dropdownVisible })

  hideBulkSendMessageModal = () => this.setState({ bulkSendMessageModalVisible: false })
  showBulkSendMessageModal = () => this.setState({ bulkSendMessageModalVisible: true })

  hideBulkInviteToInterviewModal = () => this.setState({ bulkInviteToInterviewVisible: false })
  showBulkInviteToInterviewModal = () => this.setState({ bulkInviteToInterviewVisible: true })

  showBulkDeclineModal = () => this.setState({ bulkDeclineModalVisible: true })
  hideBulkDeclineModal = () => this.setState({ bulkDeclineModalVisible: false })

  showBulkHiredModal = () => this.setState({ bulkHiredModalVisible: true })
  hideBulkHiredModal = () => this.setState({ bulkHiredModalVisible: false })

  bulkStatusUpdate = status => async () => {
    if (status === applicationStatuses.interviewed) {
      return this.showBulkInviteToInterviewModal()
    }

    if (isStatusDeclined(status)) {
      return this.showBulkDeclineModal()
    }

    if (isStatusHired(status)) {
      return this.showBulkHiredModal()
    }
    return this.props.updateJobOfferApplicationsWithStatus(status)
  }

  bulkChangeStatusWithMessage = status => message => {
    this.props.updateJobOfferApplicationsWithStatus(status, message)
  }

  bulkInviteToInterview = async () => {
    await this.props.bulkInterviewInvitations()
    return this.props.updateJobOfferApplicationsWithStatus(applicationStatuses.interviewed)
  }

  render () {
    const { t, downloadApplicantsCVs } = this.props
    const button = <SmallButton withIcon iconClassName='ion-ios-more'>{t('common.moveTo')}</SmallButton>
    return (
      <BulkManagerWrapper
        bulkOperationItemsLength={this.props.bulkOperationItems.length}
      >
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton className='tooltip-container-margin'><i className='ion-md-calendar' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          <Tooltip className='bulk-manager__action' content={this.props.t('tooltips.bulkOperations.invite')}>
            <SmallJustIconButton onClick={this.showBulkInviteToInterviewModal}>
              <i className='ion-md-calendar' />
            </SmallJustIconButton>
          </Tooltip>
        </LimitedAccessWrapperConnector>
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton className='tooltip-container-margin'><i className='ion-md-chatboxes' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          <Tooltip className='bulk-manager__action' content={this.props.t('tooltips.bulkOperations.sendMessage')}>
            <SmallJustIconButton onClick={this.showBulkSendMessageModal}>
              <i className='ion-md-chatboxes' />
            </SmallJustIconButton>
          </Tooltip>
        </LimitedAccessWrapperConnector>
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallButton className='tooltip-container-margin'><i className='ion-md-download' /></SmallButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          <Tooltip className='bulk-manager__action' content={this.props.t('tooltips.bulkOperations.downloadCVs')}>
            <SmallButton
              onClick={downloadApplicantsCVs}
              withIcon
              iconClassName='ion-md-download'>
              {t('common.cv')}
            </SmallButton>
          </Tooltip>
        </LimitedAccessWrapperConnector>
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallButton className='tooltip-container-margin'><i className='ion-ios-more' /></SmallButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.applications]}
          jobOfferId={this.props.match.params.jobOfferId}
        >
          <DropdownMenu
            upwards
            className='c-dropdown-menu bulk-manager__action'
            animate={false}
            isOpen={this.state.dropdownVisible}
            onToggle={this.toggleDropdown}
            triggerButton={button}
            align={'right'}
            closeOnOutsideClick
          >
            {arrayApplicantStatuses.map((status, i) =>
              <li
                key={i}
                role='button'
                onClick={this.bulkStatusUpdate(status)}>
                {t(`employer.applicants.applicantsType.${status}`)}
              </li>
            )}
          </DropdownMenu>
        </LimitedAccessWrapperConnector>
        <ConfirmModal
          title={t('modals.jobOfferApplicationBulkDownloadSuccess.title')}
          content={t('modals.jobOfferApplicationBulkDownloadSuccess.description')}
          handleConfirm={this.props.hideDownloadCVsConfirm}
          submitText={t('common.ok')}
          isModalVisible={this.props.downloadCVsModalVisible}
          hideConfirmModal={this.props.hideDownloadCVsConfirm}
        />
        <Modal
          title={t('modals.changeApplicationStatus.declinedTitle')}
          handleSubmit={this.props.submitDeclinedForm}
          handleClose={this.hideBulkDeclineModal}
          show={this.state.bulkDeclineModalVisible}
          handleCancel={this.hideBulkDeclineModal}
          cancelText={t('common.cancel')}
          submitText={t('common.sendMessage')}
          showContact={false}
          showCancel
          closeOnSubmit={this.props.declinedMessageFormValid}
        >
          <DeclinedMessageForm
            changeStatusAction={this.bulkChangeStatusWithMessage(applicationStatuses.declined)}
            bulk
          />
        </Modal>
        <Modal
          title={t('modals.changeApplicationStatus.title', { status: t('employer.jobOffers.jobOffersType.hired') })}
          handleSubmit={this.props.submitHiredForm}
          show={this.state.bulkHiredModalVisible}
          handleCancel={this.hideBulkHiredModal}
          handleClose={this.hideBulkHiredModal}
          cancelText={t('common.cancel')}
          submitText={t('common.sendMessage')}
          showContact={false}
          showCancel
          closeOnSubmit={this.props.hiredMessageFormValid}
        >
          <HiredMessageFormConnector
            changeStatusAction={this.bulkChangeStatusWithMessage(applicationStatuses.hired)}
            bulk
          />
        </Modal>
        <Modal
          title={t('common.sendMessage')}
          handleSubmit={this.props.bulkSendMessages}
          handleClose={this.hideBulkSendMessageModal}
          show={this.state.bulkSendMessageModalVisible}
          handleCancel={this.hideBulkSendMessageModal}
          cancelText={t('common.cancel')}
          submitText={t('common.sendMessage')}
          showContact={false}
          showCancel
          closeOnSubmit={false}
        >
          <BulkSendMessageFormConnector />
        </Modal>
        <Modal
          title={t('modals.inviteToInterview.title')}
          handleSubmit={this.bulkInviteToInterview}
          handleClose={this.hideBulkInviteToInterviewModal}
          show={this.state.bulkInviteToInterviewVisible}
          handleCancel={this.hideBulkInviteToInterviewModal}
          cancelText={t('common.cancel')}
          submitText={t('common.sendInvitation')}
          showContact={false}
          showCancel
          closeOnSubmit={false}
        >
          <BulkInviteToInterviewFormConnector />
        </Modal>
      </BulkManagerWrapper>
    )
  }
}

ApplicantsBulkManager.propTypes = {
  resetBulkActionSelection: PropTypes.func.isRequired,
  downloadApplicantsCVs: PropTypes.func.isRequired,
  updateJobOfferApplicationsWithStatus: PropTypes.func.isRequired,
  bulkInterviewInvitations: PropTypes.func.isRequired,
  bulkSendMessages: PropTypes.func.isRequired,
  bulkOperationItems: PropTypes.array.isRequired
}

export default withTranslations(ApplicantsBulkManager)



