import { connect } from 'react-redux'
import React from 'react'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileHeader from 'Employer/components/CandidateProfileDetails/CandidateProfileHeader'
import { getApplicationCandidateId, isApplicationBoosted } from 'Shared/selectors/jobOfferApplications'
import {
  getCandidateDetails,
  getCandidateLocation,
  getCandidateLastName,
  getCandidateFirstName
} from 'Shared/selectors/candidates'
import { getCandidateMajorsList } from 'Shared/selectors/education'
import ApplicationContactDetailsConnector from 'Employer/connectors/ApplicationProfileDetails/ApplicationContactDetailsConnector'
import ApplicationDetailsActionButtons from 'Employer/components/ApplicationDetails/ApplicationDetailsActionButtons'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    details: getCandidateDetails(state, candidateId),
    firstName: getCandidateFirstName(state, candidateId),
    lastName: getCandidateLastName(state, candidateId),
    major: getCandidateMajorsList(state, candidateId),
    location: getCandidateLocation(state, candidateId),
    id: candidateId,
    hasDatabaseSearchAvailable: true,
    contactSection: () => <ApplicationContactDetailsConnector />,
    asideSection: () => <ApplicationDetailsActionButtons />,
    hasOlivPlus: isApplicationBoosted(state, ownProps.match.params.applicationId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileHeader)



