import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import { linkTypes } from 'Shared/constants/Links'
import { getCandidateWebsites, hasCandidateAnySocialLink, getCandidateLinkType } from 'Shared/selectors/links'
import CandidateProfileWebsites from 'Employer/components/CandidateProfileDetails/CandidateProfileWebsites'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    websites: getCandidateWebsites(state, candidateId),
    linkedIn: getCandidateLinkType(candidateId, linkTypes.linkedin)(state),
    instagram: getCandidateLinkType(candidateId, linkTypes.instagram)(state),
    twitter: getCandidateLinkType(candidateId, linkTypes.twitter)(state),
    hasAnySocialLink: hasCandidateAnySocialLink(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileWebsites)



