import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Helmet } from 'react-helmet'
import * as R from 'ramda'
import { Field } from 'redux-form'
import { Input } from 'Shared/components/FormElements'

import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import Container from 'Shared/components/Container'
import FormContainer from 'Shared/components/FormContainer/index'
import Divider from 'Shared/components/FormElements/Divider'
import withTranslations from 'Shared/hoc/withTranslations'

import {
  Availability,
  JobDetails,
  JobDescription,
  Location,
  Salary,
  Skills,
  PreferredLanguages,
  GenderPreferences,
  AdditionalPreferences,
  SelectTemplate,
  Notifications,
  CompanyInformation,
  ExternalLink,
  OliviaShortlisting
} from './components'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import PageHeading from 'Shared/components/PageHeading/PageHeading'
import icon from 'Shared/assets/images/vectors/icon-post-a-job.svg'
import { SecondaryButton } from 'Shared/components/Button'
import { Statuses } from 'Shared/constants/Job'
import INDEX_NAME from 'Shared/constants/Algolia'
import FormUtilsService from 'Shared/services/FormUtilsService'
import PublishJobOfferErrorConnector from 'Employer/connectors/PublishJobOfferError/PublishJobOfferErrorConnector'
import InactiveMemberPricingModal from 'Employer/components/InactiveMemberPricingModal'
import { omitWhen } from 'Shared/helpers/object'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import PostJobBtn from 'Employer/forms/JobOffer/components/PostJobBtn'
import { EmployerPermissions } from 'Shared/constants/User'
import './JobOffer.scss'
import { daysBetweenTwoDates } from 'Shared/helpers/date'

class JobOffer extends Component {
  static defaultProps = {
    fetchJobOffer: () => {},
    fetchJobOfferTemplates: () => {},
    postAJobAccessAlertModalVisible: false,
    hidePostAJobAccessAlertModal: () => {}
  }

  formUtilsService = new FormUtilsService()

  state = {
    isConfirmBlankModalOpen: false,
    isConfirmOtherModalOpen: false,
    selectedTemplateId: '',
    selectedTemplateType: '',
    isDemoAccountModalOpen: false,
    showConfirmNavigation: true,
    isConfirmUseJobPostModalVisible: false,
    isNoJobPostsModalVisible: false
  }

  async componentDidMount () {
    await this.props.fetchJobOffer()
    this.props.fetchCurrenciesIfNeeded()
    this.props.fetchCurrentLocationIfNeeded()
    this.props.fetchCurrentMembershipIfNeeded()
    this.props.getOrganisationEmployers()
  }

  componentDidUpdate (prevProps) {
    // console.log(this.props.formValues)
    if (!prevProps.submitSucceeded && this.props.submitSucceeded && !this.props.neverHadAnyMembership) {
      this.props.history.push(`/employer/job-offers/${this.state.status}`)
    }
    if (prevProps.defaultCurrency !== this.props.defaultCurrency) {
      this.props.change('currencyId', this.props.defaultCurrency)
    }

    if (prevProps.isCustomCareerSiteUrlEnabled !== this.props.isCustomCareerSiteUrlEnabled && !this.props.isCustomCareerSiteUrlEnabled) {
      this.props.change('customCareerSiteUrl', null)
    }

    const confirmExit = () => {
      if (this.props.dirty && this.state.showConfirmNavigation) {
        return true
      }
      return null
    }

    window.onbeforeunload = confirmExit

    this.unblock = this.props.history.block(location => {
      if (this.props.dirty && this.state.showConfirmNavigation) {
        switch (true) {
          case this.props.invalid && this.props.formValues.status === Statuses.active:
            return this.props.t('modals.unsavedInvalidJobOffer.description')
          default:
            return this.props.t('common.unsavedChangesDescription')
        }
      }
    })
  }

  componentWillUnmount () {
    this.unblock()
    window.onbeforeunload = null
  }

  redirectToDrafts = () => this.props.history.push('/employer/job-offers/draft')
  redirectToActive = () => this.props.history.push('/employer/job-offers/active')

  openDemoAccountModal = () => this.setState({ isDemoAccountModalOpen: true })
  closeDemoAccountModal = () => this.setState({ isDemoAccountModalOpen: false })

  normalizeValues = values => {
    const safeValues = R.omit(['location', 'logoUrl'], values)
    return {
      ...safeValues,
      ...values.location
    }
  }

  beforeDemoModalRedirect = () => {
    const { postJob, formValues, updateOrganisation } = this.props
    updateOrganisation(this.normalizeValues(formValues.companyInformation))
    if (this.props.initialValues.status !== Statuses.inactive) return postJob({ ...R.dissoc('companyInformation', formValues), status: Statuses.draft })
  }

  submitForm = status => () => {
    const {
      formValues,
      neverHadAnyMembership,
      membershipDetails,
      hasCompanyProfilePermission,
      handleSubmit,
      postJob,
      isCompanyProfileComplete,
      initialValues: { status: oldStatus },
      updateOrganisation,
      jobPostsUnlimited
    } = this.props

    this.setState({ showConfirmNavigation: false, status })
    // console.log({formValues})

    if (formValues.companyInformation.locationCitiesWithCountry) {
      const [companyCity, companyCountry] = (formValues.companyInformation && formValues.companyInformation.locationCitiesWithCountry) && formValues.companyInformation.locationCitiesWithCountry.split(',')

      const companyAddress = {
        address: formValues.companyInformation.locationCitiesWithCountry,
        city: R.trim(companyCity) || formValues.location.city,
        country: R.trim(companyCountry) || formValues.location.country,
        locationCoords: '99.999',
        googlePlacesId: '99.999'
      }
      formValues.companyInformation.location = companyAddress
      // console.log(formValues.companyInformation.location)
    }

    const noJobPostsLeft = membershipDetails.jobPostAmountLeft <= 0
    const hasJobPostsEnabled = membershipDetails.jobPostEnabled
    const emptyJobPostsService = noJobPostsLeft && !jobPostsUnlimited && hasJobPostsEnabled

    const isOfferInactive = oldStatus === Statuses.inactive
    const autoExpired = R.isNil(formValues.deactivatedAt)
    const isDeactivatedMoreThanThirtyDaysAgo = daysBetweenTwoDates(formValues.deactivatedAt, formValues.publishedAt) > 30

    console.log('formValues Before', formValues)
    // return

    switch (true) {
      case neverHadAnyMembership && status === Statuses.active:
        return this.openDemoAccountModal()
      case status === Statuses.draft:
        const openedValues = omitWhen(R.isNil, Object.keys(formValues.companyInformation), formValues.companyInformation)
        !R.isEmpty(openedValues) && hasCompanyProfilePermission && updateOrganisation(this.normalizeValues(openedValues))
        return postJob({ ...R.dissoc('companyInformation', formValues), status }).then(this.redirectToDrafts)
      case isOfferInactive && emptyJobPostsService:
        this.setState({ isNoJobPostsModalVisible: true })
        break
      case isOfferInactive && autoExpired && hasJobPostsEnabled:
      case isOfferInactive && !autoExpired && isDeactivatedMoreThanThirtyDaysAgo && hasJobPostsEnabled:
        this.setState({ isConfirmUseJobPostModalVisible: true })
        break
      case hasCompanyProfilePermission && !isCompanyProfileComplete:
        updateOrganisation(this.normalizeValues(formValues.companyInformation))
        return handleSubmit(values => {
          postJob({ ...R.dissoc('companyInformation', values), status }, oldStatus)
        })()
      default:
        this.props.change('status', status)
        return handleSubmit(values => postJob({ ...values, status }, oldStatus))()
    }
  }

  untouch = () => this.props.untouch('template templateId')

  fetchJobOfferTemplates = type => () => {
    const { fetchJobOfferTemplates, fetchPreviousJobOffersAsTemplates } = this.props
    this.setState({ selectedTemplateType: type }, () => {
      this.state.selectedTemplateType === 'public' ? fetchJobOfferTemplates() : fetchPreviousJobOffersAsTemplates()
      this.untouch()
    })
  }

  openChangeTemplateToBlankModal = () => this.setState({ isConfirmBlankModalOpen: true })
  closeChangeTemplateToBlankModal = () => this.setState({ isConfirmBlankModalOpen: false })

  openChangePopularTemplateModal = (id) => {
    const selectedTemplateId = id || this.state.selectedTemplateId
    this.setState({ isConfirmOtherModalOpen: true, selectedTemplateId }, () => this.untouch())
  }

  closeChangePopularTemplateModal = () => this.setState({ isConfirmOtherModalOpen: false })

  initializeBlank = (e) => {
    this.untouch()
    if (this.props.anyTouched) {
      e.preventDefault()
      this.openChangeTemplateToBlankModal()
    } else {
      this.confirmBlank()
    }
  }

  confirmBlank = () => {
    const freshValues = this.props.edit ? this.props.blankInitialValues : this.props.initialValues
    this.untouch()
    this.props.initialize({
      ...freshValues,
      companyInformation: this.props.formValues.companyInformation,
      description: '<p></p>',
      requirements: '<p></p>'
    })
    this.props.change('location', '')
  }

  confirmChangeTemplate = () => {
    this.untouch()
    this.fetchJobOfferTemplate(this.state.selectedTemplateId)
    this.closeChangePopularTemplateModal()
  }

  fetchJobOfferTemplate = (id) => {
    this.props.change('templateId', id)
    this.untouch()
    const { fetchJobOfferTemplate, initialize, initialValues, formValues, languagesEntries } = this.props
    this.setState({ selectedTemplateId: id })
    const initializeReceivedData = (receivedData) => {
      initialize({
        ...R.omit(['template', 'templateId'], initialValues),
        ...receivedData,
        skills: this.formUtilsService.mapOptionsByLanguageTranslations(receivedData.skills),
        preferredLanguages: this.formUtilsService.mapLanguagesToOptions(receivedData.preferredLanguages, languagesEntries),
        template: formValues.template,
        templateId: id,
        id: formValues.id,
        salary: receivedData.remuneration,
        companyInformation: formValues.companyInformation
      })
    }
    this.state.selectedTemplateType === 'public'
      ? fetchJobOfferTemplate(id).then(initializeReceivedData)
      : fetchJobOfferTemplate(id, 'private').then(initializeReceivedData)
    this.props.change('location', '')
  }

  get skillsProps () {
    return R.pick(['selectedSkills', 'addUserNewSkill', 'pushSkill'], this.props)
  }

  get companyInformationProps () {
    return {
      ...R.pick([
        'uploadOrganisationLogo',
        'deleteOrganisationLogo',
        'formErrors',
        'submitFailed',
        'hasCompanyProfilePermission'
      ], this.props),
      companyInformation: this.props.initialValues.companyInformation
    }
  }

  get CancelButton () {
    return this.props.initialValues.status === Statuses.active && (
      <SecondaryButton
        onClick={this.redirectToActive}
        className='job_offer_form-footer--save-draft'
      >
        {this.props.t('common.cancel')}
      </SecondaryButton>
    )
  }

  get SaveDraftBtn () {
    return this.props.initialValues.status === Statuses.draft && (
      <LimitedAccessWrapperConnector
        placeholderComponent={<SecondaryButton className='job_offer_form-footer--save-draft'>{this.props.t('common.saveDraft')}</SecondaryButton>}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
        jobOfferId={this.props.match.params.jobOfferId}
      >
        <SecondaryButton
          onClick={this.submitForm(Statuses.draft)}
          className='job_offer_form-footer--save-draft'
        >
          {this.props.t('common.saveDraft')}
        </SecondaryButton>
      </LimitedAccessWrapperConnector>
    )
  }

  closeConfirmUseJobPostModal = () => this.setState({ isConfirmUseJobPostModalVisible: false })
  closeNoJobPostsModal = () => this.setState({ isNoJobPostsModalVisible: false })

  redirectToPricing = () => this.props.history.push('/pricing')

  confirmUseJobPost = status => () => {
    const { hasCompanyProfilePermission, handleSubmit, postJob, formValues, isCompanyProfileComplete, initialValues: { status: oldStatus }, updateOrganisation } = this.props
    switch (true) {
      case hasCompanyProfilePermission && !isCompanyProfileComplete:
        updateOrganisation(this.normalizeValues(formValues.companyInformation))
        return handleSubmit(values => {
          postJob({ ...R.dissoc('companyInformation', values), status }, oldStatus)
        })()
      default:
        this.props.change('status', status)
        handleSubmit(values => postJob({ ...values, status }, oldStatus))()
    }
  }

  render () {
    const {
      t,
      isOfferAsap,
      fetchDepartments,
      changeFieldValue,
      hasStartDateFieldError,
      hasCommitmentFieldError,
      hasRemunerationTypeFieldError,
      hasJobTypeFieldError,
      hasPermanentJobOpportunityFieldError,
      languages,
      pageHeading,
      isJobAnInternship,
      remunerationType,
      formValues,
      jobOfferTemplates,
      teamMembers,
      isAdminEmployer,
      isCustomCareerSiteUrlEnabled,
      selectedJobType
    } = this.props
    const templateValue = R.path(['template'], formValues)

    return (
      <Container>
        <Helmet>
          <title>{t('meta.employer.title.postJob')}</title>
          <meta property='og:title' content={t('meta.employer.title.postJob')} />
        </Helmet>
        <div>
          <PageTopHeaderMobile title={pageHeading} />
          <FormContainer className={'form-container--details'}>
            <form>
              <PageHeading
                heading={pageHeading}
                description={t(`jobOffer.postAJob.description.text`)}
                image={icon}
              />
              <Divider />
              <SelectTemplate
                templateValue={templateValue}
                jobOfferTemplates={jobOfferTemplates}
                fetchTemplate={this.props.anyTouched ? this.openChangePopularTemplateModal : this.fetchJobOfferTemplate}
                initializeBlank={this.initializeBlank}
                untouch={this.untouch}
                fetchJobOfferTemplates={this.fetchJobOfferTemplates('public')}
                fetchPreviousJobOffersAsTemplates={this.fetchJobOfferTemplates('private')}
              />
              <ConfirmModal
                title={t('modals.changeTemplate.title')}
                content={t('modals.changeTemplate.content')}
                hideConfirmModal={this.closeChangeTemplateToBlankModal}
                handleConfirm={this.confirmBlank}
                submitText={t('modals.changeTemplate.submitButton')}
                cancelText={t('modals.changeTemplate.cancelButton')}
                isModalVisible={this.state.isConfirmBlankModalOpen}
                closeOnSubmit
                closeOnCancel
              />
              <ConfirmModal
                title={t('modals.changeTemplate.title')}
                content={t('modals.changeTemplate.content')}
                hideConfirmModal={this.closeChangePopularTemplateModal}
                handleConfirm={this.confirmChangeTemplate}
                submitText={t('modals.changeTemplate.submitButton')}
                cancelText={t('modals.changeTemplate.cancelButton')}
                isModalVisible={this.state.isConfirmOtherModalOpen}
                closeOnSubmit
                closeOnCancel
              />
              <ConfirmModal
                title={t('modals.republishJob.confirmPostUsage.title')}
                content={t('modals.republishJob.confirmPostUsage.description', { count: this.props.membershipDetails.jobPostAmountLeft })}
                hideConfirmModal={this.closeConfirmUseJobPostModal}
                handleConfirm={this.confirmUseJobPost(Statuses.active)}
                submitText={t('modals.republishJob.confirmPostUsage.submitButton')}
                cancelText={t('modals.changeTemplate.cancelButton')}
                isModalVisible={this.state.isConfirmUseJobPostModalVisible}
                closeOnSubmit
                closeOnCancel
              />
              <ConfirmModal
                title={t('modals.republishJob.noJobsLeft.title')}
                content={t('modals.republishJob.noJobsLeft.description')}
                hideConfirmModal={this.closeNoJobPostsModal}
                handleConfirm={this.redirectToPricing}
                submitText={t('modals.republishJob.noJobsLeft.submitButton')}
                cancelText={t('modals.changeTemplate.cancelButton')}
                isModalVisible={this.state.isNoJobPostsModalVisible}
                closeOnSubmit
                closeOnCancel
              />
              <Divider />
              <JobDetails
                isJobAnInternship={isJobAnInternship}
                fetchDepartments={fetchDepartments}
                hasJobTypeFieldError={hasJobTypeFieldError}
                hasPermanentJobOpportunityFieldError={hasPermanentJobOpportunityFieldError}
              />
              <Divider />
              <Location formValues={this.props.formValues} />
              <Divider />
              <JobDescription />
              <Divider />
              <Availability
                selectedJobType={selectedJobType}
                isOfferAsap={isOfferAsap}
                changeFieldValue={changeFieldValue}
                hasStartDateFieldError={hasStartDateFieldError}
                hasCommitmentFieldError={hasCommitmentFieldError}
              />
              <Divider />
              <Salary
                remunerationType={remunerationType}
                hasRemunerationTypeFieldError={hasRemunerationTypeFieldError}
                currencies={this.props.currencies}
              />
              <Divider />
              <Skills indexName={INDEX_NAME.SKILLS} {...this.skillsProps} shouldUpdateUrl={false} />
              <Divider />
              <PreferredLanguages languages={languages} />
              <Divider />
              <GenderPreferences />
              <AdditionalPreferences />
              <OliviaShortlisting />
              <ExternalLink isCustomCareerSiteUrlEnabled={isCustomCareerSiteUrlEnabled} />
              <Divider />
              <Notifications isAdmin={isAdminEmployer} teamMembers={teamMembers} />
              <CompanyInformation {...this.companyInformationProps} />
              <Field
                component={Input}
                name='id'
                type='hidden'
              />
            </form>
          </FormContainer>
          <div className='job-offer__footer'>
            {this.CancelButton}
            {this.SaveDraftBtn}
            <div className='job_offer_form-footer--post-job'>
              <PostJobBtn {...this.props} submitForm={this.submitForm} />
            </div>
          </div>
        </div>
        <PublishJobOfferErrorConnector onCtaClick={this.beforeDemoModalRedirect} />
        <InactiveMemberPricingModal
          show={this.state.isDemoAccountModalOpen}
          handleClose={this.closeDemoAccountModal}
          onCtaClick={this.beforeDemoModalRedirect}
        />
        <div id='confirmPrompt' />
      </Container>
    )
  }
}

JobOffer.propTypes = {
  canUseJobSlots: PropTypes.bool.isRequired,
  canUseJobPosts: PropTypes.bool.isRequired,
  selectedSkills: PropTypes.array.isRequired,
  addUserNewSkill: PropTypes.func.isRequired,
  pushSkill: PropTypes.func.isRequired,
  postJob: PropTypes.func.isRequired,
  changeFieldValue: PropTypes.func.isRequired,
  formValues: PropTypes.object.isRequired,
  isOfferAsap: PropTypes.bool,
  fetchDepartments: PropTypes.func.isRequired,
  fetchCurrenciesIfNeeded: PropTypes.func.isRequired,
  fetchCurrentLocationIfNeeded: PropTypes.func.isRequired,
  fetchJobOfferTemplates: PropTypes.func.isRequired,
  fetchJobOfferTemplate: PropTypes.func.isRequired,
  fetchPreviousJobOffersAsTemplates: PropTypes.func.isRequired,
  hasStartDateFieldError: PropTypes.bool.isRequired,
  hasCommitmentFieldError: PropTypes.bool.isRequired,
  hasRemunerationTypeFieldError: PropTypes.bool.isRequired,
  hasJobTypeFieldError: PropTypes.bool.isRequired,
  hasPermanentJobOpportunityFieldError: PropTypes.bool.isRequired,
  languages: PropTypes.array.isRequired,
  pageHeading: PropTypes.string.isRequired,
  isJobAnInternship: PropTypes.bool,
  remunerationType: PropTypes.string
}

export default withTranslations(JobOffer)



