import React, { Component } from 'react'
import { withRouter, Switch, Redirect } from 'react-router-dom'
import AddJobOfferConnector from 'Employer/connectors/Forms/JobOffer/AddJobOfferConnector'
import EditJobOfferConnector from 'Employer/connectors/Forms/JobOffer/EditJobOfferConnector'
import ApplicantsViewWrapper from 'Employer/views/ApplicantsView/ApplicantsViewWrapper'
import ApplicantDetailsViewConnector from 'Employer/connectors/Views/Applicants/ApplicantDetailsViewConnector'
import EditJobOffersViewLoaders from './EditJobOffersViewLoaders'
import withTranslations from 'Shared/hoc/withTranslations'
import EmployerPrivateRoute from 'Employer/components/EmployerPrivateRoute'
import { EmployerPermissions } from 'Shared/constants/User'
import './EditJobOffersView.scss'

class EditJobOffersView extends Component {
  async componentDidMount () {
    await this.props.fetchJobOffer()
    await this.props.fetchLanguages()
    this.setState({ ready: true })
  }

  state = {
    ready: false
  }

  render () {
    return (
      <div className='edit-job-offer-view__container parsing--hidden'>
        {
          this.props.languagesLoaded && this.state.ready
            ? (
              <Switch>
                <EmployerPrivateRoute allowedFor={[EmployerPermissions.jobPosts, EmployerPermissions.jobOfferId]} exact path='/employer/job-offer/:jobOfferId/edit' component={EditJobOfferConnector} />
                <EmployerPrivateRoute allowedFor={[EmployerPermissions.jobPosts, EmployerPermissions.jobOfferId]} exact path='/employer/job-offer/:jobOfferId/add' component={AddJobOfferConnector} />
                <EmployerPrivateRoute allowedFor={[EmployerPermissions.jobOfferId]} exact path='/employer/job-offer/:jobOfferId/applicants/:status/:savedSearchId?' component={ApplicantsViewWrapper} />
                <EmployerPrivateRoute
                  allowedFor={[EmployerPermissions.applications, EmployerPermissions.jobOfferId]}
                  exact
                  path='/employer/job-offer/:jobOfferId/applicant/:applicationId'
                  component={ApplicantDetailsViewConnector}
                />
                <Redirect to='/employer/job-offers/active' />
              </Switch>
            )
            : <EditJobOffersViewLoaders />
        }
      </div>
    )
  }
}

export default withRouter(withTranslations(EditJobOffersView))



