import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ToggleApplicantButton from 'Employer/components/ToogleApplicantWrapper/components/ToggleApplicantButton'
import { changeApplicationStatus } from 'Shared/ducks/jobOfferApplications'

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeApplicationStatus: ({ jobOfferId, candidateId, status, id, oldStatus, message, includeNotification, candidateName }) => {
    return dispatch(changeApplicationStatus({ jobOfferId, candidateId, status, id, message, includeNotification, candidateName }, ownProps.applicantStatus))
  }
})

export default connect(null, mapDispatchToProps)(withRouter(ToggleApplicantButton))



