import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ApplicationHits from 'Employer/views/ApplicantsView/components/ApplicationHits'
import { shouldShowSorting } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => ({
  applicationsLoading: state.jobOfferApplications.isLoading,
  shouldShowSorting: shouldShowSorting(state, ownProps.match.params.jobOfferId)
})

export default withRouter(connect(mapStateToProps)(ApplicationHits))



