import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import ApplicationStatusChangeDropdown from 'Employer/components/ApplicationStatusChangeDropdown'
import { getApplicationCandidateId, getApplicationJobOfferId, getApplicationStatusById } from 'Shared/selectors/jobOfferApplications'
import { getCandidateFirstName, getCandidateDetails } from 'Shared/selectors/candidates'

const mapStateToProps = (state, ownProps) => {
  const applicationId = ownProps.match.params.applicationId
  const candidateId = getApplicationCandidateId(state, applicationId)
  const jobOfferId = getApplicationJobOfferId(state, applicationId)

  return ({
    applicationId,
    candidateId,
    jobOfferId,
    applicationsLoading: state.jobOfferApplications.isLoading,
    candidate: getCandidateDetails(state, candidateId),
    candidateName: getCandidateFirstName(state, candidateId),
    applicationStatus: getApplicationStatusById(state, applicationId)
  })
}

const mapDispatchToProps = dispatch => ({})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(ApplicationStatusChangeDropdown)



