import { connect } from 'react-redux'
import { isInvalid, submit } from 'redux-form'
import { withRouter } from 'react-router-dom'
import ApplicationDropdownMenu from 'Employer/components/ApplicationDropdownMenu'
import { changeApplicationStatus } from 'Shared/ducks/jobOfferApplications'
import { applicationStatuses } from 'Shared/constants/Applicants'
import { formName as declinedFormName } from 'Employer/connectors/Forms/DeclinedMessageFormConnector'
import { formName as hiredFormName } from 'Employer/connectors/Forms/Applications/HiredMessageFormConnector'

const mapStateToProps = (state) => ({
  declinedMessageFormValid: !isInvalid(declinedFormName)(state),
  hiredMessageFormValid: !isInvalid(hiredFormName)(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeApplicationStatus: async ({ candidateId, status, id, message, includeNotification, candidateName }) => {
    if (status === applicationStatuses.interviewed) {
      return ownProps.openInviteToInterviewModal()
    }
    return dispatch(changeApplicationStatus({ jobOfferId: ownProps.jobOfferId, candidateId, status, id, message, includeNotification, candidateName }, ownProps.currentStatus))
  },
  submitDeclinedForm: () => dispatch(submit(declinedFormName)),
  submitHiredForm: () => dispatch(submit(hiredFormName))
})

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ApplicationDropdownMenu))



