import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import { lengthLimit } from 'Shared/constants/Editor'
import { Field, Form } from 'redux-form'
import { Textarea } from 'Shared/components/FormElements'
import { PrimaryButton, PrimaryButtonOutline } from 'Shared/components/Button'
import Divider from 'Shared/components/FormElements/Divider'
import TimeSlotsManager from 'Employer/components/TimeSlotsManager'
import { ExternalRoutes } from 'Shared/constants/Routes'

import './InterviewInviteForm.scss'
import NotificationService from 'Shared/services/NotificationService'
import { getTwentyFourHourTimeWithIsoDate } from 'Shared/helpers/date'

const [, max] = lengthLimit.interviewInvite

class InterviewInviteForm extends PureComponent {
  static propTypes = {
    handleClose: PropTypes.func.isRequired,
    invitationSending: PropTypes.bool.isRequired,
    sendInterviewMessage: PropTypes.func.isRequired
  }

  handleInterviewSubmit = async values => {
    const { t, jobOffer, applicationId, candidate, employerDetails } = this.props

    const data = {
      emailTo: candidate.email,
      emailFrom: employerDetails.email,
      startDate: values && getTwentyFourHourTimeWithIsoDate(values.timeSlots[0].from, values.timeSlots[0].date),
      endDate: values && getTwentyFourHourTimeWithIsoDate(values.timeSlots[0].to, values.timeSlots[0].date),
      message: values.description,
      jobOfferId: jobOffer.id,
      timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      jobUrl: `${process.env.REACT_APP_ROOT_URL}/jobs/${jobOffer.jobType}/${jobOffer.city}/${jobOffer.title}/${jobOffer.id}`,
      employerId: employerDetails.id,
      companyName: jobOffer.organisationId,
      applicantName: candidate.firstName,
      jobTitle: jobOffer.title,
      employerName: employerDetails.firstName
    }
    await this.props.apiSendInvitation(data)
    await this.props.sendInterviewMessage(values, jobOffer, candidate.id, applicationId)
    NotificationService.success(t('alerts.interviewInvitationSent'))
    return this.props.handleClose()
  }

  render () {
    const { t, handleSubmit, invitationSending } = this.props

    return (
      <div className='interview-invite-form'>
        <Form onSubmit={handleSubmit(this.handleInterviewSubmit)}>
          <div className='interview-invite-form__section-description'>
            {t('modals.inviteToInterview.description')}
            <a href={ExternalRoutes.unbiasedInterviews} target='_blank'> {t('modals.inviteToInterview.link')}</a>
          </div>
          <Divider className='interview-invite-form__divider' />
          <TimeSlotsManager />
          <Field
            className='interview-invite-form__message'
            component={Textarea}
            label={t('forms.message.label')}
            name='description'
            limit={max}
            placeholder={t('forms.message.placeholder')}
          />
          <div className='form-modal__footer'>
            <PrimaryButtonOutline type='button' className='button--form-controls-sm' onClick={this.props.handleClose}>
              {t('common.cancel')}
            </PrimaryButtonOutline>
            <PrimaryButton
              loading={invitationSending}
              disabled={invitationSending}
              type='submit'
              className='button--form-controls-sm'
            >
              {invitationSending ? t('common.loading') : t('common.sendInvitation')}
            </PrimaryButton>
          </div>
        </Form>
      </div>
    )
  }
}

export default withTranslations(InterviewInviteForm)



