import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ApplicantsMoreSearchFilters from 'Employer/components/ApplicantsSearchFilters/components/ApplicantsMoreSearchFilters'
import { isSensitiveMarket } from 'Shared/selectors/geolocation'

const mapStateToProps = state => ({
  languages: state.resources.languages.entries,
  countries: state.resources.countries.entries,
  isSensitiveMarket: isSensitiveMarket(state)
})

export default withRouter(connect(mapStateToProps)(ApplicantsMoreSearchFilters))



