import { connectHits } from 'react-instantsearch-dom'
import React, { useState, useEffect } from 'react'
import TrackVisibility from 'react-on-screen'
import PropTypes from 'prop-types'
import { take } from 'ramda'
import Loader from 'Shared/components/Loader/Loader'

function AnchorHit (props) {
  const { hasMore, refine } = props

  useEffect(() => {
    if (hasMore) {
      refine()
    }
  }, [])

  useEffect(() => {
    if (hasMore) {
      refine()
    }
  }, [hasMore])

  return (
    <div style={{ height: '10px', width: '100%' }}>&nbsp;</div>
  )
}

function ApplicantsAllHitsInfiniteScroll (props) {
  const { hits, sortBy, hitsPerPage, hitComponent, viewedFilterValue, infiniteScrollClass } = props
  const [page, setPage] = useState(0)
  const [hasMore, setHasMore] = useState(true)
  const [applications, setApplications] = useState([])

  useEffect(() => {
    loadHits()
    setApplications(getApplicationList())
  }, [])

  useEffect(() => {
    setApplications(getApplicationList())
  }, [hits])

  useEffect(() => {
    setApplications(getApplicationList())
  }, [sortBy])

  const getApplicationList = () => props.sortBy === 'CandidatesByApplicationDate' ? props.sortHitsByApplicationDate(props.hits) : props.hits

  const loadHits = () => {
    const pagesCount = Math.ceil(props.hits.length / props.hitsPerPage)
    setPage(page + 1)
    setHasMore(page < pagesCount)
  }

  const getHits = () => {
    const HitComponent = hitComponent
    const filteredHits = take(page * hitsPerPage, applications)

    return filteredHits
      .map((hit, index, hits) => <HitComponent
        key={hit.objectID}
        hit={hit}
        hitNumber={index}
        hitsLength={hits.length}
        viewedFilterValue={viewedFilterValue}
      />)
  }

  return (
    <div className={infiniteScrollClass}>
      {getHits()}
      <TrackVisibility offset={10} className='column col-12'>
        {({ isVisible }) => isVisible && <AnchorHit refine={loadHits} hasMore={hasMore} />}
      </TrackVisibility>
      { hasMore && <div style={{ margin: '20px 0', width: '100%' }}><Loader /></div> }
    </div>
  )
}

ApplicantsAllHitsInfiniteScroll.defaultProps = {
  hitsPerPage: 9,
  hits: []
}

ApplicantsAllHitsInfiniteScroll.propTypes = {
  hits: PropTypes.array.isRequired
}

export default connectHits(ApplicantsAllHitsInfiniteScroll)



