import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { compose } from 'redux'
import CandidateUnlockContactButtons from 'Employer/components/CandidateProfileDetails/CandidateUnlockContactButtons'
import {
  getCandidateMail,
  getCandidatePhone
} from 'Shared/selectors/candidates'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    email: getCandidateMail(state, candidateId),
    phoneNumber: getCandidatePhone(state, candidateId),
    shouldUnlock: false,
    canUseDatabaseSearch: true
  })
}
export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateUnlockContactButtons)



