import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import ApplicationProfileAbout from 'Employer/components/CandidateProfileDetails/CandidateProfileAbout/CandidateProfileAboutWithHighlights'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import { getCandidatePitch } from 'Shared/selectors/candidates'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    candidateId,
    pitch: getCandidatePitch(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(ApplicationProfileAbout)



