import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { getEditorContentLength, parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import './ReadMore.scss'

class ReadMore extends PureComponent {
  static propTypes = {
    text: PropTypes.string.isRequired,
    minLength: PropTypes.number.isRequired,
    className: PropTypes.string
  }

  constructor (props) {
    super(props)
    this.state = {
      text: getEditorContentLength(props.text) > props.minLength ? `${props.text.slice(0, props.minLength)}...` : props.text,
      readMore: false
    }
  }

  componentWillReceiveProps (nextProps) {
    const { text, minLength } = nextProps

    return text !== this.props.text && this.setState({
      text: getEditorContentLength(text) > minLength ? `${text.slice(0, minLength)}...` : text
    })
  }

  onReadMore = () => {
    this.setState({ text: this.props.text, readMore: true })
  }

  onReadLess = () => {
    this.setState({ text: `${this.props.text.slice(0, this.props.minLength)}...`, readMore: false })
  }

  readMoreBtn = () => {
    const { t } = this.props

    return this.state.readMore
      ? <button className='read-more__btn' onClick={this.onReadLess}>{t('common.readLess')}</button>
      : <button className='read-more__btn' onClick={this.onReadMore}>{t('common.readMore')}</button>
  }

  render () {
    const { className } = this.props
    const readMoreClass = cx({
      'read-more__last-tag-inline': true,
      [className]: className
    })
    const readMoreContainerClass = cx({
      'read-more-container': !this.state.readMore,
      'read-more-container--shadow': this.props.text.length > this.props.minLength && !this.state.readMore
    })

    return (
      <div className={readMoreClass}>
        <div className={readMoreContainerClass}>
          {parseHtmlWithoutSpan(this.state.text)}
        </div>
        { this.props.text.length > this.props.minLength && this.readMoreBtn() }
      </div>
    )
  }
}

export default withTranslations(ReadMore)



