import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileTagList from 'Employer/components/CandidateProfileDetails/CandidateProfileTagList'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import { getCandidateInterestsList } from 'Shared/selectors/interests'

const mapStateToProps = (state, ownProps) => {
  const candidateId = getApplicationCandidateId(state, ownProps.match.params.applicationId)

  return ({
    tagsList: getCandidateInterestsList()(state),
    withHighlights: false,
    candidateId
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileTagList)



