import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import HireApplicationModal from 'Employer/components/HireApplicationModal'
import { hasSubmitSucceeded, submit, isSubmitting } from 'redux-form'
import { formName as hiredFormName } from 'Employer/connectors/Forms/Applications/HireApplicationFormConnector'
import { getApplicationCandidateId } from 'Shared/selectors/jobOfferApplications'
import { getCandidateFirstName } from 'Shared/selectors/candidates'

const mapStateToProps = (state, ownProps) => {
  const applicationId = ownProps.applicationId
  const candidateId = getApplicationCandidateId(state, applicationId)

  return ({
    applicationId,
    shouldCloseModal: hasSubmitSucceeded(hiredFormName)(state),
    isModalSubmitting: isSubmitting(hiredFormName)(state),
    candidateFirstName: getCandidateFirstName(state, candidateId)
  })
}

const mapDispatchToProps = dispatch => ({
  submitDeclinedForm: () => dispatch(submit(hiredFormName))
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(HireApplicationModal)



