import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field } from 'redux-form'
import { RadioWrapper, Radio, InputDatePicker, Select } from 'Shared/components/FormElements'
import { normalizeRadio } from 'Shared/helpers/forms'
import withTranslations from 'Shared/hoc/withTranslations'
import moment from 'moment'
import { availabilityDurationOptions, AvailabilityCommitmentTypes, JobTypes, DurationTypesLabels } from 'Shared/constants/Job'
import cx from 'classnames'
import ProfileStepSection from 'Shared/components/ProfileStepSection/index'

class Availability extends Component {
  componentWillReceiveProps (nextProps) {
    if (this.props.isOfferAsap !== nextProps.isOfferAsap) {
      nextProps.isOfferAsap && this.props.changeFieldValue('startDate', null)
    }

    if (this.props.selectedJobType !== nextProps.selectedJobType && nextProps.selectedJobType === JobTypes().mirror.graduate_job) {
      this.props.changeFieldValue('duration', DurationTypesLabels().mirror.over_6_months)
    }
  }

  render () {
    const { t, isOfferAsap, hasStartDateFieldError, hasCommitmentFieldError, selectedJobType } = this.props
    const durationClass = cx({
      'form__group': true,
      'job-offer__job-duration': true,
      'job-offer__job-duration--hidden': selectedJobType === JobTypes().mirror.graduate_job
    })

    return (
      <div>
        <ProfileStepSection
          label={t('jobOffer.postAJob.availability.label')}
          text={t('jobOffer.postAJob.availability.text')}
        />
        <div className='columns form__group'>
          <div className='column col-6 col-xs-12'>
            <RadioWrapper
              legend={t('forms.startJobOfferDate.label')}
              hasRequiredError={hasStartDateFieldError}
            >
              <Field
                className='step_basic__radio_button'
                component={Radio}
                text={t('common.asap')}
                normalize={normalizeRadio}
                name='startDateAsap'
                radioButtonValue
              />
              <Field
                className='step_basic__radio_button'
                component={Radio}
                text={t('common.specificDate')}
                name='startDateAsap'
                normalize={normalizeRadio}
                radioButtonValue={false}
              />
            </RadioWrapper>
          </div>
          <div className='column col-6 col-xs-12'>
            <Field
              component={InputDatePicker}
              label={t('forms.startDate.label')}
              name='startDate'
              icon={'ion-md-clock'}
              minDate={moment().utc()}
              floatRight
              scrollableYearDropdown
              showYearDropdown
              showMonthDropdown
              placeholderText={t('forms.startDate.placeholder')}
              format={(value, name) => value ? moment(value, moment.ISO_8601) : null}
              disabled={isOfferAsap || isOfferAsap === undefined}
            />
          </div>
        </div>
        <div className={durationClass}>
          <Field
            component={Select}
            options={availabilityDurationOptions()}
            label={t('forms.jobDuration.label')}
            placeholder={t('forms.jobDuration.placeholder')}
            name='duration'
            withIcon
          />
        </div>
        <div className='form__group'>
          <RadioWrapper
            name='commitment'
            legend={t('jobOffer.postAJob.availability.commitment.title')}
            className='form__wrapper--radio-checkbox--large'
            hasRequiredError={hasCommitmentFieldError}
          >
            <Field
              className='step_basic__radio_button form__group--radio-large'
              component={Radio}
              text={t('jobOffer.postAJob.availability.commitment.fullTime.label')}
              name='commitment'
              radioButtonValue={AvailabilityCommitmentTypes().mirror.over_30_hrs_weekly}
              description={t('jobOffer.postAJob.availability.commitment.fullTime.description')}
            />
            <Field
              className='step_basic__radio_button form__group--radio-large'
              component={Radio}
              text={t('jobOffer.postAJob.availability.commitment.partTime.label')}
              name='commitment'
              radioButtonValue={AvailabilityCommitmentTypes().mirror.less_than_30_hrs_weekly}
              description={t('jobOffer.postAJob.availability.commitment.partTime.description')}
            />
            <Field
              className='step_basic__radio_button form__group--radio-large'
              component={Radio}
              text={t('jobOffer.postAJob.availability.commitment.flexible.label')}
              name='commitment'
              radioButtonValue={AvailabilityCommitmentTypes().mirror.i_dont_know_yet}
              description={t('jobOffer.postAJob.availability.commitment.flexible.description')}
            />
          </RadioWrapper>
        </div>
      </div>
    )
  }
}

Availability.propTypes = {
  changeFieldValue: PropTypes.func.isRequired,
  isOfferAsap: PropTypes.bool,
  hasStartDateFieldError: PropTypes.bool,
  hasCommitmentFieldError: PropTypes.bool
}

export default withTranslations(Availability)



